import { FontSizes, isContrastTheme, isDefaultTheme, makeStyles } from '@addressbook/components-fluentui-teams-theme';

export const useFileNodeViewStyles = makeStyles(theme => ({
    root: {
        borderRadius: '4px',
        overflow: 'hidden',
        '&.selected a': {
            textDecorationStyle: 'double',
        },
    },
    error: {
        borderColor: theme.palette.redDark,
        '[data-node-view-content]': {
            color: theme.palette.redDark,
        },
        a: {
            textDecoration: 'line-through',
        },
    },
    image: {
        display: 'inline-flex',
        maxWidth: '100%',
        boxShadow: theme.effects.elevation4,
        margin: '2px',
        borderWidth: '1px',
        borderStyle: 'solid',
        borderColor: isContrastTheme(theme.name) ? theme.palette.black : 'transparent',
        '&.selected': {
            borderColor: isContrastTheme(theme.name) ? theme.palette.accent : theme.palette.black,
        },
    },
}));

export const useImageViewStyles = makeStyles({
    root: {
        width: 'fit-content',
        maxWidth: '100%',
        height: '100%',
    },
    embedded: {
        cursor: 'pointer',
    },
    hidden: {
        display: 'none',
    },
});

export const useEmbeddedImageContentStyles = makeStyles(theme => ({
    root: {
        position: 'relative',
        maxWidth: '100%',
    },
    image: {
        display: 'block',
        objectFit: 'contain',
        width: '100%',
        height: '100%',
    },
    mask: {
        position: 'absolute',
        inset: 0,
        backgroundColor: isDefaultTheme(theme.name) ? 'rgba(0, 0, 0, 0.4)' : 'rgba(0, 0, 0, 0.5)',
    },
    error: {
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        padding: '24px',
        gap: '12px',
    },
    buttons: {
        display: 'flex',
        gap: '12px',
        '.ms-Button': {
            minWidth: '32px',
            padding: 0,
        },
    },
    icon: {
        flex: 1,
        fontSize: '48px',
    },
}));

export const useFullscreenImageContentStyles = makeStyles({
    root: {
        height: '100vh',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'stretch',
        backgroundColor: 'rgba(0, 0, 0, 0.9)',
    },
    header: {
        display: 'flex',
        flexShrink: 0,
        alignItems: 'center',
        justifyContent: 'space-between',
        padding: '16px 24px',
        gap: '16px',
    },
    name: {
        fontSize: FontSizes.size16,
        color: 'white',
        overflow: 'hidden',
        textOverflow: 'ellipsis',
        whiteSpace: 'nowrap',
    },
    controls: {
        display: 'flex',
        gap: '8px',
    },
    button: {
        color: 'white',
    },
    main: {
        flex: 1,
    },
    wrapper: {
        width: 'unset !important',
        height: 'unset !important',
    },
    image: {
        display: 'block',
    },
    message: {
        fontSize: FontSizes.size16,
        color: 'white',
        textAlign: 'center',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        padding: '24px',
    },
});

export const useFallbackViewStyles = makeStyles({
    spinner: {
        display: 'inline-flex',
        margin: '0 4px',
    },
});
