import { IButton, IconButton, ITextField, ITextFieldProps, mergeStyles, TextField } from '@fluentui/react';
import React from 'react';
import { useCollapsibleTextFieldStyles } from './CollapsibleTextField.styles';

const defaultStrings = {
    editButton: (label: string, value: string) => `Edit ${label} "${value}"`,
    doneButton: 'Done',
};

const defaultIconNames = {
    editButton: 'Edit',
    doneButton: 'CheckMark',
};

export interface CollapsibleTextFieldProps
    extends Omit<ITextFieldProps, 'componentRef' | 'onKeyPress' | 'onRenderInput'> {
    strings?: typeof defaultStrings;
    iconNames?: typeof defaultIconNames;
}

export const CollapsibleTextField: React.FC<CollapsibleTextFieldProps> = ({
    label,
    className,
    strings,
    iconNames,
    ...props
}) => {
    const [collapsed, setCollapsed] = React.useState(true);
    const fieldRef = React.useRef<ITextField>(null);
    const buttonRef = React.useRef<IButton>(null);

    const expand = React.useCallback(() => {
        setCollapsed(false);
        requestAnimationFrame(() => fieldRef.current?.focus());
    }, []);

    const collapse = React.useCallback(() => {
        fieldRef.current?.blur();
        setCollapsed(true);
        requestAnimationFrame(() => buttonRef.current?.focus());
    }, []);

    const onKeyPress = React.useCallback(
        (event: React.KeyboardEvent<HTMLInputElement>) => {
            if (event.key === 'Enter') collapse();
        },
        [collapse]
    );

    const stringsInternal = React.useMemo(() => ({ ...defaultStrings, ...strings }), [strings]);
    const iconNamesInternal = React.useMemo(() => ({ ...defaultIconNames, ...iconNames }), [iconNames]);
    const styles = useCollapsibleTextFieldStyles();
    return (
        <TextField
            {...props}
            componentRef={fieldRef}
            className={mergeStyles(
                styles.root,
                collapsed && styles.collapsed,
                props.errorMessage && styles.error,
                className
            )}
            title={label}
            onKeyPress={onKeyPress}
            onRenderInput={(inputProps, defaultRender) =>
                collapsed ? (
                    <>
                        <span
                            id={inputProps?.id}
                            title={inputProps?.title}
                            aria-placeholder={inputProps?.placeholder}
                            aria-required={inputProps?.required}
                            aria-readonly={inputProps?.readOnly}
                            aria-errormessage={inputProps?.['aria-errormessage']}
                            aria-invalid={inputProps?.['aria-invalid']}
                            aria-disabled
                            role='textbox'
                            className={mergeStyles(
                                styles.collapsedText,
                                !inputProps?.value && styles.collapsedPlaceholder
                            )}>
                            {inputProps?.value || inputProps?.placeholder}
                        </span>
                        <IconButton
                            componentRef={buttonRef}
                            className={styles.button}
                            title={stringsInternal.editButton(label ?? '', (inputProps?.value ?? '') as string)}
                            iconProps={{ iconName: iconNamesInternal.editButton }}
                            onClick={expand}
                        />
                    </>
                ) : (
                    <>
                        {defaultRender?.(inputProps)}
                        <IconButton
                            componentRef={buttonRef}
                            className={styles.button}
                            title={stringsInternal.doneButton}
                            iconProps={{ iconName: iconNamesInternal.doneButton }}
                            onClick={collapse}
                        />
                    </>
                )
            }
        />
    );
};
