import { mergeStyles } from '@fluentui/react';
import React from 'react';
import { Form } from '../abstractions';
import { defaultFormFeature, FormContext, FormEntry, FormFeature, validateEntries } from '../implementations';
import { useFormFacadeStyles } from './FormFacade.styles';

/**
 * The ref for the form facade, providing access to the form functionalities.
 */
export interface FormFacadeRef {
    /**
     * Validate the form.
     *
     * @param quietly Whether to suppress the error messages.
     */
    validate: (quietly?: boolean) => void;
}

/**
 * The props for the form facade.
 */
export interface FormFacadeProps extends React.HTMLAttributes<HTMLDivElement> {
    /**
     * The context for the form.
     */
    context: FormContext;

    /**
     * The callback to be invoked when the context changes.
     */
    onContextChange?: (context: FormContext) => void;

    /**
     * The feature for the form.
     */
    feature?: Partial<FormFeature>;
}

/**
 * The form facade component that renders a form with default feature in a single column layout.
 */
export const FormFacade = React.forwardRef<FormFacadeRef, FormFacadeProps>(
    ({ context, onContextChange, feature, className, ...props }, ref) => {
        const featureInternal = React.useMemo(() => ({ ...defaultFormFeature, ...feature }), [feature]);

        // Populate the ref.
        React.useImperativeHandle(
            ref,
            () => ({
                validate: quietly => {
                    const newEntries = validateEntries(context.entries, featureInternal.validators, quietly);
                    onContextChange?.({ ...context, entries: newEntries });
                },
            }),
            [context, onContextChange, featureInternal.validators]
        );

        const styles = useFormFacadeStyles();

        return (
            <div className={mergeStyles(styles.root, className)} {...props}>
                <Form<FormEntry, FormContext, FormFeature>
                    context={context}
                    onContextChange={onContextChange}
                    feature={featureInternal}
                />
            </div>
        );
    }
);
