import { Extension } from '@tiptap/core';

declare module '@tiptap/core' {
    interface Commands<ReturnType> {
        misc: {
            /**
             * Focus the editor after a number of frames to avoid focus being stolen by other elements, for example the
             * ribbon.
             *
             * @param frames Number of frames to wait before focusing the editor, defaults to 0.
             * @param args Arguments to pass to the `focus` command.
             */
            focusLater: (frames?: number, ...args: Parameters<Commands['focus']['focus']>) => ReturnType;
        };
    }
}

export const Misc = Extension.create({
    name: 'misc',

    addCommands() {
        return {
            focusLater:
                (frames = 0, ...args) =>
                ({ commands }) => {
                    schedule(frames, () => commands.focus(...args));
                    return true;
                },
        };
    },
});

function schedule(frames: number, callback: () => void): void {
    if (frames <= 0) {
        callback();
        return;
    }

    requestAnimationFrame(() => schedule(frames - 1, callback));
}
