import { makeStyles } from '@addressbook/components-fluentui-teams-theme';

export const useRestrictionSettingsStyles = makeStyles(theme => ({
    row: {
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center',
        gap: '8px',
        flexWrap: 'wrap',
        maxWidth: '400px',
        '> .ms-Dropdown-container': {
            flexGrow: 3,
            flexBasis: '160px',
        },
    },
    values: {
        flex: '1 0 min-content',

        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center',
        gap: '8px',

        '> .ms-TextField': {
            flexGrow: 1,
            minWidth: '64px',
        },
    },
    between: {
        gap: '20px',

        position: 'relative',
        ':before': {
            content: '""',
            position: 'absolute',
            insetBlock: 'calc((100% - 1px) / 2)',
            insetInline: 'calc((100% - 4px) / 2)',
            backgroundColor: theme.palette.black,
        },
    },
}));
