import { mergeStyles } from '@fluentui/react';
import { Editor } from '@tiptap/core';
import { RichTextFieldTableSize } from '../../types';
import { Link } from '../InsertLink';

export function getCurrentLink(editor: Editor): Link {
    const url = editor.isActive('link') ? editor.getAttributes('link').href : '';
    const selection = editor.state.selection;
    const displayText = editor.state.doc.textBetween(selection.from, selection.to);
    return { url, displayText };
}

export const maxTableSize: RichTextFieldTableSize = { rows: 6, cols: 6 };

export const menuCalloutClassName = mergeStyles({
    '.ms-Callout-main': {
        padding: 0,
    },
    '.ms-ContextualMenu': {
        minWidth: 0,
        width: 'auto',
    },
});

export const colorMenuCalloutClassName = mergeStyles(menuCalloutClassName, {
    '.ms-ContextualMenu-list': {
        display: 'flex',
        flexWrap: 'wrap',
        alignItems: 'center',
        justifyContent: 'center',
        gap: '12px',
        padding: '8px',
    },
});

export function hasExtensionByName(editor: Editor, name: string): boolean {
    return editor.extensionManager.extensions.some(extension => extension.name === name);
}
