import { CommandBar, ICommandBarItemProps } from '@fluentui/react';
import { isDefined } from '@addressbook/utilities-core';
import { useRenderings } from '@addressbook/utilities-hooks';
import React from 'react';
import { compactRibbonItemGroups, defaultRibbonItemGroups, getItemProps } from './RichTextRibbon.items';
import { useRibbonCommandBarStyles, useRibbonStyles } from './RichTextRibbon.styles';
import { RichTextRibbonProps } from './RichTextRibbon.types';

/**
 * The ribbon component for the rich text editor.
 */
export const RichTextRibbon: React.FC<RichTextRibbonProps> = ({ editor, compact, callbacks = {}, strings }) => {
    const styles = useRibbonStyles();
    const commandBarStyles = useRibbonCommandBarStyles();
    const { renderings, render, unrender } = useRenderings();

    if (!editor) return null;

    const ribbonItemGroups = compact ? compactRibbonItemGroups : defaultRibbonItemGroups;
    const ribbonItemParams = { editor, compact, callbacks, strings, render, unrender };
    const items: ICommandBarItemProps[] = ribbonItemGroups.flatMap(ribbonItemGroup => {
        const group = ribbonItemGroup.map(ribbonItem => getItemProps(ribbonItem, ribbonItemParams)).filter(isDefined);
        if (group.length) group[0].className = styles.leadingButton;
        return group;
    });

    return (
        <div>
            <CommandBar styles={commandBarStyles} items={items} />
            {renderings}
        </div>
    );
};
