/* istanbul ignore file */

import { IScrollablePaneStyles, ITheme } from '@fluentui/react';

export function getScrollablePaneStyles(_themeName: string, _theme: ITheme) {
    return (): Partial<IScrollablePaneStyles> => {
        const styles = {
            root: {
                fontFamily: 'inherit',
            },
        };

        return styles;
    };
}
