/* istanbul ignore file */

import {
    FontWeights,
    ICheckStyleProps,
    ICheckStyles,
    IDetailsListStyleProps,
    IDetailsListStyles,
    IDetailsRowStyleProps,
    IDetailsRowStyles,
    ITheme,
} from '@fluentui/react';
import { FontSizes } from '../teams-sizes';
import { isContrastTheme } from '../teams-theme';

export function getCheckStyles(_themeName: string, _theme: ITheme) {
    return (props: ICheckStyleProps): Partial<ICheckStyles> => {
        const { theme, checked } = props;
        const { palette } = theme;

        const styles = {
            circle: [!checked && { color: palette.neutralSecondary }],
            check: [!checked && { color: palette.neutralSecondary }],
        };

        return styles;
    };
}
export function getDetailsListStyles(themeName: string, _theme: ITheme) {
    return (props: IDetailsListStyleProps): Partial<IDetailsListStyles> => {
        const { theme } = props;
        const { palette } = theme;

        const TEXT_COLOR = palette.black;
        const DETAILS_HEADER_BORDER_COLOR = palette.neutralQuaternary;

        const styles = {
            root: {
                background: 'transparent',
                selectors: {
                    '.ms-GroupHeader': {
                        background: palette.neutralLighterAlt,
                        color: TEXT_COLOR,
                        borderBottom: 'none',
                        selectors: {
                            ':hover': {
                                background: palette.neutralLighterAlt,
                                color: TEXT_COLOR,
                                selectors: {
                                    '.ms-GroupHeader-expand': {
                                        color: palette.neutralDark,
                                    },
                                },
                            },
                            '.ms-GroupSpacer': {
                                width: '40px !important', // Needed to override inline css
                            },
                            '.ms-GroupHeader-title': {
                                fontSize: FontSizes.size14,
                                fontWeight: FontWeights.bold,
                                selectors: {
                                    "[class^='headerCount-']": {
                                        fontWeight: FontWeights.regular,
                                    },
                                },
                            },
                            '.ms-GroupHeader-expand': {
                                color: palette.neutralDark,
                                selectors: {
                                    ':hover': {
                                        background: 'transparent',
                                        color: palette.themePrimary,
                                        selectors: {
                                            i: {
                                                fontWeight: FontWeights.bold,
                                            },
                                        },
                                    },
                                    i: {
                                        fontSize: FontSizes.size12,
                                    },
                                },
                            },
                        },
                    },
                },
            },
            headerWrapper: {
                selectors: {
                    '.ms-DetailsHeader': {
                        height: '32px',
                        lineHeight: '32px',
                        backgroundColor: 'transparent',
                        borderBottomColor: DETAILS_HEADER_BORDER_COLOR,
                        /**
                         * Override min-width calculated by items-view
                         */
                        minWidth: '100% !important',
                        padding: 0,
                        selectors: {
                            '.ms-DetailsHeader-cell': {
                                height: '32px',
                            },
                            '.ms-DetailsHeader-cellName': {
                                fontSize: FontSizes.size12,
                                fontWeight: FontWeights.regular,
                                display: 'flex',
                                alignItems: 'center',
                            },
                            '.ms-DetailsRow-check': {
                                width: '40px',
                                height: '32px',
                            },
                            '.is-actionable': {
                                selectors: {
                                    '.ms-DetailsHeader-cellTitle': {
                                        selectors: {
                                            i: {
                                                color: TEXT_COLOR,
                                                position: 'relative' as const,
                                                bottom: '2px',
                                            },
                                        },
                                    },
                                    ':hover': {
                                        background: palette.neutralLighter,
                                        color: TEXT_COLOR,
                                        selectors: {
                                            '.ms-DetailsHeader-cellTitle': {
                                                color: TEXT_COLOR,
                                                selectors: {
                                                    i: {
                                                        color: TEXT_COLOR,
                                                    },
                                                },
                                            },
                                        },
                                    },
                                },
                            },
                            "[class*='cellIsGroupExpander-']": {
                                background: 'transparent',
                                color: TEXT_COLOR,
                                selectors: {
                                    ':hover': {
                                        background: palette.neutralLighter,
                                        color: TEXT_COLOR,
                                    },
                                },
                            },
                            '.ms-DetailsHeader-cellTitle': {
                                alignItems: 'center',
                                color: TEXT_COLOR,
                            },
                            '.ms-DetailsHeader-filterChevron': {
                                paddingTop: '2px',
                            },
                            ':hover .ms-DetailsHeader-cellTitle': {
                                background: 'none',
                            },
                        },
                    },
                    '.ms-DetailsHeader-cellSizer': {
                        cursor: 'ew-resize',
                    },
                },
            },
            contentWrapper: {
                '.ms-DetailsRow': {
                    ':hover': {
                        backgroundColor: isContrastTheme(themeName)
                            ? theme.palette.themeTertiary
                            : theme.palette.neutralQuaternaryAlt,
                    },
                    ':active': {
                        backgroundColor: theme.palette.neutralTertiaryAlt,
                    },
                    '::after': {
                        // Override the default border color when selected, whose contrast is not enough
                        borderColor: `${palette.black} !important`,
                    },
                },
            },
        };

        // Contrast theme overrides
        if (isContrastTheme(themeName)) {
            styles.headerWrapper.selectors['.ms-DetailsHeader'].borderBottomColor = palette.neutralTertiaryAlt;

            styles.headerWrapper.selectors['.ms-DetailsHeader'].selectors['.is-actionable'].selectors[
                ':hover'
            ].background = palette.themeDark;

            styles.headerWrapper.selectors['.ms-DetailsHeader'].selectors['.is-actionable'].selectors[':hover'].color =
                palette.white;
            styles.headerWrapper.selectors['.ms-DetailsHeader'].selectors['.is-actionable'].selectors[
                ':hover'
            ].selectors['.ms-DetailsHeader-cellTitle'].selectors.i.color = palette.white;
            styles.headerWrapper.selectors['.ms-DetailsHeader'].selectors['.is-actionable'].selectors[
                ':hover'
            ].selectors['.ms-DetailsHeader-cellTitle'].color = palette.white;
            styles.headerWrapper.selectors['.ms-DetailsHeader'].selectors['.ms-DetailsHeader-cellTitle'].color =
                palette.black;

            styles.headerWrapper.selectors['.ms-DetailsHeader'].selectors["[class*='cellIsGroupExpander-']"].selectors[
                ':hover'
            ].background = palette.themeDark;
            styles.headerWrapper.selectors['.ms-DetailsHeader'].selectors["[class*='cellIsGroupExpander-']"].selectors[
                ':hover'
            ].color = palette.white;

            // Group Header Styling
            styles.root.selectors['.ms-GroupHeader'].selectors[':hover'].color = theme.palette.white;
            styles.root.selectors['.ms-GroupHeader'].selectors[':hover'].background = palette.themeDark;
            styles.root.selectors['.ms-GroupHeader'].selectors[':hover'].selectors['.ms-GroupHeader-expand'].color =
                theme.palette.white;
            styles.root.selectors['.ms-GroupHeader'].selectors['.ms-GroupHeader-expand'].selectors[':hover'].color =
                theme.palette.white;
        }

        return styles;
    };
}
export function getDetailsRowStyles(themeName: string, _theme: ITheme) {
    return (props: IDetailsRowStyleProps): Partial<IDetailsRowStyles> => {
        const { theme, isSelected } = props;
        const { palette } = theme;

        let ROW_HOVER_BACKGROUND_COLOR = palette.neutralLight;
        let SELECTED_ROW_BACKGROUND_COLOR = palette.white;
        const ROW_BORDER_COLOR = palette.neutralLighter;

        const TEXT_COLOR = palette.black;
        const LINK_TEXT_COLOR = palette.black;
        let LINK_TEXT_HOVER_COLOR = palette.black;
        let SELECTED_HOVER_COLOR = palette.black;

        const HERO_BUTTON = "[class*='heroButton']";

        // Contrast theme overrides
        if (isContrastTheme(themeName)) {
            ROW_HOVER_BACKGROUND_COLOR = palette.themeTertiary;
            SELECTED_ROW_BACKGROUND_COLOR = palette.themePrimary;
            SELECTED_HOVER_COLOR = palette.white;
            LINK_TEXT_HOVER_COLOR = palette.white;
        }

        const styles = {
            root: [
                {
                    background: 'transparent',
                    color: TEXT_COLOR,
                    borderBottomColor: ROW_BORDER_COLOR,
                    fontSize: FontSizes.size14,
                    /**
                     * Override min-width calculated by items-view
                     */
                    minWidth: '100% !important',
                    selectors: {
                        '.ms-DetailsRow-cell': {
                            color: TEXT_COLOR,
                            selectors: {
                                '.ms-DetailsRow-check': {
                                    height: '100%',
                                    width: '40px',
                                },
                                '.ms-Button--icon': {
                                    color: 'inherit',
                                },
                            },
                        },
                        '.ms-Details': {
                            selectors: {
                                '.ms-Button': {
                                    background: 'transparent',
                                },
                            },
                        },
                        '.ms-Link': {
                            // Need it to override all state (i.e. focus, hover, active) styles for the ms-link component
                            color: `${LINK_TEXT_COLOR} !important`,
                        },
                        ':hover': {
                            background: ROW_HOVER_BACKGROUND_COLOR,
                            selectors: {
                                '.ms-DetailsRow-cell': {
                                    color: SELECTED_HOVER_COLOR,
                                },
                                // Need it to override all state (i.e. focus, hover) styles for the ms-link component
                                '.ms-Link': {
                                    color: `${LINK_TEXT_HOVER_COLOR} !important`,
                                },
                                '.ms-TooltipHost': {
                                    color: SELECTED_HOVER_COLOR,
                                },
                            },
                        },
                        [HERO_BUTTON]: {
                            background: 'transparent',
                            selectors: {
                                ':hover': {
                                    background: 'transparent',
                                    selectors: {
                                        i: {
                                            fontWeight: FontWeights.bold,
                                        },
                                    },
                                },
                            },
                        },
                        '.ms-Button--icon': {
                            width: '2rem',
                        },
                    },
                },
                isSelected && [
                    {
                        background: SELECTED_ROW_BACKGROUND_COLOR,

                        selectors: {
                            '.ms-DetailsRow-cell': {
                                color: SELECTED_HOVER_COLOR,
                            },
                            // Need it to override all state (i.e. focus, hover, active) styles for the ms-link component
                            '.ms-Link': {
                                color: `${LINK_TEXT_COLOR} !important`,
                            },
                            ':focus': {
                                background: SELECTED_ROW_BACKGROUND_COLOR,
                                color: SELECTED_HOVER_COLOR,
                                selectors: {
                                    '.ms-DetailsRow-cell': {
                                        color: SELECTED_HOVER_COLOR,
                                    },
                                    // Need it to override all state (i.e. focus, hover) styles for the ms-link component
                                    '.ms-Link': {
                                        color: `${LINK_TEXT_HOVER_COLOR} !important`,
                                    },
                                    ':hover': {
                                        background: ROW_HOVER_BACKGROUND_COLOR,
                                    },
                                },
                            },
                            ':hover': {
                                background: ROW_HOVER_BACKGROUND_COLOR,
                            },
                        },
                    },
                ],
            ],
            cell: {
                margin: 'auto',
            },
        } as Partial<IDetailsRowStyles>;

        return styles;
    };
}
