import { DefaultButton, Icon, IconButton, Layer, mergeStyles, Popup, Spinner, SpinnerSize } from '@fluentui/react';
import { useBoolean } from '@fluentui/react-hooks';
import { copyImageToClipboard, ImageSize } from '@addressbook/utilities-core';
import { useBlobObjectUrl } from '@addressbook/utilities-hooks';
import React from 'react';
import { TransformComponent, TransformWrapper } from 'react-zoom-pan-pinch';
import { RichTextFieldButton } from '../../types';
import { useEmbeddedImageContentStyles, useFullscreenImageContentStyles, useImageViewStyles } from './styles';
import { FileOptions } from './types';
import { accommodateImage, AccomodatePolicy } from './utils';

export interface ImageViewProps extends React.PropsWithChildren<React.HTMLAttributes<HTMLDivElement>> {
    name?: string;
    url?: string;
    blob?: Blob;
    thumbnail?: Blob;
    imageSize?: ImageSize;
    loading?: boolean;
    error?: Error;
    onRetry?: () => void;
    onRemove?: () => void;
    compact?: boolean;
    strings: FileOptions['strings'];
}

export const ImageView: React.FC<ImageViewProps> = ({
    name,
    url,
    blob,
    thumbnail,
    imageSize,
    loading,
    error,
    onRetry,
    onRemove,
    compact,
    strings,
    children,
    className,
    ...props
}) => {
    const [fullscreen, { setTrue: enterFullscreen, setFalse: exitFullscreen }] = useBoolean(false);
    const contentProps = { name, url, blob, thumbnail, imageSize, loading, error, onRetry, onRemove, compact, strings };

    const styles = useImageViewStyles();
    return (
        <div className={mergeStyles(styles.root, className)} {...props}>
            <EmbeddedImageContent className={styles.embedded} onClick={enterFullscreen} {...contentProps} />
            <FullscreenImageContent isOpen={fullscreen} onDismiss={exitFullscreen} {...contentProps} />
            <div className={styles.hidden}>{children}</div>
        </div>
    );
};

const embeddedHeight = 180;

const EmbeddedImageContent: React.FC<ImageViewProps> = ({
    name,
    blob,
    url: _url,
    thumbnail,
    imageSize,
    loading,
    error,
    onRetry,
    onRemove,
    compact: _compact,
    strings,
    className,
    style,
    ...props
}) => {
    const blobUrl = useBlobObjectUrl(blob ?? thumbnail);

    const height = embeddedHeight;
    const width = imageSize ? height * (imageSize.width / imageSize.height) : height;

    const styles = useEmbeddedImageContentStyles();
    return (
        <div className={mergeStyles(styles.root, className)} style={{ width, height, ...style }} {...props}>
            {blobUrl && <img className={styles.image} src={blobUrl} alt={name} title={name} />}
            {error && (
                <div
                    className={mergeStyles(styles.mask, styles.error)}
                    role='alert'
                    title={strings.messageContentUnavailable}>
                    <Icon className={styles.icon} iconName='ImageOff' />
                    <div className={styles.buttons}>
                        {onRetry && (
                            <DefaultButton
                                onClick={event => {
                                    event.stopPropagation();
                                    onRetry();
                                }}
                                iconProps={{ iconName: 'ArrowClockwise' }}
                                title={strings.button(RichTextFieldButton.Retry)}
                            />
                        )}
                        {onRemove && (
                            <DefaultButton
                                onClick={event => {
                                    event.stopPropagation();
                                    onRemove();
                                }}
                                iconProps={{ iconName: 'Delete' }}
                                title={strings.button(RichTextFieldButton.Remove)}
                            />
                        )}
                    </div>
                </div>
            )}
            {loading && <Spinner className={styles.mask} size={SpinnerSize.large} />}
        </div>
    );
};

interface FullscreenImageContentProps extends ImageViewProps {
    isOpen?: boolean;
    onDismiss?: () => void;
}

const FullscreenImageContent: React.FC<FullscreenImageContentProps> = ({
    name,
    blob,
    url,
    thumbnail: _thumbnail,
    imageSize: _imageSize,
    loading,
    error,
    onRetry: _onRetry,
    onRemove: _onRemove,
    compact,
    strings,
    isOpen,
    onDismiss,
    className,
    ...props
}) => {
    const blobUrl = useBlobObjectUrl(blob);

    const styles = useFullscreenImageContentStyles();
    if (!isOpen) return null;
    return (
        <Layer eventBubblingEnabled>
            <Popup
                className={mergeStyles(styles.root, className)}
                onDismiss={onDismiss}
                onRestoreFocus={({ originalElement }) => originalElement?.focus()}
                {...props}>
                <div className={styles.header}>
                    <div className={styles.name} title={name}>
                        {name}
                    </div>
                    <div className={styles.controls}>
                        {!compact && blobUrl && (
                            <IconButton
                                className={styles.button}
                                as='a'
                                href={blobUrl}
                                download={name}
                                iconProps={{ iconName: 'ArrowDownload' }}
                                title={strings.button(RichTextFieldButton.Download)}
                            />
                        )}
                        {!compact && blob && (
                            <IconButton
                                className={styles.button}
                                onClick={() => {
                                    copyImageToClipboard({ blob, name });
                                }}
                                iconProps={{ iconName: 'Copy' }}
                                title={strings.button(RichTextFieldButton.Copy)}
                            />
                        )}
                        {url && (
                            <IconButton
                                className={styles.button}
                                as='a'
                                href={url}
                                target='_blank'
                                rel='noopener noreferrer'
                                iconProps={{ iconName: 'Open' }}
                                title={strings.button(RichTextFieldButton.ViewOriginalFile)}
                            />
                        )}
                        <IconButton
                            className={styles.button}
                            iconProps={{ iconName: 'Cancel' }}
                            onClick={onDismiss}
                            title={strings.button(RichTextFieldButton.Close)}
                        />
                    </div>
                </div>
                {blobUrl ? (
                    <TransformedImage
                        src={blobUrl}
                        alt={name}
                        className={styles.image}
                        wrapperClassName={mergeStyles(styles.main, styles.wrapper)}
                    />
                ) : loading ? (
                    <Spinner className={styles.main} size={SpinnerSize.large} />
                ) : error ? (
                    <div className={mergeStyles(styles.main, styles.message)} role='alert'>
                        {strings.messageContentUnavailable}
                    </div>
                ) : null}
            </Popup>
        </Layer>
    );
};

interface TransformedImageProps extends Omit<React.ImgHTMLAttributes<HTMLImageElement>, 'onLoad'> {
    wrapperClassName?: string;
}

const TransformedImage: React.FC<TransformedImageProps> = ({ wrapperClassName, ...props }) => {
    const [disabled, setDisabled] = React.useState(true);
    const [panning, { setTrue: startPanning, setFalse: stopPanning }] = useBoolean(false);

    const wrapperStyle = React.useMemo(() => ({ cursor: panning ? 'grabbing' : 'grab' }), [panning]);

    return (
        <TransformWrapper
            disabled={disabled}
            minScale={0.5}
            maxScale={4}
            onPanningStart={startPanning}
            onPanningStop={stopPanning}>
            {({ centerView }) => (
                <TransformComponent wrapperClass={wrapperClassName} wrapperStyle={wrapperStyle}>
                    <img
                        {...props}
                        onLoad={event => {
                            setDisabled(false);
                            accommodateImage(event.target as HTMLImageElement, {
                                selector: '.react-transform-wrapper',
                                policy: AccomodatePolicy.ScaleDown,
                            });
                            centerView(1, 0);
                        }}
                    />
                </TransformComponent>
            )}
        </TransformWrapper>
    );
};
