import React from 'react';
import { RenderFunction } from '../../abstractions';
import { DesigningTrailingActions } from '../components';
import { FormContext, FormFeature, FormMode } from '../types';

export const defaultTrailingRender: RenderFunction<FormContext, FormFeature> = props => {
    switch (props.context.mode) {
        case FormMode.Designing:
            return <DesigningTrailingActions {...props} />;
        default:
            return null;
    }
};
