import { ITextFieldProps, TextField } from '@fluentui/react';
import { parseFloatStrictly } from '@addressbook/utilities-core';
import React from 'react';

export interface DecimalTextFieldProps
    extends Omit<ITextFieldProps, 'componentRef' | 'value' | 'onChange' | 'inputMode'> {
    value: number;
    onChange: (value: number) => void;
}

/**
 * A text field that only accepts decimal numbers.
 */
export const DecimalTextField: React.FC<DecimalTextFieldProps> = ({ value, onChange, invalid, ...props }) => {
    const [pendingValue, setPendingValue] = React.useState<string>(() => String(value));
    const pendingValueRef = React.useRef(pendingValue);
    pendingValueRef.current = pendingValue;

    const onChangeRef = React.useRef(onChange);
    onChangeRef.current = onChange;

    const committedValueRef = React.useRef(value);
    const commit = React.useCallback((pendingValue: string) => {
        const parsedValue = parseFloatStrictly(pendingValue);
        if (isNaN(parsedValue)) return;
        onChangeRef.current(parsedValue);
        committedValueRef.current = parsedValue;
    }, []);

    React.useEffect(() => {
        if (value === committedValueRef.current) return;
        setPendingValue(String(value));
    }, [value]);

    const synchronize = React.useCallback((value: number) => {
        const stringValue = String(value);
        if (pendingValueRef.current === stringValue) return;
        setPendingValue(String(value));
    }, []);

    return (
        <TextField
            {...props}
            value={pendingValue}
            onChange={(_, newValue = '') => {
                setPendingValue(newValue);
                commit(newValue);
            }}
            onBlur={() => synchronize(value)}
            invalid={invalid || isNaN(parseFloatStrictly(pendingValue))}
            inputMode='decimal'
        />
    );
};
