const backgroundColor: RibbonItemDescriptor = {
    key: 'background-color',
    text: params => params.strings.button(RichTextFieldButton.BackgroundColor),
    icon: 'FabricTextHighlight',
    supported: ({ editor }) => hasExtensionByName(editor, BackgroundColor.name),
    props: ({ editor, strings }) => ({
        subMenuProps: {
            calloutProps: { className: colorMenuCalloutClassName, calloutWidth: 120 },
            items: backgroundColorValues.map(([key, value]) => ({
                key: RichTextFieldBackgroundColor[key],
                onRender: () => (
                    <ColorMenuItem
                        text={strings.backgroundColor(key)}
                        color={value}
                        onClick={() =>
                            // The first focus is to close the callout. The second focus is to focus back.
                            value
                                ? editor.chain().focus().focusLater(2).setBackgroundColor(value).run()
                                : editor.chain().focus().focusLater(2).unsetBackgroundColor().run()
                        }
                    />
                ),
            })),
        },
    }),
};

