const decreaseIndent: RibbonItemDescriptor = {
    key: 'decrease-indent',
    text: params => params.strings.button(RichTextFieldButton.DecreaseIndent),
    icon: 'DecreaseIndentLegacy',
    supported: ({ editor }) => hasExtensionByName(editor, ListItem.name) && hasExtensionByName(editor, Blockquote.name),
    disabled: params =>
        !params.editor
            .can()
            .chain()
            .first([({ commands }) => commands.liftListItem('listItem'), ({ commands }) => commands.unsetBlockquote()])
            .run(),
    onClick: params =>
        params.editor
            .chain()
            .focus()
            .first([({ commands }) => commands.liftListItem('listItem'), ({ commands }) => commands.unsetBlockquote()])
            .run(),
};

