const insertTable: RibbonItemDescriptor = {
    key: 'insert-table',
    text: params => params.strings.button(RichTextFieldButton.InsertTable),
    icon: 'Table',
    supported: ({ editor }) => hasExtensionByName(editor, Table.name),
    props: ({ editor, strings }) => ({
        subMenuProps: {
            calloutProps: { className: menuCalloutClassName },
            items: [{ key: 'insert-table' }],
            onRenderMenuList: () => (
                <InsertTable
                    maxTableSize={maxTableSize}
                    strings={strings}
                    onConfirm={tableSize =>
                        // The first focus is to close the callout. The second focus is to focus back.
                        editor
                            .chain()
                            .focus()
                            .focusLater(2)
                            .insertTable({ ...tableSize, withHeaderRow: false })
                            .run()
                    }
                />
            ),
        },
    }),
};

