import { PrimaryButton } from '@fluentui/react';
import React from 'react';
import { FormProps } from '../../../abstractions';
import { FormContext, FormFeature, QuestionAction } from '../../types';
import { addNewFormEntry } from '../../utilities';
import { useDesigningTrailingActionsStyles } from './DesigningTrailingActions.styles';

export function DesigningTrailingActions({
    context,
    onContextChange,
    feature,
}: FormProps<FormContext, FormFeature>): JSX.Element {
    const { entries } = context;
    const { comparator, idGenerator, orderGenerator, constants, strings } = feature;

    const reachMaxEntries = entries.length >= constants.maxEntries;

    const addEntry = React.useCallback(() => {
        const trailingEntry = [...entries].sort(comparator).pop();
        const trailingSlotOrder = orderGenerator(trailingEntry?.question.order, undefined);
        const newEntries = addNewFormEntry(entries, { idGenerator, order: trailingSlotOrder, strings });
        onContextChange?.({ ...context, entries: newEntries });
    }, [comparator, context, entries, idGenerator, onContextChange, orderGenerator, strings]);

    const styles = useDesigningTrailingActionsStyles();

    return (
        <div className={styles.root}>
            <PrimaryButton
                iconProps={{ iconName: 'Add' }}
                text={strings.questionAction(QuestionAction.Add)}
                onClick={addEntry}
                disabled={reachMaxEntries}
            />
        </div>
    );
}
