import { mergeStyles } from '@fluentui/react';
import { NodeViewProps } from '@tiptap/core';
import { NodeViewContent, NodeViewWrapper } from '@tiptap/react';
import { isImageMime } from '@addressbook/utilities-core';
import React from 'react';
import { getCompactByExtension, getStringsByExtension } from '../utils';
import { defaultFileSession, FileSessions, isValidFileSession } from './sessions';
import { useFileNodeViewStyles } from './styles';
import { useDeleteNode, useUpdateAttributes } from './utils';
import { FallbackView } from './view.fallback';
import { ImageView } from './view.image';

export type FileNodeViewProps = NodeViewProps & {
    node: {
        attrs: FileNodeViewAttributes;
    };
};

interface FileNodeViewAttributes {
    href: string | null;
    target: string | null;
    rel: string | null;
    mime: string | null;
    error: true | null;
    key: string;
}

export const FileNodeView: React.FC<FileNodeViewProps> = ({ editor, node, selected, extension }) => {
    const strings = React.useMemo(() => getStringsByExtension(extension), [extension]);
    const compact = React.useMemo(() => getCompactByExtension(extension), [extension]);

    // Prepare variables for the node rendering
    const name = node.textContent;
    const { target, rel, mime, key } = node.attrs;
    const isImage = Boolean(mime && isImageMime(mime));

    // Prepare functions for node operations
    const updateAttributes = useUpdateAttributes<FileNodeViewAttributes>({ editor, key });
    const deleteNode = useDeleteNode({ editor, key });

    // Derive the session from the extension storage
    const sessions = extension.storage as FileSessions;
    const [session, setSession] = React.useState(() => sessions.get(key) ?? defaultFileSession);

    React.useEffect(
        () =>
            sessions.subscribe(key, session => {
                setSession(session);

                // Workaround for "flushSync was called from inside a lifecycle method. React cannot flush when React is
                // already rendering. Consider moving this call to a scheduler task or micro task."
                // See https://github.com/ueberdosis/tiptap/issues/3764
                queueMicrotask(() =>
                    updateAttributes({ href: session.url ?? null, error: session.error ? true : null }, true)
                );
            }),
        [key, sessions, updateAttributes]
    );

    // Ensure the session is valid
    React.useEffect(() => {
        if (isValidFileSession(session)) return;

        // Workaround for "flushSync was called from inside a lifecycle method. React cannot flush when React is
        // already rendering. Consider moving this call to a scheduler task or micro task."
        // See https://github.com/ueberdosis/tiptap/issues/3764
        queueMicrotask(() => deleteNode(true));
    }, [deleteNode, session]);

    const remove = React.useMemo(
        () => (editor.isEditable ? () => deleteNode() : undefined),
        [deleteNode, editor.isEditable]
    );

    const styles = useFileNodeViewStyles();
    const content = <NodeViewContent as='span' />;
    return (
        <NodeViewWrapper
            as='span'
            className={mergeStyles(
                styles.root,
                selected && 'selected',
                session.error && styles.error,
                isImage && styles.image
            )}
            data-drag-handle
            contentEditable={false}>
            {isImage ? (
                <ImageView
                    name={name}
                    blob={session.blob}
                    url={session.url}
                    thumbnail={session.thumbnail}
                    imageSize={session.imageSize}
                    loading={session.loading}
                    error={session.error}
                    onRetry={session.retry}
                    onRemove={remove}
                    compact={compact}
                    strings={strings}>
                    {content}
                </ImageView>
            ) : (
                <FallbackView
                    href={session.url}
                    target={target || undefined}
                    rel={rel || undefined}
                    thumbnail={session.thumbnail}
                    loading={session.loading}>
                    {content}
                </FallbackView>
            )}
        </NodeViewWrapper>
    );
};
