/* istanbul ignore file */

import { IButtonStyles, ITheme, IRawStyle } from '@fluentui/react';
import { ShadowLevels } from '../teams-shadow-levels';
import { ContrastColors } from '../teams-colors';
import { isContrastTheme, isDarkTheme } from '../teams-theme';
import { teamsBorderRadius } from './constants';

export function getPrimaryButtonStyles(themeName: string, theme: ITheme): Partial<IButtonStyles> {
    let iconFillColor = theme.palette.white;
    const buttonBoxShadow = ShadowLevels.shadowLevel1Dark;
    const buttonBorderRadius = teamsBorderRadius;
    const rootColor = theme.palette.themePrimary;
    const textColor = theme.palette.white;
    const rootHoverColor = theme.palette.themeDark;
    const rootPressedCheckedColor = theme.palette.themeDarker;

    if (isContrastTheme(themeName)) {
        iconFillColor = theme.palette.white;
    }

    if (isDarkTheme(themeName)) {
        iconFillColor = theme.palette.black;
    }

    const styles = {
        root: {
            backgroundColor: rootColor,
            color: textColor,
            boxShadow: buttonBoxShadow,
            borderColor: rootColor,
            selectors: {
                'svg, .icons-default-fill': {
                    fill: iconFillColor,
                },
            },
        },
        rootHovered: {
            backgroundColor: rootHoverColor,
            borderColor: rootHoverColor,
            color: textColor,
            selectors: {
                'svg, .icons-default-fill': {
                    fill: iconFillColor,
                },
            },
        },
        rootPressed: {
            backgroundColor: rootPressedCheckedColor,
            borderColor: rootPressedCheckedColor,
            color: textColor,
            selectors: {
                'svg, .icons-default-fill': {
                    fill: iconFillColor,
                },
            },
        },
        rootChecked: {
            backgroundColor: rootPressedCheckedColor,
            borderColor: rootPressedCheckedColor,
            color: textColor,
            selectors: {
                'svg, .icons-default-fill': {
                    fill: iconFillColor,
                },
            },
        },
        rootCheckedHovered: {
            backgroundColor: rootHoverColor,
            borderColor: rootHoverColor,
            color: textColor,
            selectors: {
                'svg, .icons-default-fill': {
                    fill: iconFillColor,
                },
            },
        },
        rootDisabled: {
            backgroundColor: theme.palette.neutralQuaternary,
            borderColor: theme.palette.neutralQuaternary,
            color: theme.palette.neutralTertiary,
            selectors: {
                'svg, .icons-default-fill': {
                    fill: theme.palette.neutralTertiary,
                },
            },
        },
        rootFocused: {
            backgroundColor: rootColor,
            borderColor: rootColor,
            borderRadius: buttonBorderRadius,
            color: textColor,
            selectors: {
                'svg, .icons-default-fill': {
                    fill: iconFillColor,
                },
            },
        },
    } as Partial<IButtonStyles>;

    // Dark theme overrides
    if (isDarkTheme(themeName)) {
        // Root
        (styles.root as IRawStyle).color = theme.palette.black;
        (styles.root as IRawStyle).backgroundColor = theme.palette.themeTertiary;
        (styles.root as IRawStyle).borderColor = theme.palette.themeTertiary;

        // Root Hovered
        (styles.rootHovered as IRawStyle).color = theme.palette.black;
        (styles.rootHovered as IRawStyle).backgroundColor = theme.palette.themeSecondary;
        (styles.rootHovered as IRawStyle).borderColor = theme.palette.themeSecondary;
        ((styles.rootHovered as Required<IRawStyle>).selectors['svg, .icons-default-fill'] as IRawStyle).fill =
            theme.palette.black;

        // Root Checked
        (styles.rootChecked as IRawStyle).color = theme.palette.black;
        (styles.rootChecked as IRawStyle).backgroundColor = theme.palette.themeLight;
        (styles.rootChecked as IRawStyle).borderColor = theme.palette.themeLight;
        ((styles.rootChecked as Required<IRawStyle>).selectors['svg, .icons-default-fill'] as IRawStyle).fill =
            theme.palette.black;

        // Root Checked Hovered
        (styles.rootCheckedHovered as IRawStyle).color = theme.palette.black;
        (styles.rootCheckedHovered as IRawStyle).backgroundColor = theme.palette.themeSecondary;
        (styles.rootCheckedHovered as IRawStyle).borderColor = theme.palette.themeSecondary;
        ((styles.rootCheckedHovered as Required<IRawStyle>).selectors['svg, .icons-default-fill'] as IRawStyle).fill =
            theme.palette.black;

        // Root Pressed
        (styles.rootPressed as IRawStyle).color = theme.palette.black;
        (styles.rootPressed as IRawStyle).backgroundColor = theme.palette.themeLight;
        (styles.rootPressed as IRawStyle).borderColor = theme.palette.themeLight;
        ((styles.rootPressed as Required<IRawStyle>).selectors['svg, .icons-default-fill'] as IRawStyle).fill =
            theme.palette.black;

        // Root Focused
        (styles.rootFocused as IRawStyle).backgroundColor = theme.palette.themeTertiary;
        (styles.rootFocused as IRawStyle).borderColor = theme.palette.themeTertiary;
        (styles.rootFocused as IRawStyle).color = theme.palette.black;
        ((styles.rootFocused as Required<IRawStyle>).selectors['svg, .icons-default-fill'] as IRawStyle).fill =
            theme.palette.black;

        // Root Disabled
        ((styles.rootDisabled as Required<IRawStyle>).selectors['svg, .icons-default-fill'] as IRawStyle).fill =
            theme.palette.neutralTertiary;
    }

    // Contrast theme overrides
    if (isContrastTheme(themeName)) {
        // Root
        (styles.root as IRawStyle).color = theme.palette.white;
        (styles.root as IRawStyle).backgroundColor = theme.palette.black;
        (styles.root as IRawStyle).borderColor = theme.palette.black;

        // Root Hovered
        (styles.rootHovered as IRawStyle).backgroundColor = theme.palette.themeDark;
        (styles.rootHovered as IRawStyle).borderColor = theme.palette.themeDark;
        (styles.rootHovered as IRawStyle).borderWidth = '2px';

        // Root Pressed
        (styles.rootPressed as IRawStyle).backgroundColor = theme.palette.themeDark;
        (styles.rootPressed as IRawStyle).borderColor = theme.palette.themeDark;

        // Root Checked
        (styles.rootChecked as IRawStyle).backgroundColor = theme.palette.themePrimary;
        (styles.rootChecked as IRawStyle).borderColor = theme.palette.themePrimary;

        // Root Checked Hovered
        (styles.rootChecked as IRawStyle).backgroundColor = theme.palette.themeDark;
        (styles.rootChecked as IRawStyle).borderColor = theme.palette.themeDark;

        // Root Disabled
        (styles.rootDisabled as IRawStyle).color = theme.palette.white;
        (styles.rootDisabled as IRawStyle).backgroundColor = ContrastColors.appGreenContrast;
        (styles.rootDisabled as IRawStyle).borderColor = ContrastColors.appGreenContrast;
        ((styles.rootDisabled as Required<IRawStyle>).selectors['svg, .icons-default-fill'] as IRawStyle).fill =
            theme.palette.white;

        // Root Focused
        (styles.rootFocused as IRawStyle).backgroundColor = theme.palette.themeDark;
        (styles.rootFocused as IRawStyle).borderColor = theme.palette.themeDark;
    }
    return styles;
}
