import React from 'react';
import { Descriptor, FieldProps } from '../../../abstractions';
import { FieldComponentType, RenderingField } from '../../components';
import { FormContext, FormEntry, FormFeature } from '../../types';

/**
 * The base descriptor that provides the common logic for all descriptors, such as validation.
 * Any inherited descriptor must implement the `test` method and `FieldComponent` property.
 */
export abstract class BaseDescriptor<CoercedEntry extends FormEntry>
    implements Descriptor<FormEntry, FormContext, FormFeature, CoercedEntry>
{
    /**
     * The component that renders the field, which can be either a class component or a function component.
     * No entry validation is needed in this component, as it is handled by the base descriptor.
     */
    protected abstract readonly FieldComponent: FieldComponentType<CoercedEntry>;

    /**
     * Tests whether the entry is the type that the descriptor can handle.
     *
     * @param entry The entry to test.
     * @param context The form context.
     * @returns Passes the test or not.
     */
    abstract test(entry: FormEntry, context: FormContext): entry is CoercedEntry;

    /**
     * Renders the field with some common logic, such as validation and designing tools.
     *
     * @param props The props to render the field.
     * @returns The rendered field.
     */
    render(props: FieldProps<FormEntry, FormContext, FormFeature, CoercedEntry>): JSX.Element {
        return <RenderingField {...props} Component={this.FieldComponent} />;
    }
}

export type FieldComponentTypeFactory<Options, CoercedEntry extends FormEntry> = (
    options: Options
) => FieldComponentType<CoercedEntry>;
