/**
 * The default file storage for rich text fields, which uploads files as data URLs.
 */
export const defaultRichTextFileStorageService: RichTextFileStorageService = {
    upload: encodeDataUrl,
    download: downloadDataUrl,
    thumbnail: thumbnailDataUrl,
};

async function downloadDataUrl(url: string): Promise<Blob> {
    const blob = decodeDataUrl(url);
    if (!blob) throw new Error('Invalid data URL');
    return blob;
}

async function thumbnailDataUrl(url: string): Promise<Blob> {
    const mime = getMimeByDataUrl(url);
    if (!(mime && isImageMime(mime))) throw new Error('Unsupported MIME type');
    return downloadDataUrl(url);
}
