import { Label, mergeStyles } from '@fluentui/react';
import { useId } from '@fluentui/react-hooks';
import { EditorOptions, Extensions } from '@tiptap/core';
import { Placeholder } from '@tiptap/extension-placeholder';
import { EditorContent, useEditor } from '@tiptap/react';
import React, { forwardRef } from 'react';
import { Defaults, File } from '../../extensions';
import { defaultRichTextFieldStrings } from '../../utils';
import { RichTextRibbon } from '../RichTextRibbon';
import { useRichTextFieldStyles } from './RichTextField.styles';
import { RibbonPosition, RichTextFieldProps } from './RichTextField.types';

export const RichTextField = forwardRef<HTMLDivElement, RichTextFieldProps>(
    (
        {
            value,
            onChange,
            errorMessage,
            label,
            description,
            required,
            placeholder,
            prefix,
            readOnly,
            invalid,
            compact,
            ribbonPosition = RibbonPosition.Top,
            focusable = true,
            callbacks,
            fileStorageService,
            additionalExtensions,
            strings,
            className,
            rootClassName,
            containerClassName,
            children,
            'aria-required': ariaRequired,
            'aria-placeholder': ariaPlaceholder,
            'aria-readonly': ariaReadonly,
            'aria-labelledby': externalLabelId,
            'aria-describedby': externalDescriptionId,
            ...props
        },
        ref
    ) => {
        const stringsInternal = React.useMemo(() => ({ ...defaultRichTextFieldStrings, ...strings }), [strings]);

        const valueRef = React.useRef(value);
        valueRef.current = value;

        const onChangeRef = React.useRef(onChange);
        onChangeRef.current = onChange;

        const uncontrolledValueRef = React.useRef(value);

        // Prevent calling onChange when the html is not changed, since we catch any possible content updates from
        // `UpdateContentPlugin`, which could be noisy.
        const onUpdate = React.useCallback<EditorOptions['onUpdate']>(event => {
            const html = event.editor.getHTML();
            if (valueRef.current === html) return;
            onChangeRef.current?.(html);
            uncontrolledValueRef.current = html;
        }, []);

        const internalLabelId = useId('label');
        const internalDescriptionId = useId('description');
        const internalErrorMessageId = useId('errormessage');

        // Prefix is used to add a data attribute to the editor container, which can be used for styling, to dynamically
        // add a prefix to the content. This is useful for example when the editor is used to render replies in comments.
        // Tiptap does not provide a way to directly access the contenteditable element, so we have to pass these attributes
        // to support accessibility. The contenteditable element is not a native input, so it must have a role of textbox.
        // Error message is handled separately, because it can change without the editor content changing, and we want to
        // avoid unnecessary re-renders.
        const composedArialRequired = required || ariaRequired;
        const composedAriaPlaceholder = placeholder || ariaPlaceholder;
        const composedAriaReadonly = readOnly || ariaReadonly;
        const composedLabelId = externalLabelId || (label && internalLabelId);
        const composedDescriptionId = externalDescriptionId || (description && internalDescriptionId);
        const editorProps = React.useMemo<EditorOptions['editorProps']>(
            () => ({
                attributes: {
                    ...(prefix && { 'data-prefix': prefix }),
                    role: 'textbox',
                    'aria-multiline': 'true',
                    ...(composedArialRequired && { 'aria-required': 'true' }),
                    ...(composedAriaPlaceholder && { 'aria-placeholder': composedAriaPlaceholder }),
                    ...(composedAriaReadonly && { 'aria-readonly': 'true' }),
                    ...(composedLabelId && { 'aria-labelledby': composedLabelId }),
                    ...(composedDescriptionId && { 'aria-describedby': composedDescriptionId }),
                },
            }),
            [
                prefix,
                composedArialRequired,
                composedAriaPlaceholder,
                composedAriaReadonly,
                composedLabelId,
                composedDescriptionId,
            ]
        );

        const extensions = React.useMemo<Extensions>(
            () => [
                Defaults,
                Placeholder.configure({ placeholder }),
                ...(fileStorageService
                    ? [File.configure({ fileStorageService, compact, strings: stringsInternal })]
                    : []),
                ...(additionalExtensions ?? []),
            ],
            [additionalExtensions, compact, fileStorageService, placeholder, stringsInternal]
        );

        const editor = useEditor(
            {
                parseOptions: { preserveWhitespace: 'full' },
                editorProps,
                extensions,
                editable: !readOnly,
                content: value,
                onUpdate,
            },
            [editorProps, extensions, readOnly]
        );

        // Because Tiptap handles its own changes, and does not allow preventing edits,
        // we have to settle for a hybrid between controlled and uncontrolled mode.
        // It can't prevent the change, but will still override the content whenever value differs from current state.
        // Like https://www.npmjs.com/package/react-quill#user-content-controlled-mode-caveats
        React.useEffect(() => {
            if (!editor || uncontrolledValueRef.current === value) return;

            // Workaround for "flushSync was called from inside a lifecycle method. React cannot flush when React is
            // already rendering. Consider moving this call to a scheduler task or micro task."
            // See https://github.com/ueberdosis/tiptap/issues/3764
            queueMicrotask(() => {
                if (uncontrolledValueRef.current === value) return;
                editor.commands.setContent(value);
            });
        }, [editor, value]);

        const styles = useRichTextFieldStyles();
        return (
            <div ref={ref} className={rootClassName}>
                {label && (
                    <Label id={internalLabelId} required={required}>
                        {label}
                    </Label>
                )}
                {description && (
                    <div id={internalDescriptionId} className={styles.description}>
                        {description}
                    </div>
                )}
                <div
                    className={mergeStyles(
                        'ms-RichTextField',
                        styles.editorContainer,
                        errorMessage || invalid ? styles.error : focusable && styles.focusable,
                        containerClassName
                    )}>
                    {ribbonPosition === RibbonPosition.Top && (
                        <RichTextRibbon
                            editor={editor}
                            compact={compact}
                            callbacks={callbacks}
                            strings={stringsInternal}
                        />
                    )}
                    {children}
                    <EditorContent
                        editor={editor}
                        className={mergeStyles(styles.editor, className)}
                        aria-errormessage={errorMessage ? internalErrorMessageId : undefined}
                        aria-invalid={!!errorMessage || invalid}
                        {...props}
                    />
                    {ribbonPosition === RibbonPosition.Bottom && (
                        <RichTextRibbon
                            editor={editor}
                            compact={compact}
                            callbacks={callbacks}
                            strings={stringsInternal}
                        />
                    )}
                </div>
            </div>
        );
    }
);
