import { IToggleStyleProps } from '@fluentui/react';
import { ContrastColors, CoreColors, DarkColors } from '../teams-colors';
import { isContrastTheme } from '../teams-theme';

export const getToggleStyles = (themeName: string): any => {
    const themeColors = getThemeColors(themeName);

    return (props: IToggleStyleProps) => ({
        root: {
            selectors: {
                '.ms-Label': {
                    fontWeight: 'normal',
                    color: themeColors.textColor,
                },
            },
        },
        pill: getPillStyles(!props.disabled, !!props.checked, themeColors),
        thumb: getThumbStyles(!props.disabled, !!props.checked, themeColors),
    });
};

interface ToggleThemeColors {
    textColor: string;
    backgroundColor: string;
    checkedBackgroundColor: string;
    disabledBackgroundColor: string;
    disabledAccentColor: string;
    themeName: string;
}

const getThemeColors = (themeName: string): ToggleThemeColors => {
    let colors: Partial<ToggleThemeColors> = {
        themeName,
    };

    switch (themeName) {
        case 'dark':
            colors = {
                ...colors,
                textColor: DarkColors.appBlack,
                backgroundColor: DarkColors.appWhite,
                checkedBackgroundColor: DarkColors.appBrand06,
                disabledBackgroundColor: DarkColors.appGray09,
                disabledAccentColor: DarkColors.appGray06,
            };
            break;

        case 'contrast':
            colors = {
                ...colors,
                textColor: ContrastColors.appBlack,
                backgroundColor: ContrastColors.appWhite,
                checkedBackgroundColor: ContrastColors.appBrand06,
                disabledBackgroundColor: ContrastColors.appGreenContrast,
                disabledAccentColor: ContrastColors.appGreenContrast,
            };
            break;

        default:
        case 'default':
            colors = {
                ...colors,
                textColor: CoreColors.appBlack,
                backgroundColor: CoreColors.appWhite,
                checkedBackgroundColor: CoreColors.appBrand06,
                disabledBackgroundColor: CoreColors.appGray09,
                disabledAccentColor: CoreColors.appGray06,
            };
            break;
    }

    return colors as ToggleThemeColors;
};

interface ThumbStyles {
    backgroundColor: string;
    transition?: string;
    border?: string;
}

function getThumbStyles(isEnabled: boolean, isChecked: boolean, themeColors: ToggleThemeColors): any {
    const { backgroundColor, borderColor } = getThumbColors(isEnabled, isChecked, themeColors);
    const baseStyles: ThumbStyles = {
        backgroundColor,
        transition: 'background-color 0.1s, border-color 0.1s',
    };

    if (!isChecked) {
        baseStyles.border = `thin solid ${borderColor}`;
    }

    return baseStyles;
}

interface ThumbColors {
    backgroundColor: string;
    borderColor?: string;
}

function getThumbColors(isEnabled: boolean, isChecked: boolean, themeColors: ToggleThemeColors): ThumbColors {
    const thumbColors: ThumbColors = {
        backgroundColor: themeColors.backgroundColor,
    };

    if (!isEnabled && isChecked) {
        thumbColors.backgroundColor = isContrastTheme(themeColors.themeName)
            ? themeColors.backgroundColor
            : themeColors.disabledAccentColor;
    }

    if (!isChecked) {
        thumbColors.borderColor = isEnabled ? themeColors.textColor : themeColors.disabledAccentColor;
    }

    return thumbColors;
}

interface PillStyles {
    backgroundColor: string;
    transition?: string;
    border?: string;
    selectors?: any;
}

function getPillStyles(isEnabled: boolean, isChecked: boolean, themeColors: ToggleThemeColors): PillStyles {
    const { backgroundColor: thumbBackgroundColor } = getThumbColors(isEnabled, isChecked, themeColors);
    const {
        backgroundColor: pillBackgroundColor,
        borderColor,
        contrastHoverColor,
    } = getPillColors(isEnabled, isChecked, themeColors);
    const baseStyles: PillStyles = {
        backgroundColor: pillBackgroundColor,
        transition: 'background-color 0.1s, border-color 0.1s',
    };

    if (!isChecked) {
        baseStyles.border = `thin solid ${borderColor} !important`;
    }

    if (isEnabled) {
        baseStyles.selectors = {
            ':hover': {
                ...(contrastHoverColor && {
                    backgroundColor: pillBackgroundColor,
                    ...(!isChecked && { borderColor: `${contrastHoverColor} !important` }),
                }),
                selectors: {
                    '.ms-Toggle-thumb': {
                        backgroundColor: thumbBackgroundColor,
                        ...(contrastHoverColor && !isChecked && { borderColor: contrastHoverColor }),
                    },
                },
            },
        };
    }

    return baseStyles;
}

interface PillColors {
    backgroundColor: string;
    borderColor?: string;
    contrastHoverColor?: string;
}

function getPillColors(isEnabled: boolean, isChecked: boolean, themeColors: ToggleThemeColors): PillColors {
    const pillColors: PillColors = {
        backgroundColor: themeColors.backgroundColor,
    };

    if (isChecked) {
        pillColors.backgroundColor = isEnabled
            ? themeColors.checkedBackgroundColor
            : themeColors.disabledBackgroundColor;
    }

    if (!isChecked) {
        pillColors.borderColor = isEnabled ? themeColors.textColor : themeColors.disabledAccentColor;
    }

    if (isContrastTheme(themeColors.themeName)) {
        pillColors.contrastHoverColor = themeColors.checkedBackgroundColor;
    }

    return pillColors;
}
