import { AnyExtension } from '@tiptap/core';
import { RichTextFieldStrings } from '../types';
import { defaultRichTextFieldStrings } from '../utils';

/**
 * Get the compact by an extension.
 *
 * @param extension The extension.
 * @returns The compact.
 */
export function getCompactByExtension(extension: AnyExtension): boolean | undefined {
    const compact = extension.options.compact;
    return typeof compact === 'boolean' ? compact : undefined;
}

/**
 * Get the strings by an extension.
 *
 * @param extension The extension.
 * @returns The strings.
 */
export function getStringsByExtension(extension: AnyExtension): RichTextFieldStrings {
    const strings = extension.options.strings;
    return typeof strings === 'object' ? (strings as RichTextFieldStrings) : defaultRichTextFieldStrings;
}
