/* istanbul ignore file */

import { IModalStyles, ITheme, IRawStyle } from '@fluentui/react';
import { ShadowLevels } from '../teams-shadow-levels';
import { CoreColors, ContrastColors, DarkColors } from '../teams-colors';
import { isContrastTheme, isDarkTheme } from '../teams-theme';
import { teamsBorderRadius } from './constants';

export function getModalStyles(themeName: string, theme: ITheme, isMobile?: boolean): Partial<IModalStyles> {
    const MODAL_MIN_WIDTH = isMobile ? undefined : '400px';
    const MODAL_MIN_HEIGHT = '116px';
    const BORDER_WIDTH = '1px';

    const styles = {
        root: {
            backgroundColor: 'none',
            selectors: {
                '.ms-Overlay--dark': {
                    backgroundColor: CoreColors.overlayBackground,
                },
            },
        },

        main: {
            backgroundColor: theme.palette.white,
            borderRadius: teamsBorderRadius,
            color: theme.palette.black,
            borderWidth: BORDER_WIDTH,
            borderStyle: 'solid',
            borderColor: CoreColors.appBlackAlpha10,
            boxShadow: ShadowLevels.shadowLevel4,
            minWidth: `${MODAL_MIN_WIDTH} !important`,
            minHeight: `${MODAL_MIN_HEIGHT} !important`,
        },
    } as Partial<IModalStyles>;

    const iRawStyle = (styles.root as Required<IRawStyle>).selectors['.ms-Overlay--dark'] as IRawStyle;

    // Dark theme overrides
    if (isDarkTheme(themeName)) {
        if (iRawStyle) {
            iRawStyle.backgroundColor = DarkColors.overlayBackground;
        }

        (styles.main as IRawStyle).backgroundColor = theme.palette.neutralLight;
        (styles.main as IRawStyle).borderColor = theme.palette.white;
    }

    // Contrast theme overrides
    if (isContrastTheme(themeName)) {
        if (iRawStyle) {
            iRawStyle.backgroundColor = ContrastColors.overlayBackground;
        }

        (styles.main as IRawStyle).borderColor = theme.palette.black;
    }
    return styles;
}
