import { Toggle } from '@fluentui/react';
import React from 'react';
import { FormQuestionType, TextNumberRestrictionType } from '../../../types';
import { deriveTextNumberRestriction, useToggleStyles } from '../../../utilities';
import { QuestionSettingsProps } from '../types';
import { useSecondarySettingsStyles } from './SecondarySettings.styles';

export function SecondarySettings({ question, onQuestionChange, strings }: QuestionSettingsProps): JSX.Element {
    const styles = useSecondarySettingsStyles();
    const toggleStyles = useToggleStyles();

    return (
        <div className={styles.row}>
            <Toggle
                checked={question.required}
                onChange={(_, checked) => {
                    const newQuestion = { ...question };
                    if (checked) {
                        newQuestion.required = true;
                    } else {
                        delete newQuestion.required;
                    }
                    onQuestionChange(newQuestion);
                }}
                label={strings.questionRequired}
                inlineLabel
                styles={toggleStyles}
            />
            {question.type === FormQuestionType.Choice && (
                <Toggle
                    checked={question.shuffle}
                    onChange={(_, checked) => {
                        const newQuestion = { ...question };
                        if (checked) {
                            newQuestion.shuffle = true;
                        } else {
                            delete newQuestion.shuffle;
                        }
                        onQuestionChange(newQuestion);
                    }}
                    label={strings.questionShuffle}
                    inlineLabel
                    styles={toggleStyles}
                />
            )}
            {question.type === FormQuestionType.Text && question.number && (
                <Toggle
                    checked={Boolean(question.restriction)}
                    onChange={(_, checked) => {
                        const newQuestion = { ...question };
                        if (checked) {
                            newQuestion.restriction = deriveTextNumberRestriction(
                                TextNumberRestrictionType.GreaterThan
                            );
                        } else {
                            delete newQuestion.restriction;
                        }
                        onQuestionChange(newQuestion);
                    }}
                    label={strings.questionRestriction}
                    inlineLabel
                    styles={toggleStyles}
                />
            )}
        </div>
    );
}
