/* istanbul ignore file */

import {
    IContextualMenuItemStyleProps,
    IContextualMenuItemStyles,
    IContextualMenuStyleProps,
    IContextualMenuStyles,
    IStyleFunctionOrObject,
    ITheme,
} from '@fluentui/react';
import { ContrastColors } from '../teams-colors';
import { ShadowLevels } from '../teams-shadow-levels';
import { FontSizes } from '../teams-sizes';
import { isContrastTheme, isDarkTheme } from '../teams-theme';

export function getContextualMenuStyles(
    themeName: string,
    _theme: ITheme
): IStyleFunctionOrObject<IContextualMenuStyleProps, IContextualMenuStyles> {
    return (props: IContextualMenuStyleProps): Partial<IContextualMenuStyles> => {
        const { theme } = props;
        const { palette } = theme;
        const CONTEXTUAL_MENU_ITEM_HEIGHT = 32;

        const iconStyles = {
            maxHeight: CONTEXTUAL_MENU_ITEM_HEIGHT,
            fontSize: FontSizes.size20,
            width: FontSizes.size20,
            margin: '0 8px 0 0',
            color: palette.neutralDark,
        };

        const TEXT_COLOR = palette.neutralDark;
        let TEXT_COLOR_HOVER_ACTIVE = palette.black;
        let BACKGROUND_COLOR_HOVER = palette.neutralLight;
        let BACKGROUND_COLOR_ACTIVE = palette.neutralTertiaryAlt;
        let BACKGROUND_FOCUS_COLOR = 'transparent';
        let FOCUS_AFTER = {};
        let CONTRAST_FOCUS_HOVER = {};

        const hoverActiveStyles = (textColor: string, background_color: string) =>
            ({
                color: textColor,
                backgroundColor: background_color,
            } as const);

        if (isDarkTheme(themeName)) {
            BACKGROUND_COLOR_HOVER = palette.neutralQuaternary;
            BACKGROUND_COLOR_ACTIVE = palette.neutralTertiary;
        }
        if (isContrastTheme(themeName)) {
            TEXT_COLOR_HOVER_ACTIVE = palette.themeDarker;
            BACKGROUND_COLOR_HOVER = palette.themeDark;
            BACKGROUND_COLOR_ACTIVE = palette.themeDark;
            BACKGROUND_FOCUS_COLOR = palette.themeDark;
            FOCUS_AFTER = {
                ':after': {
                    display: 'none',
                },
            };
            CONTRAST_FOCUS_HOVER = {
                '&:focus': {
                    ...hoverActiveStyles(TEXT_COLOR_HOVER_ACTIVE, BACKGROUND_COLOR_HOVER),
                },
                '&:focus:hover': {
                    ...hoverActiveStyles(TEXT_COLOR_HOVER_ACTIVE, BACKGROUND_COLOR_HOVER),
                },
            };
        }

        const isFocusVisible = {
            '.ms-Fabric--isFocusVisible &': {
                selectors: {
                    ':hover': {
                        backgroundColor: BACKGROUND_FOCUS_COLOR,
                    },
                    ':focus': {
                        backgroundColor: BACKGROUND_FOCUS_COLOR,
                        selectors: {
                            ':after': {
                                borderColor: palette.white,
                                outline: `1px solid ${palette.black}`,
                            },
                        },
                    },
                    ':focus:hover': {
                        backgroundColor: BACKGROUND_FOCUS_COLOR,
                    },
                    ...FOCUS_AFTER,
                },
            },
        };

        const commonStyles = {
            color: TEXT_COLOR,
            backgroundColor: 'transparent',
            selectors: {
                '.ms-ContextualMenu-icon, .ms-ContextualMenu-submenuIcon, .ms-ContextualMenu-secondaryText, .ms-ContextualMenu-checkmarkIcon, .ms-ContextualMenu-checkmarkIcon':
                    {
                        color: 'inherit',
                        backgroundColor: 'transparent',
                    },
                svg: {
                    fill: 'currentColor',
                },
                '.icons-default-fill': {
                    fill: 'currentColor',
                },
                '.ms-ContextualMenu-link': {
                    selectors: {
                        '&:hover': {
                            ...hoverActiveStyles(TEXT_COLOR_HOVER_ACTIVE, BACKGROUND_COLOR_HOVER),
                        },
                        '&:active': {
                            ...hoverActiveStyles(TEXT_COLOR_HOVER_ACTIVE, BACKGROUND_COLOR_ACTIVE),
                        },
                        ...isFocusVisible,
                        ...CONTRAST_FOCUS_HOVER,
                    },
                },
            },
            ':hover .ms-ContextualMenu-icon': {
                color: 'inherit',
            },
        };

        const styles = {
            subComponentStyles: {
                callout: {
                    root: {
                        boxShadow: ShadowLevels.shadowLevel3,
                        zIndex: 999,
                        selectors: {
                            '.ms-ContextualMenu': {
                                backgroundColor: 'transparent',
                            },
                            '.ms-ContextualMenu-link, .ms-ContextualMenu-header': {
                                padding: '0 16px',
                            },
                            '.ms-ContextualMenu-itemText': {
                                margin: '0 16px 0 0',
                            },
                            '.ms-ContextualMenu-checkmarkIcon': {
                                width: '14px',
                            },
                            '.ms-ContextualMenu-submenuIcon': {
                                width: '14px',
                            },
                            '.ms-ContextualMenu-header': {
                                fontSize: FontSizes.size14,
                            },
                        },
                    },
                    calloutMain: {
                        paddingTop: '8px',
                        paddingBottom: '8px',
                    },
                },

                menuItem: (itemStyleProps: IContextualMenuItemStyleProps): Partial<IContextualMenuItemStyles> => {
                    const { disabled, expanded, primaryDisabled, checked } = itemStyleProps;
                    let THEME_LIGHT = palette.neutralQuaternary;
                    let NEUTRAL_TERTIARY = palette.neutralTertiary;
                    let NEUTRAL_PRIMARY = palette.neutralPrimary;
                    let NEUTRAL_DARK = palette.neutralDark;
                    let NEUTRAL_DARK_WHITE = palette.neutralDark;
                    let NEUTRAL_QUATERNARY_ALT = palette.neutralTertiaryAlt;

                    // Dark theme overrides
                    if (isDarkTheme(themeName)) {
                        NEUTRAL_QUATERNARY_ALT = palette.white;
                        THEME_LIGHT = palette.neutralTertiaryAlt;
                    }

                    // Contrast theme overrides
                    if (isContrastTheme(themeName)) {
                        THEME_LIGHT = palette.themePrimary;
                        NEUTRAL_TERTIARY = ContrastColors.appGreenContrast;
                        NEUTRAL_PRIMARY = palette.black;
                        NEUTRAL_DARK = palette.neutralDark;
                        NEUTRAL_DARK_WHITE = palette.white;
                        NEUTRAL_QUATERNARY_ALT = palette.black;
                    }

                    const menuStyles = {
                        divider: {
                            margin: '8px 0',
                            height: '1px',
                            backgroundColor: NEUTRAL_QUATERNARY_ALT,
                        },
                        root: [
                            {
                                height: CONTEXTUAL_MENU_ITEM_HEIGHT,
                                lineHeight: CONTEXTUAL_MENU_ITEM_HEIGHT,
                                color: palette.neutralDark,
                                // High contrast styles https://github.com/microsoft/fluentui/issues/30069
                                '@media (forced-colors: active)': {
                                    ':hover, :focus, :focus:hover': {
                                        forcedColorAdjust: 'none',
                                    },
                                },
                            },
                            !disabled && {
                                ...commonStyles,
                            },
                            expanded && {
                                ...hoverActiveStyles(TEXT_COLOR_HOVER_ACTIVE, THEME_LIGHT),
                                selectors: {
                                    ':hover': {
                                        backgroundColor: BACKGROUND_COLOR_ACTIVE,
                                    },
                                },
                            },
                            disabled && {
                                color: NEUTRAL_TERTIARY,
                            },
                        ],
                        splitPrimary: [
                            {
                                height: CONTEXTUAL_MENU_ITEM_HEIGHT,
                                lineHeight: CONTEXTUAL_MENU_ITEM_HEIGHT,
                                padding: '0 16px',
                                color: NEUTRAL_DARK,
                                selectors: {
                                    '&:hover ~ button': {
                                        backgroundColor: `${THEME_LIGHT} !important`,
                                        color: NEUTRAL_DARK_WHITE,
                                        selectors: {
                                            '.ms-ContextualMenu-submenuIcon': {
                                                color: NEUTRAL_DARK_WHITE,
                                            },
                                        },
                                    },
                                },
                            },
                            !(disabled || primaryDisabled) &&
                                !checked && {
                                    ...commonStyles,
                                },
                            (disabled || primaryDisabled) && {
                                color: NEUTRAL_TERTIARY,
                                selectors: {
                                    '.ms-ContextualMenu-icon': {
                                        color: NEUTRAL_TERTIARY,
                                    },
                                },
                            },
                            checked && {
                                selectors: {
                                    '.ms-ContextualMenu-checkmarkIcon': {
                                        color: NEUTRAL_PRIMARY,
                                    },
                                },
                            },
                        ],
                        splitMenu: [
                            {
                                height: CONTEXTUAL_MENU_ITEM_HEIGHT,
                                lineHeight: CONTEXTUAL_MENU_ITEM_HEIGHT,
                                width: CONTEXTUAL_MENU_ITEM_HEIGHT, // to match the numbers from the default styles
                                ...commonStyles,
                            },
                            expanded && {
                                ...hoverActiveStyles(TEXT_COLOR_HOVER_ACTIVE, THEME_LIGHT),
                                selectors: {
                                    ':hover': {
                                        backgroundColor: BACKGROUND_COLOR_ACTIVE,
                                    },
                                },
                            },
                        ],
                        icon: [{ ...iconStyles }, disabled && { color: NEUTRAL_TERTIARY }],
                        checkmarkIcon: [{ ...iconStyles }],

                        splitContainer: [
                            {
                                height: CONTEXTUAL_MENU_ITEM_HEIGHT,
                                backgroundColor: 'transparent !important',
                                selectors: {
                                    button: {
                                        selectors: {
                                            '.ms-ContextualMenu-submenuIcon': {
                                                margin: 0,
                                            },
                                        },
                                    },
                                },
                            },
                            !(disabled || primaryDisabled) &&
                                !checked && {
                                    selectors: { ...isFocusVisible },
                                },
                            disabled && { color: NEUTRAL_TERTIARY },
                        ],
                        subMenuIcon: [
                            {
                                height: CONTEXTUAL_MENU_ITEM_HEIGHT,
                                lineHeight: CONTEXTUAL_MENU_ITEM_HEIGHT,
                                fontSize: FontSizes.size12,
                                color: NEUTRAL_DARK,
                            },
                            expanded && {
                                ...hoverActiveStyles(TEXT_COLOR_HOVER_ACTIVE, THEME_LIGHT),
                                selectors: {
                                    ':hover': {
                                        backgroundColor: BACKGROUND_COLOR_ACTIVE,
                                    },
                                },
                            },
                            disabled && { color: NEUTRAL_TERTIARY },
                        ],
                    } as Partial<IContextualMenuItemStyles>;
                    return menuStyles;
                },
            },
        };

        // Dark theme overrides
        if (isDarkTheme(themeName)) {
            styles.subComponentStyles.callout.root.boxShadow = ShadowLevels.shadowLevel3Dark;
        }

        // Contrast theme overrides
        if (isContrastTheme(themeName)) {
            styles.subComponentStyles.callout.root.boxShadow = 'none';
        }
        return styles;
    };
}
