import { Checkbox, FocusZone, ICheckboxProps, TextField, mergeStyles } from '@fluentui/react';
import React from 'react';
import { ChoiceAppearance, ChoiceEntry, FormContext, FormEntry, FormMode, FormQuestionType } from '../../types';
import {
    useCheckboxStyles,
    useChoiceChange,
    useChosenAndOther,
    useOtherFieldStyles,
    useShuffled,
} from '../../utilities';
import { BaseDescriptor, FieldComponentTypeFactory } from './BaseDescriptor';

const containerClassName = mergeStyles({
    display: 'flex',
    flexDirection: 'column',
    gap: '12px',
    paddingInline: '12px',
});

const defaultOptions = {
    otherPlaceholder: 'Other',
};

export type CheckboxChoiceDescriptorOptions = Partial<typeof defaultOptions>;

const makeFieldComponent: FieldComponentTypeFactory<
    CheckboxChoiceDescriptorOptions & typeof defaultOptions,
    ChoiceEntry
> =
    ({ otherPlaceholder }) =>
    ({ entry, onEntryChange, delegatedProps, showPlaceholder }) => {
        const { question, answer } = entry;
        const { choices, shuffle, allowOther } = question;

        const shuffledChoices = useShuffled({ array: choices, enabled: shuffle && !delegatedProps.readOnly });
        const onChange = useChoiceChange(entry, onEntryChange);
        const { chosen, otherKey, otherValue, otherSelected, otherFieldRef } = useChosenAndOther(
            choices,
            answer.content
        );

        const keys = allowOther ? [...shuffledChoices, otherKey] : shuffledChoices;

        const checkboxStyles = useCheckboxStyles();
        const otherFieldStyles = useOtherFieldStyles();

        const onRenderOtherField: ICheckboxProps['onRenderLabel'] = checkboxProps => (
            <TextField
                componentRef={otherFieldRef}
                value={checkboxProps?.label ?? ''}
                onChange={onChange && ((_, newValue) => onChange(chosen, newValue))}
                placeholder={showPlaceholder ? otherPlaceholder : undefined}
                readOnly={delegatedProps.readOnly}
                invalid={otherSelected && delegatedProps.invalid}
                className='ms-Checkbox-text'
                styles={otherFieldStyles}
            />
        );

        return (
            <FocusZone {...delegatedProps} className={containerClassName} role='listbox'>
                {keys.map(key => (
                    <Checkbox
                        key={key}
                        label={key === otherKey ? otherValue : key}
                        checked={key === otherKey ? otherSelected : chosen.includes(key)}
                        onChange={
                            onChange &&
                            ((_, checked) => {
                                if (key === otherKey) {
                                    if (checked) otherFieldRef.current?.focus();
                                    onChange(chosen, checked ? '' : undefined);
                                } else {
                                    // Add or remove the choice from the answer, depending on the checked state.
                                    const newChosen = checked ? [...chosen, key] : chosen.filter(c => c !== key);
                                    onChange(newChosen, otherValue);
                                }
                            })
                        }
                        onRenderLabel={key === otherKey ? onRenderOtherField : undefined}
                        inputProps={{ role: 'option' }}
                        styles={checkboxStyles}
                    />
                ))}
            </FocusZone>
        );
    };

/**
 * The descriptor that renders a group of checkboxes for choice entries with checkbox appearance.
 */
export class CheckboxChoiceDescriptor extends BaseDescriptor<ChoiceEntry> {
    protected readonly FieldComponent;

    constructor(options?: CheckboxChoiceDescriptorOptions) {
        super();
        this.FieldComponent = makeFieldComponent({ ...defaultOptions, ...options });
    }

    /**
     * Tests whether the entry is a choice entry with checkbox appearance, and the form is not in viewing mode.
     *
     * @param entry The entry to test.
     * @param context The form context.
     * @returns Passes the test or not.
     */
    test(entry: FormEntry, context: FormContext): entry is ChoiceEntry {
        return (
            entry.question.type === FormQuestionType.Choice &&
            entry.question.appearance === ChoiceAppearance.Checkbox &&
            context.mode !== FormMode.Viewing
        );
    }
}
