/* istanbul ignore file */

import { FontWeights, IButtonStyles, IRawStyle, ITheme } from '@fluentui/react';
import { ContrastColors } from '../teams-colors';
import { ShadowLevels } from '../teams-shadow-levels';
import { isContrastTheme, isDarkTheme } from '../teams-theme';
import { teamsBorderRadius } from './constants';

export function getDefaultButtonStyles(themeName: string, theme: ITheme): Partial<IButtonStyles> {
    let primaryTextColor = theme.palette.white;
    let primaryBackgroundColor = theme.palette.themePrimary;
    let primaryBackgroundHoverColor = theme.palette.themeDark;
    let primaryBackgroundActiveColor = theme.palette.themeDarker;
    let primaryBackgroundFocusColor = theme.palette.themeDark;
    let primaryDividerColor = theme.palette.neutralTertiaryAlt;
    let focusBoxShadowColor = theme.palette.white;
    let focusBackgroundColor = theme.palette.neutralTertiaryAlt;
    let focusTextColor = theme.palette.black;
    let focusPrimaryTextColor = theme.palette.white;
    const buttonMinWidth = '96px';
    const buttonBorderRadius = teamsBorderRadius;
    const buttonPadding = '0 16px';
    const buttonBoxShadow = ShadowLevels.shadowLevel1;
    const buttonBoxShadowDark = ShadowLevels.shadowLevel1Dark;
    const rootHoverColor = theme.palette.neutralQuaternary;
    const rootPressedCheckedColor = theme.palette.neutralTertiaryAlt;

    if (isDarkTheme(themeName)) {
        primaryTextColor = theme.palette.black;
        primaryBackgroundColor = theme.palette.themeTertiary;
        primaryBackgroundHoverColor = theme.palette.themeSecondary;
        primaryBackgroundActiveColor = theme.palette.themeLight;
        primaryBackgroundFocusColor = theme.palette.themeLight;
        primaryDividerColor = theme.palette.white;
        focusTextColor = theme.palette.black;
        focusPrimaryTextColor = theme.palette.black;
    }

    if (isContrastTheme(themeName)) {
        primaryTextColor = theme.palette.white;
        primaryBackgroundColor = theme.palette.black;
        primaryBackgroundHoverColor = theme.palette.themeDark;
        primaryBackgroundActiveColor = theme.palette.themeDark;
        primaryDividerColor = theme.palette.white;
        focusBoxShadowColor = 'transparent';
        focusBackgroundColor = theme.palette.themeDark;
        focusTextColor = theme.palette.white;
    }

    const commonStyles = {
        borderColor: 'transparent',
        background: primaryBackgroundColor,
        color: primaryTextColor,
        boxShadow: buttonBoxShadowDark,
        selectors: {
            ':hover': {
                borderColor: 'transparent',
                background: primaryBackgroundHoverColor,
                color: primaryTextColor,
            },
            ':active': {
                borderColor: 'transparent',
                background: primaryBackgroundActiveColor,
                color: primaryTextColor,
            },
        },
    };

    const focusStyles = {
        // Work around for fluentui button focus outline not showing in iOS Safari.
        ':focus-visible, .ms-Fabric--isFocusVisible &:focus': {
            borderColor: theme.palette.black,
            '::after': {
                outline: 'none',
                borderRadius: buttonBorderRadius,
                borderColor: theme.palette.white,
                borderWidth: '1px',
                borderStyle: 'solid',
                inset: 0,
            },
        },
    };

    const styles = {
        root: {
            borderRadius: buttonBorderRadius,
            backgroundColor: theme.palette.white,
            borderColor: theme.palette.neutralTertiaryAlt,
            color: theme.palette.black,
            boxShadow: buttonBoxShadow,
            cursor: 'pointer',
            minWidth: buttonMinWidth,
            padding: buttonPadding,
            selectors: {
                '.icons-default-fill': {
                    fill: theme.palette.black,
                },
                '.icons-filled': {
                    display: 'none',
                },
            },
        },
        rootHovered: {
            backgroundColor: rootHoverColor,
            borderColor: theme.palette.neutralTertiary,
            color: theme.palette.black,
            selectors: {
                ...focusStyles,
            },
        },
        rootPressed: {
            backgroundColor: rootPressedCheckedColor,
            borderColor: theme.palette.neutralTertiary,
            boxShadow: 'none',
            color: theme.palette.black,
        },
        rootChecked: {
            backgroundColor: rootPressedCheckedColor,
            borderColor: theme.palette.neutralTertiary,
            color: theme.palette.black,
            selectors: {
                ...focusStyles,
            },
        },

        rootCheckedHovered: {
            selectors: {
                ...focusStyles,
            },
        },
        rootDisabled: {
            backgroundColor: rootHoverColor,
            color: theme.palette.neutralTertiary,
            borderColor: 'transparent',
            boxShadow: 'none',
            selectors: {
                ...focusStyles,
            },
        },
        rootFocused: {
            boxShadow: buttonBoxShadow,
            selectors: {
                ...focusStyles,
            },
        },
        icon: {
            fontSize: '1.5em',
            height: 'unset',
        },
        menuIcon: {
            fontSize: '1.5em',
            height: 'unset',
        },
        splitButtonMenuButton: {
            borderTopRightRadius: buttonBorderRadius,
            borderBottomRightRadius: buttonBorderRadius,
            borderWidth: '1px',
            borderStyle: 'solid',
            background: theme.palette.white,
            borderLeft: 0,
            color: theme.palette.black,
            marginLeft: 0,
            selectors: {
                '.icons-filled': {
                    display: 'none',
                },
                ':hover': {
                    backgroundColor: theme.palette.neutralLighter,
                    color: theme.palette.black,
                },
                ':active': {
                    backgroundColor: theme.palette.neutralTertiaryAlt,
                    color: theme.palette.black,
                },
                '.ms-Button-icon': {
                    color: 'inherit',
                    fontWeight: FontWeights.bold,
                },
            },
        },
        splitButtonContainer: {
            selectors: {
                '.ms-Button': {
                    borderColor: theme.palette.neutralTertiaryAlt,
                    backgroundClip: 'padding-box',
                },
                '.ms-Button--default': {
                    borderRight: 0,
                    borderTopRightRadius: '0px',
                    borderBottomRightRadius: '0px',
                },
                '.ms-Button--default + .ms-Button': {
                    boxShadow: buttonBoxShadow,
                },
                '.ms-Button--primary': {
                    borderRight: 0,
                    borderTopRightRadius: '0px',
                    borderBottomRightRadius: '0px',
                    ...commonStyles,
                },
                '.ms-Button--primary + .ms-Button': { ...commonStyles },
                ".ms-Button--primary ~ span[class*='css-']": {
                    top: 0,
                    bottom: 0,
                    backgroundColor: primaryDividerColor,
                },
                ".ms-Button--primary + .ms-Button[aria-expanded='true']": {
                    background: primaryBackgroundActiveColor,
                    color: primaryTextColor,
                },
                ':hover': {
                    selectors: {
                        '.ms-Button': {
                            borderColor: theme.palette.neutralTertiary,
                        },
                        '.ms-Button--primary': {
                            borderColor: 'transparent',
                        },
                        '.ms-Button--primary + .ms-Button': {
                            borderColor: 'transparent',
                        },
                    },
                },
                ':active': {
                    selectors: {
                        '.ms-Button': {
                            borderColor: theme.palette.neutralTertiary,
                            boxShadow: 'none',
                        },
                        '.ms-Button--primary': {
                            borderColor: 'transparent',
                        },
                        '.ms-Button--primary + .ms-Button': {
                            borderColor: 'transparent',
                        },
                    },
                },
            },
        },
        splitButtonContainerFocused: {
            selectors: {
                '.ms-Fabric--isFocusVisible &': {
                    selectors: {
                        ':focus::after': {
                            outline: 'none',
                            borderRadius: teamsBorderRadius,
                            borderColor: theme.palette.black,
                            top: 0,
                            bottom: 0,
                            left: 0,
                            right: 0,
                            boxShadow: `${focusBoxShadowColor} 0 0 0 1px inset`,
                        },
                        ':focus': {
                            selectors: {
                                '.ms-Button': {
                                    backgroundColor: focusBackgroundColor,
                                    color: focusTextColor,
                                },
                                ".ms-Button ~ span[class*='css-']": {
                                    backgroundColor: theme.palette.white,
                                },
                                '.ms-Button--primary': {
                                    backgroundColor: primaryBackgroundFocusColor,
                                    color: focusPrimaryTextColor,
                                },
                                '.ms-Button--primary + .ms-Button': {
                                    backgroundColor: primaryBackgroundFocusColor,
                                    color: focusPrimaryTextColor,
                                },
                            },
                        },
                    },
                },
            },
        },
        splitButtonMenuButtonExpanded: {
            background: theme.palette.neutralTertiaryAlt,
            color: theme.palette.black,
            borderColor: theme.palette.neutralTertiary,
        },
        splitButtonDivider: {
            top: 1,
            bottom: 1,
            width: 1,
            backgroundColor: theme.palette.neutralTertiaryAlt,
        },
        description: {
            color: 'inherit',
        },
    } as Partial<IButtonStyles>;

    // Dark theme overrides
    if (isDarkTheme(themeName)) {
        // Root
        (styles.root as IRawStyle).boxShadow = buttonBoxShadowDark;
        (styles.root as IRawStyle).borderColor = theme.palette.neutralTertiary;
        (styles.root as IRawStyle).backgroundColor = theme.palette.neutralLight;
    }

    // Contrast theme overrides
    if (isContrastTheme(themeName)) {
        // Root
        (styles.root as IRawStyle).borderWidth = '2px';
        (styles.root as IRawStyle).borderColor = theme.palette.black;
        // Root Hovered
        (styles.rootHovered as IRawStyle).borderColor = theme.palette.themeDark;
        (styles.rootHovered as IRawStyle).color = theme.palette.white;
        (styles.rootHovered as IRawStyle).backgroundColor = theme.palette.themeDark;
        // Root Pressed
        (styles.rootPressed as IRawStyle).borderColor = theme.palette.themeDark;
        (styles.rootPressed as IRawStyle).color = theme.palette.white;
        (styles.rootPressed as IRawStyle).backgroundColor = theme.palette.themeDark;
        // Root Checked
        (styles.rootChecked as IRawStyle).borderColor = theme.palette.black;
        (styles.rootChecked as IRawStyle).color = theme.palette.white;

        // Root Disabled
        (styles.rootDisabled as IRawStyle).color = ContrastColors.appGreenContrast;
        (styles.rootDisabled as IRawStyle).borderWidth = '2px';
        (styles.rootDisabled as IRawStyle).borderColor = ContrastColors.appGreenContrast;
        (styles.rootDisabled as IRawStyle).backgroundColor = theme.palette.white;
        // Root Focused
        (styles.rootFocused as IRawStyle).color = theme.palette.black;
        (styles.rootFocused as IRawStyle).borderWidth = '2px';
        (styles.rootFocused as IRawStyle).backgroundColor = theme.palette.white;
        (styles.rootFocused as IRawStyle).borderColor = theme.palette.themePrimary;

        // Splitmenu Button Overrides
        (styles.splitButtonMenuButton as IRawStyle).color = theme.palette.black;
        (styles.splitButtonMenuButton as IRawStyle).borderWidth = '2px';
        ((styles.splitButtonMenuButton as Required<IRawStyle>).selectors[':hover'] as IRawStyle).color =
            theme.palette.white;

        ((styles.splitButtonMenuButton as Required<IRawStyle>).selectors[':active'] as IRawStyle).color =
            theme.palette.white;

        ((styles.splitButtonMenuButton as Required<IRawStyle>).selectors[':hover'] as IRawStyle).backgroundColor =
            theme.palette.themeDark;

        ((styles.splitButtonMenuButton as Required<IRawStyle>).selectors[':active'] as IRawStyle).backgroundColor =
            theme.palette.themeDark;

        (
            ((styles.splitButtonContainer as Required<IRawStyle>).selectors[':hover'] as Required<IRawStyle>).selectors[
                '.ms-Button'
            ] as IRawStyle
        ).borderColor = theme.palette.black;

        (
            ((styles.splitButtonContainer as Required<IRawStyle>).selectors[':active'] as Required<IRawStyle>)
                .selectors['.ms-Button'] as IRawStyle
        ).borderColor = theme.palette.black;

        (styles.splitButtonDivider as IRawStyle).width = '2px';

        (styles.splitButtonMenuButtonExpanded as IRawStyle).background = theme.palette.themeDark;
        (styles.splitButtonMenuButtonExpanded as IRawStyle).color = theme.palette.white;
        (styles.splitButtonMenuButtonExpanded as IRawStyle).borderColor = theme.palette.white;
    }
    return styles;
}
