/* istanbul ignore file */

import { IDatePickerStyleProps, IDatePickerStyles, ITheme } from '@fluentui/react';
import { ContrastColors } from '../teams-colors';
import { isContrastTheme, isDarkTheme } from '../teams-theme';

export function getDatePickerStyles(themeName: string, _theme: ITheme) {
    return (props: IDatePickerStyleProps): Partial<IDatePickerStyles> => {
        const { theme } = props;
        const { palette } = theme;

        let HOVER_TEXT_COLOR = palette.black;
        let HOVER_BACKGROUND_COLOR = palette.neutralLight;
        let DISABLED_TEXT_COLOR = palette.neutralTertiary;
        let DISABLED_BACKGROUND_COLOR = palette.white;

        if (isDarkTheme(themeName)) {
            HOVER_BACKGROUND_COLOR = palette.white;
            DISABLED_TEXT_COLOR = palette.neutralTertiary;
            DISABLED_BACKGROUND_COLOR = palette.neutralLight;
        }

        if (isContrastTheme(themeName)) {
            HOVER_TEXT_COLOR = palette.white;
            HOVER_BACKGROUND_COLOR = palette.themeDark;
            DISABLED_TEXT_COLOR = ContrastColors.appGreenContrast;
            DISABLED_BACKGROUND_COLOR = palette.white;
        }

        const styles: Partial<IDatePickerStyles> = {
            callout: {
                '[class^="monthAndYear-"]': {
                    color: theme.palette.black,
                },
                '[class^="headerIconButton-"], [class^="navigationButton-"]': {
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    color: isContrastTheme(themeName) ? palette.black : undefined,
                    ':active, :hover': {
                        color: HOVER_TEXT_COLOR,
                        backgroundColor: HOVER_BACKGROUND_COLOR,
                    },
                },
                '.ms-DatePicker': {
                    table: {
                        th: {
                            color: palette.black,
                        },
                        td: {
                            cursor: 'pointer',
                            color: palette.black,
                            ':active, :hover': {
                                // Fluent UI already uses important here, so we must have a more specific selector with
                                // important too to override it.
                                // See https://github.com/microsoft/fluentui/blob/master/packages/react/src/components/CalendarDayGrid/CalendarDayGrid.styles.ts
                                color: HOVER_TEXT_COLOR + ' !important',
                                backgroundColor: HOVER_BACKGROUND_COLOR + ' !important',
                            },
                            'button[disabled]': {
                                color: DISABLED_TEXT_COLOR,
                                backgroundColor: DISABLED_BACKGROUND_COLOR,
                            },
                        },
                    },
                    '[role=grid]': {
                        '[role=gridcell]': {
                            cursor: 'pointer',
                            color: palette.black,
                            ':active, :hover': {
                                color: HOVER_TEXT_COLOR,
                                backgroundColor: HOVER_BACKGROUND_COLOR,
                            },
                            'button[disabled]': {
                                color: DISABLED_TEXT_COLOR,
                                backgroundColor: DISABLED_BACKGROUND_COLOR,
                            },
                        },
                    },
                },
            },
        };

        return styles;
    };
}
