import { ITheme } from '@fluentui/theme';
import { getTeamStyles } from './teams/teams-styles';
import { themeNames, getPalette } from './teams-palette';

export const getTeamsTheme = (themeName: themeNames, isMobile?: boolean): ITheme => {
    const palette = getPalette(themeName);
    palette.components = getTeamStyles(themeName, palette, isMobile);
    return palette;
};
