/* istanbul ignore file */

import { IStyleFunctionOrObject, IPivotStyleProps, IPivotStyles, FontWeights } from '@fluentui/react';
import { isContrastTheme } from '../teams-theme';

export const PivotStyles: IStyleFunctionOrObject<IPivotStyleProps, IPivotStyles> = ({ theme, linkFormat }) => {
    const rootIsTabs = linkFormat === 'tabs';

    const LINK_SELECTED_COLOR = theme.palette.black;
    const LINK_COLOR = isContrastTheme(theme.name) ? theme.palette.themeDark : theme.palette.neutralPrimary;

    const commonStyles = {
        height: 60,
        lineHeight: 60,
        paddingTop: 4,
        marginRight: 0,
        // Work around for fluentui button focus outline not showing in iOS Safari.
        ':focus-visible, .ms-Fabric--isFocusVisible &:focus': {
            outline: `1px auto ${theme.palette.black}`,
        },
        '::after': { display: 'none' },
    };
    const styles = {
        link: [
            commonStyles,
            !rootIsTabs && {
                fontWeight: FontWeights.regular,
                color: LINK_COLOR,
                '-webkit-tap-highlight-color': 'transparent',
                selectors: {
                    ':hover': {
                        color: LINK_COLOR,
                        backgroundColor: 'transparent',
                        selectors: {
                            ':before': {
                                backgroundColor: theme.palette.themeTertiary,
                                height: '3px',
                            },
                        },
                    },
                    ':active': {
                        backgroundColor: 'transparent',
                    },
                },
            },
        ],
        linkIsSelected: [
            commonStyles,
            !rootIsTabs && {
                color: LINK_SELECTED_COLOR,
                fontWeight: FontWeights.semibold,
                selectors: {
                    ':hover': {
                        color: LINK_SELECTED_COLOR,
                        backgroundColor: 'transparent',
                        selectors: {
                            ':before': {
                                left: '8px',
                                right: '8px',
                            },
                        },
                    },
                    ':active': {
                        color: LINK_SELECTED_COLOR,
                        backgroundColor: 'transparent',
                    },
                    ':before': {
                        border: 'none',
                        height: '3px',
                        backgroundColor: theme.palette.themePrimary,
                        transition: 'none',
                    },
                },
            },
        ],
    };

    return styles;
};
