import { Icon, mergeStyles } from '@fluentui/react';
import React from 'react';
import { FormQuestion, FormStrings, QuestionDisplayType } from '../../../types';
import { getDisplayType } from '../../../utilities';
import { QuestionSettingsProps } from '../types';
import { useMockedFieldStyles } from './MockedField.styles';

export type MockedFieldProps = Omit<QuestionSettingsProps, 'onQuestionChange'>;

export function MockedField({ question, strings }: MockedFieldProps): JSX.Element | null {
    const styles = useMockedFieldStyles();

    const { hidden, placeholder, trailingIconName, multiline, ribbonIconNames } = React.useMemo(
        () => getMockedFieldAppearance(question, strings),
        [question, strings]
    );

    if (hidden) return null;

    return (
        <div className={mergeStyles(styles.root, multiline && styles.multiline)}>
            {ribbonIconNames && (
                <div className={styles.ribbon}>
                    {ribbonIconNames.map(iconName => (
                        <Icon className={styles.icon} key={iconName} iconName={iconName} />
                    ))}
                </div>
            )}
            <div className={styles.input}>
                <span>{placeholder}</span>
                {trailingIconName && <Icon className={styles.icon} iconName={trailingIconName} />}
            </div>
        </div>
    );
}

interface MockedFieldAppearance {
    hidden?: boolean;
    placeholder?: string;
    trailingIconName?: string;
    multiline?: boolean;
    ribbonIconNames?: string[];
}

const hiddenDisplayTypes = [QuestionDisplayType.MultipleChoice, QuestionDisplayType.SingleChoice];
const multilineDisplayTypes = [QuestionDisplayType.MultilineText, QuestionDisplayType.RichText];
const displayTypeTrailingIconNames: Partial<Record<QuestionDisplayType, string>> = {
    [QuestionDisplayType.Date]: 'Calendar',
    [QuestionDisplayType.Dropdown]: 'ChevronDown',
};
const richTextFieldRibbonIconNames = ['Bold', 'Italic', 'Underline', 'Strikethrough', 'More'];

function getMockedFieldAppearance(question: FormQuestion, strings: FormStrings): MockedFieldAppearance {
    const displayType = getDisplayType(question);
    if (hiddenDisplayTypes.includes(displayType)) return { hidden: true };
    return {
        placeholder: strings.questionMockedFieldDefaultPlaceholder(displayType),
        trailingIconName: displayTypeTrailingIconNames[displayType],
        multiline: multilineDisplayTypes.includes(displayType),
        ...(displayType === QuestionDisplayType.RichText && { ribbonIconNames: richTextFieldRibbonIconNames }),
    };
}
