import { ImageSize, isDefined, isImageMime, measureImage } from '@addressbook/utilities-core';
import { BehaviorSubject } from 'rxjs';
import { distinctUntilChanged, filter, map } from 'rxjs/operators';
import { RichTextFileStorageService } from '../../types';

/**
 * A key to identify a file session.
 */
export type FileSessionKey = string;

/**
 * A file session that contains the file URL, blob, thumbnail, image size, loading status, error, and retry function.
 */
export interface FileSession {
    url?: string;
    blob?: Blob;
    thumbnail?: Blob;
    imageSize?: ImageSize;
    loading?: boolean;
    error?: Error;
    retry?: () => void;
}

export const defaultFileSession: FileSession = {};

/**
 * A class to manage file sessions.
 */
export class FileSessions {
    private sessions$ = new BehaviorSubject<Record<FileSessionKey, FileSession>>({});

    constructor(private service: RichTextFileStorageService) {}

    /**
     * Get the file session by the key.
     *
     * @param key The key to identify the file session.
     * @returns The file session.
     */
    get(key: FileSessionKey): FileSession | undefined {
        return this.sessions$.value[key];
    }

    /**
     * Subscribe to the file session changes.
     *
     * @param key The key to identify the file session.
     * @param onChange The callback to be called when the file session changes.
     * @returns A function to unsubscribe the changes.
     */
    subscribe(key: FileSessionKey, onChange: (session: FileSession) => void): () => void {
        const subscription = this.sessions$
            .pipe(
                map(sessions => sessions[key]),
                filter(isDefined),
                distinctUntilChanged()
            )
            .subscribe({ next: onChange });

        return () => subscription.unsubscribe();
    }

    /**
     * Manage an upload file session.
     *
     * @param key The key to identify the file session.
     * @param file The file to be uploaded.
     */
    upload(key: FileSessionKey, file: File): void {
        if (this.get(key)) return;

        this.put(key, { blob: file });
        this.performUpload(key, file);
    }

    /**
     * Manage a download file session.
     *
     * @param key The key to identify the file session.
     * @param url The URL to download the file.
     */
    download(key: FileSessionKey, url: string): void {
        if (this.get(key)) return;

        this.put(key, { url });
        this.performDownload(key, url);
        this.performThumbnail(key, url);
    }

    /**
     * Manage a thumbnail file session.
     *
     * @param key The key to identify the file session.
     * @param url The URL to thumbnail the file.
     */
    thumbnail(key: FileSessionKey, url: string): void {
        if (this.get(key)) return;

        this.put(key, { url });
        this.performThumbnail(key, url);
    }

    private put(key: FileSessionKey, session: FileSession): void {
        this.sessions$.next({ ...this.sessions$.value, [key]: session });
    }

    private update(key: FileSessionKey, callback: (session: FileSession) => FileSession, preventClean?: boolean): void {
        const session = this.sessions$.value[key];
        const newSession = callback(preventClean ? session : cleanFileSession(session));
        this.put(key, newSession);
    }

    private async performUpload(key: FileSessionKey, file: File): Promise<void> {
        try {
            const imageSize = await this.measureImageInternal(key, file);
            const url = await this.uploadInternal(key, file);

            const thumbnail = await this.thumbnailInternal(key, url);
            if (thumbnail && !imageSize) await this.measureImageInternal(key, file);
        } catch {
            // Ignore any error intentionally
        }
    }

    private async performDownload(key: FileSessionKey, url: string): Promise<void> {
        try {
            const blob = await this.downloadInternal(key, url);
            await this.measureImageInternal(key, blob);
        } catch {
            // Ignore any error intentionally
        }
    }

    private async performThumbnail(key: FileSessionKey, url: string): Promise<void> {
        try {
            const thumbnail = await this.thumbnailInternal(key, url);
            if (thumbnail) await this.measureImageInternal(key, thumbnail, true);
        } catch {
            // Ignore any error intentionally
        }
    }

    private handleError(key: FileSessionKey, error: Error): Promise<void> {
        return new Promise<void>(resolve => this.update(key, session => ({ ...session, error, retry: resolve })));
    }

    private async uploadInternal(key: FileSessionKey, file: File): Promise<string> {
        while (true) {
            try {
                this.update(key, session => ({ ...session, loading: true }));
                const url = await this.service.upload(file);
                this.update(key, session => ({ ...session, url }));
                return url;
            } catch (e) {
                await this.handleError(key, coerceError(e));
            }
        }
    }

    private async downloadInternal(key: FileSessionKey, url: string): Promise<Blob> {
        while (true) {
            try {
                this.update(key, session => ({ ...session, loading: true }));
                const blob = await this.service.download(url);
                this.update(key, session => ({ ...session, blob }));
                return blob;
            } catch (error) {
                await this.handleError(key, coerceError(error));
            }
        }
    }

    private async thumbnailInternal(key: FileSessionKey, url: string): Promise<Blob | undefined> {
        if (!this.service.thumbnail) return;

        // The thumbnail is optional, so we don't manage loading and error states here
        try {
            const thumbnail = await this.service.thumbnail(url);
            this.update(key, session => ({ ...session, thumbnail }), true);
            return thumbnail;
        } catch {
            // Ignore any error intentionally
        }
    }

    private async measureImageInternal(
        key: FileSessionKey,
        blob: Blob,
        preventOverride?: boolean
    ): Promise<ImageSize | undefined> {
        if (!isImageMime(blob.type)) return;

        try {
            const imageSize = await measureImage(blob);
            this.update(
                key,
                session => (preventOverride && session.imageSize ? session : { ...session, imageSize }),
                true
            );
            return imageSize;
        } catch {
            // Ignore any error intentionally
        }
    }
}

function coerceError(error: unknown): Error {
    return error instanceof Error ? error : new Error('Unknown error');
}

function cleanFileSession(session: FileSession): FileSession {
    const newSession = { ...session };
    delete newSession.loading;
    delete newSession.error;
    delete newSession.retry;
    return newSession;
}

/**
 * Check if the file session is valid. A session is considered invalid if it does not have a URL or a blob, which means
 * the file cannot be uploaded or downloaded.
 *
 * @param session The file session.
 * @returns `true` if the file session is valid, otherwise `false`.
 */
export function isValidFileSession(session: FileSession): boolean {
    return Boolean(session.blob || session.url);
}
