export enum FormQuestionType {
    Text,
    RichText,
    Choice,
    DateTime,
}

export interface BaseFormQuestion<Type extends FormQuestionType> {
    type: Type;
    id: string;
    familyId?: string;
    eTag?: string;
    order: number;
    title?: string;
    rtTitle?: string;
    subtitle?: string;
    rtSubtitle?: string;
    required?: boolean;
    tooltip?: string;
    placeholder?: string;
}

export interface BaseFormAnswer<Content> {
    eTag?: string;
    content?: Content;
}

export interface BaseFormEntry<Question, Answer> {
    question: Question;
    answer: Answer;
    active?: boolean;
    invalid?: boolean;
    errorMessage?: string;
}
