import { FormEntry, Validation, ValidationFailure, Validator } from '../../types';
import { createValidationSuccess } from '../../utilities';

export interface RequiredValidatorOptions {
    errorMessage?: string;
}

const defaultErrorMessage = 'This field is required';

/**
 * A validator that checks if the answer is empty.
 * If the question is not required, this validator will always pass.
 * Otherwise, this validator will fail if the answer is empty.
 */
export class RequiredValidator implements Validator<FormEntry> {
    private errorMessage: string;

    /**
     * Creates a required validator.
     *
     * @param options.errorMessage The error message if the answer is empty.
     * @returns A required validator.
     */
    constructor({ errorMessage = defaultErrorMessage }: RequiredValidatorOptions = {}) {
        this.errorMessage = errorMessage;
    }

    validate({ question, answer }: FormEntry): Validation {
        if (!question.required) {
            return createValidationSuccess();
        }

        const content = answer.content;

        // Check for undefined.
        if (content === undefined) {
            return this.createValidationFailure();
        }

        // Check for string.
        if (typeof content === 'string') {
            return content ? createValidationSuccess() : this.createValidationFailure();
        }

        // Check for choice answer.
        if (typeof content === 'object' && 'chosen' in content) {
            return content.chosen.length || content.other ? createValidationSuccess() : this.createValidationFailure();
        }

        return createValidationSuccess();
    }

    private createValidationFailure(): ValidationFailure {
        return { valid: false, errorMessage: this.errorMessage };
    }
}
