export enum RichTextFieldButton {
    OK,
    Cancel,
    Close,
    Retry,
    Remove,
    Copy,
    Download,
    ViewOriginalFile,
    Bold,
    Italic,
    Underline,
    Strikethrough,
    BackgroundColor,
    TextColor,
    FontSize,
    BulletedList,
    NumberedList,
    DecreaseIndent,
    IncreaseIndent,
    InsertLink,
    InsertImage,
    InsertFile,
    InsertTable,
    Code,
    ClearFormat,
}

export enum RichTextFieldFontSize {
    Small,
    Medium,
    Large,
}

export enum RichTextFieldBackgroundColor {
    Removal,
    KobiRed,
    RoseBud,
    Goldenrod,
    Primrose,
    Mantis,
    RegentStBlue,
    PeriwinkleGray,
    PaleRose,
}

export enum RichTextFieldTextColor {
    Removal,
    Red,
    TuscanOrange,
    Sunglow,
    Pear,
    Eucalyptus,
    FadedJade,
    FunBlue,
    TyrianPurple,
}

export interface RichTextFieldTableSize {
    rows: number;
    cols: number;
}

export interface RichTextFieldStrings {
    button: (button: RichTextFieldButton) => string;
    fontSize: (size: RichTextFieldFontSize) => string;
    backgroundColor: (color: RichTextFieldBackgroundColor) => string;
    textColor: (color: RichTextFieldTextColor) => string;
    insertLinkTitle: string;
    insertLinkUrl: string;
    insertLinkDisplayAs: string;
    insertTableTitle: (size?: RichTextFieldTableSize) => string;
    messageContentUnavailable: string;
    defaultImageFileStem: string;
}

/**
 * A file storage for rich text fields.
 *
 * @property upload - Upload a file and return the file locator URL.
 * @property download - Retrieve the file blob by the file locator URL.
 * @property thumbnail - Retrieve the thumbnail blob by the file locator URL.
 */
export interface RichTextFileStorageService {
    upload: (file: File) => Promise<string>;
    download: (url: string) => Promise<Blob>;
    thumbnail?: (url: string) => Promise<Blob>;
}
