import { parseFloatStrictly } from '@addressbook/utilities-core';
import {
    FormEntry,
    FormQuestionType,
    TextNumberRestriction,
    TextNumberRestrictionType,
    Validation,
    ValidationFailure,
    Validator,
} from '../../types';
import { createValidationSuccess } from '../../utilities';

/**
 * A function that returns the error message by the given restriction.
 *
 * @param restriction The restriction.
 * @returns The error message.
 */
export type ErrorMessageByRestriction = (restriction: TextNumberRestriction) => string;

export interface NumberValidatorOptions {
    errorMessageWhenNaN?: string;
    errorMessageByRestriction?: ErrorMessageByRestriction;
}

const defaultErrorMessageWhenNaN = 'Please enter a number';
const defaultErrorMessageByRestriction: ErrorMessageByRestriction = restriction => {
    switch (restriction.type) {
        case TextNumberRestrictionType.GreaterThan:
            return `Please enter a number greater than ${restriction.min}`;
        case TextNumberRestrictionType.GreaterThanOrEqualTo:
            return `Please enter a number greater than or equal to ${restriction.min}`;
        case TextNumberRestrictionType.LessThan:
            return `Please enter a number less than ${restriction.max}`;
        case TextNumberRestrictionType.LessThanOrEqualTo:
            return `Please enter a number less than or equal to ${restriction.max}`;
        case TextNumberRestrictionType.EqualTo:
            return `Please enter a number equal to ${restriction.value}`;
        case TextNumberRestrictionType.NotEqualTo:
            return `Please enter a number not equal to ${restriction.value}`;
        case TextNumberRestrictionType.Between:
            return `Please enter a number between ${restriction.min} and ${restriction.max}`;
        case TextNumberRestrictionType.NotBetween:
            return `Please enter a number not between ${restriction.min} and ${restriction.max}`;
    }
};

function follow(content: number, restriction: TextNumberRestriction): boolean {
    switch (restriction.type) {
        case TextNumberRestrictionType.GreaterThan:
            return content > restriction.min;
        case TextNumberRestrictionType.GreaterThanOrEqualTo:
            return content >= restriction.min;
        case TextNumberRestrictionType.LessThan:
            return content < restriction.max;
        case TextNumberRestrictionType.LessThanOrEqualTo:
            return content <= restriction.max;
        case TextNumberRestrictionType.EqualTo:
            return content === restriction.value;
        case TextNumberRestrictionType.NotEqualTo:
            return content !== restriction.value;
        case TextNumberRestrictionType.Between:
            return content >= restriction.min && content <= restriction.max;
        case TextNumberRestrictionType.NotBetween:
            return content < restriction.min || content > restriction.max;
    }
}

/**
 * A validator that checks if the answer is a number.
 * If the question is not a number question, this validator will always pass.
 * Otherwise, this validator will fail if the answer is not a number.
 */
export class NumberValidator implements Validator<FormEntry> {
    private errorMessageWhenNaN: string;
    private errorMessageByRestriction: ErrorMessageByRestriction;

    /**
     * Creates a number validator.
     *
     * @param options.errorMessageWhenNaN The error message if the answer content is not a number.
     * @param options.errorMessageByRestriction The error message if the answer content violates a restriction.
     * @returns A number validator.
     */
    constructor({
        errorMessageWhenNaN = defaultErrorMessageWhenNaN,
        errorMessageByRestriction = defaultErrorMessageByRestriction,
    }: NumberValidatorOptions = {}) {
        this.errorMessageWhenNaN = errorMessageWhenNaN;
        this.errorMessageByRestriction = errorMessageByRestriction;
    }

    validate({ question, answer }: FormEntry): Validation {
        // Filter out non-number questions, or no answer yet.
        if (question.type !== FormQuestionType.Text || !question.number || !answer.content) {
            return createValidationSuccess();
        }

        const content = parseFloatStrictly(answer.content as string);

        // Check for NaN.
        if (isNaN(content)) {
            return this.createValidationFailure(this.errorMessageWhenNaN);
        }

        // Check for restrictions.
        const restriction = question.restriction;
        if (restriction && !follow(content, restriction)) {
            return this.createValidationFailure(this.errorMessageByRestriction(restriction));
        }

        return createValidationSuccess();
    }

    private createValidationFailure(errorMessage: string): ValidationFailure {
        return { valid: false, errorMessage };
    }
}
