import { mergeStyles } from '@fluentui/react';
import React from 'react';
import { useColorMenuItemStyles } from './ColorMenuItem.styles';

export interface ColorMenuItemProps {
    text: string;
    color?: string;
    onClick?: () => void;
}

export const ColorMenuItem: React.FC<ColorMenuItemProps> = ({ text, color, onClick }) => {
    const styles = useColorMenuItemStyles();

    return (
        <button
            className={mergeStyles(styles.root, color ? styles.color : styles.text)}
            style={{ backgroundColor: color }}
            onClick={onClick}
            title={text}>
            {!color && text}
        </button>
    );
};
