import { FormEntry, FormQuestionType, Validation, ValidationFailure, Validator } from '../../types';
import { hasErrorFiles, hasUploadingFiles, isHtmlEmpty, createValidationSuccess } from '../../utilities';

export interface RichTextValidatorOptions {
    requiredErrorMessage?: string;
    filesErrorMessage?: string;
}

const defaultRequiredErrorMessage = 'This field is required';
const defaultFilesErrorMessage = 'Something went wrong while uploading files';

/**
 * A validator that checks rich text answers.
 */
export class RichTextValidator implements Validator<FormEntry> {
    private requiredErrorMessage: string;
    private filesErrorMessage: string;

    /**
     * Creates a rich text validator.
     *
     * @param options.requiredErrorMessage The error message to display when the answer is empty.
     * @param options.filesErrorMessage The error message to display when there are files with errors.
     * @returns A required validator.
     */
    constructor({
        requiredErrorMessage = defaultRequiredErrorMessage,
        filesErrorMessage = defaultFilesErrorMessage,
    }: RichTextValidatorOptions = {}) {
        this.requiredErrorMessage = requiredErrorMessage;
        this.filesErrorMessage = filesErrorMessage;
    }

    validate({ question, answer }: FormEntry): Validation {
        if (question.type !== FormQuestionType.RichText) {
            return createValidationSuccess();
        }

        const content = (answer.content ?? '') as string;
        const dom = new DOMParser().parseFromString(content, 'text/html');

        // Check for required.
        if (question.required && isHtmlEmpty(dom)) {
            return this.createValidationFailure(this.requiredErrorMessage);
        }

        // Check for error files.
        if (hasErrorFiles(dom)) {
            return this.createValidationFailure(this.filesErrorMessage);
        }

        // Check for uploading files.
        if (hasUploadingFiles(dom)) {
            return this.createValidationFailure();
        }

        return createValidationSuccess();
    }

    private createValidationFailure(errorMessage?: string): ValidationFailure {
        return { valid: false, errorMessage };
    }
}
