/* istanbul ignore file */

import { FontWeights, IMessageBarStyleProps, IMessageBarStyles, ITheme, MessageBarType } from '@fluentui/react';
import { FontSizes } from '../teams-sizes';

export function getMessageBarStyles(_themeName: string, _theme: ITheme) {
    return (props: IMessageBarStyleProps): Partial<IMessageBarStyles> => {
        const { theme, messageBarType } = props;

        const margin = {
            marginTop: '12px',
            marginBottom: '12px',
        };

        const ERROR_COLOR = theme.palette.redDark;
        const ERROR_BACKGROUND_COLOR = theme.palette.redLight;
        const ERROR_BOTTOM_BORDER_COLOR = theme.palette.red;

        const INFO_COLOR = theme.palette.neutralDark;
        const INFO_BACKGROUND_COLOR = theme.palette.neutralTertiaryAlt;
        const INFO_BOTTOM_BORDER_COLOR = theme.palette.neutralTertiary;

        const styles = {
            actions: {},
            iconContainer: margin,
            icon: {
                display: 'none',
            },
            dismissal: [
                {
                    width: '26px',
                    height: 'auto',
                    padding: '2px 4px',
                    marginRight: '8px',
                    ...margin,
                    selectors: {
                        '.ms-Button-icon': {
                            fontSize: FontSizes.size10,
                            fontWeight: 'bold',
                        },
                    },
                },
                messageBarType === MessageBarType.error && {
                    color: ERROR_COLOR,
                    selectors: {
                        '.ms-Button-icon': {
                            color: ERROR_COLOR,
                        },
                    },
                },
                messageBarType === MessageBarType.info && {
                    color: INFO_COLOR,
                    selectors: {
                        '.ms-Button-icon': {
                            color: INFO_COLOR,
                        },
                    },
                },
            ],
            text: [
                {
                    fontSize: FontSizes.size14,
                    fontWeight: FontWeights.semibold,
                    justifyContent: 'center',
                    flexGrow: 1,
                    ...margin,
                },
            ],
            root: [
                {
                    selectors: {
                        '.ms-Link': {
                            fontWeight: 'inherit',
                            fontSize: 'inherit',
                            textDecoration: 'underline',
                            color: 'inherit',
                            selectors: {
                                ':hover': {
                                    color: 'inherit',
                                },
                                ':focus': {
                                    color: 'inherit',
                                },
                                ':active': {
                                    color: 'inherit',
                                },
                                ':active:hover': {
                                    color: 'inherit',
                                },
                            },
                        },
                    },
                },
                messageBarType === MessageBarType.info && {
                    color: INFO_COLOR,
                    background: INFO_BACKGROUND_COLOR,
                    borderBottom: `1px solid ${INFO_BOTTOM_BORDER_COLOR}`,
                },
                messageBarType === MessageBarType.error && {
                    color: ERROR_COLOR,
                    background: ERROR_BACKGROUND_COLOR,
                    borderBottom: `1px solid ${ERROR_BOTTOM_BORDER_COLOR}`,
                },
            ],
        } as Partial<IMessageBarStyles>;

        return styles;
    };
}
