import { ITextField } from '@fluentui/react';
import React from 'react';
import { ChoiceAnswerContent, ChoiceEntry } from '../types';

export interface UseShuffledProps<T> {
    array: T[];
    enabled?: boolean;
}

/**
 * Returns a shuffled copy of the array.
 *
 * @param props.array The array to shuffle.
 * @param props.enabled Whether to shuffle the array.
 * @returns An array.
 *
 * @example useShuffled(['Yes', 'No', 'Maybe later']) // ['No', 'Maybe later', 'Yes']
 * @example useShuffled(['Yes', 'No', 'Maybe later'], false) // ['Yes', 'No', 'Maybe later']
 */
export function useShuffled<T>({ array, enabled }: UseShuffledProps<T>): T[] {
    return React.useMemo(() => {
        if (!enabled) return array;

        return array
            .map<[number, T]>(element => [Math.random(), element])
            .sort(([first], [second]) => first - second)
            .map(([, element]) => element);
    }, [array, enabled]);
}

/**
 * Returns a function that updates the entry's answer, according to the chosen options and the "other" value.
 *
 * @param entry The current choice entry.
 * @param onEntryChange A function that is called when the entry changes.
 * @returns A function if `onEntryChange` is defined, `undefined` otherwise.
 */
export function useChoiceChange(
    entry: ChoiceEntry,
    onEntryChange?: (entry: ChoiceEntry) => void
): ((chosen: string[], other?: string) => void) | undefined {
    return React.useMemo(
        () =>
            onEntryChange &&
            ((chosen: string[], other?: string) => {
                const newAnswer = { ...entry.answer };

                if (chosen.length > 0 || other !== undefined) {
                    newAnswer.content = { chosen, other };
                } else {
                    delete newAnswer.content;
                }

                onEntryChange({ ...entry, answer: newAnswer });
            }),
        [entry, onEntryChange]
    );
}

export interface UseChosenAndOther {
    chosen: string[];
    otherKey: string;
    otherValue?: string;
    otherSelected: boolean;
    otherFieldRef: React.RefObject<ITextField>;
}

/**
 * Returns the chosen options and the "other" related values.
 *
 * @param choices The choices besides the "other" option.
 * @param content The current answer content.
 * @returns An object with the chosen options and the "other" related values.
 */
export function useChosenAndOther(choices: string[], content?: ChoiceAnswerContent): UseChosenAndOther {
    const otherFieldRef = React.useRef<ITextField>(null);
    return React.useMemo(
        () => ({
            chosen: content?.chosen ?? [],
            otherKey: ['other', ...choices.map(choice => choice.replace(/(\s|-)+/g, '_').toLowerCase())].join('-'),
            otherValue: content?.other,
            otherSelected: content?.other !== undefined,
            otherFieldRef,
        }),
        [choices, content]
    );
}
