import { DefaultButton, mergeStyles, PrimaryButton, TextField } from '@fluentui/react';
import React from 'react';
import { RichTextFieldButton, RichTextFieldStrings } from '../../types';
import { useInsertLinkStyles } from './InsertLink.styles';

export interface Link {
    url: string;
    displayText: string;
}

export interface InsertLinkProps extends React.HTMLAttributes<HTMLDivElement> {
    initialLink: Link;
    onConfirm?: (link: Link) => void;
    onCancel?: () => void;
    normalizeUrl?: (url: string) => string;
    strings: Pick<RichTextFieldStrings, 'button' | 'insertLinkTitle' | 'insertLinkUrl' | 'insertLinkDisplayAs'>;
}

export const InsertLink: React.FC<InsertLinkProps> = ({
    initialLink,
    onConfirm,
    onCancel,
    normalizeUrl,
    strings,
    className,
    ...props
}) => {
    const [displayText, setDisplayText] = React.useState(initialLink.displayText);
    const [url, setUrl] = React.useState(initialLink.url);
    const normalizedUrl = React.useMemo(() => normalizeUrl?.(url) ?? url, [url, normalizeUrl]);

    const onConfirmInternal = React.useCallback(() => {
        if (!normalizedUrl) return;
        onConfirm?.({ url: normalizedUrl, displayText: displayText || url });
    }, [onConfirm, normalizedUrl, displayText, url]);

    const onKeyPress = React.useCallback(
        (e: React.KeyboardEvent) => {
            if (e.key === 'Enter') onConfirmInternal();
        },
        [onConfirmInternal]
    );

    const styles = useInsertLinkStyles();
    return (
        <div className={mergeStyles(styles.root, className)} {...props}>
            <TextField
                label={strings.insertLinkUrl}
                required
                type='url'
                value={url}
                onChange={(_, newValue = '') => setUrl(newValue)}
                onKeyPress={onKeyPress}
            />
            <TextField
                label={strings.insertLinkDisplayAs}
                value={displayText}
                onChange={(_, newValue = '') => setDisplayText(newValue)}
                onKeyPress={onKeyPress}
            />
            <div className={mergeStyles(styles.buttons)}>
                <DefaultButton onClick={onCancel}>{strings.button(RichTextFieldButton.Cancel)}</DefaultButton>
                <PrimaryButton onClick={onConfirmInternal} disabled={!normalizedUrl}>
                    {strings.button(RichTextFieldButton.OK)}
                </PrimaryButton>
            </div>
        </div>
    );
};
