import { makeStyles, FontSizes, isContrastTheme } from '@addressbook/components-fluentui-teams-theme';

export const useInsertTableStyles = makeStyles(theme => ({
    root: {
        display: 'flex',
        flexDirection: 'column',
        gap: '8px',
        padding: '16px',
    },
    header: {
        fontSize: FontSizes.size14,
        fontWeight: 600,
    },
    table: {
        display: 'grid',
        gap: '2px',
    },
    cell: {
        width: '16px',
        height: '16px',
        cursor: 'pointer',
        backgroundColor: 'transparent',
        border: `1px solid ${isContrastTheme(theme.name) ? theme.palette.black : theme.palette.neutralPrimary}`,
    },
    selecting: {
        backgroundColor: theme.palette.themePrimary,
    },
}));
