/* istanbul ignore file */

import { IButtonStyles, IRawStyle, ITheme } from '@fluentui/react';
import { ContrastColors } from '../teams-colors';
import { ShadowLevels } from '../teams-shadow-levels';
import { isContrastTheme } from '../teams-theme';
import { teamsBorderRadius } from './constants';

export function getIconButtonStyles(themeName: string, theme: ITheme): Partial<IButtonStyles> {
    const styles = {
        root: {
            background: 'none',
            color: theme.palette.black,
            // Inherits the icon color from root
            '.ms-Icon': {
                color: 'unset',
            },
            // Work around for fluentui button focus outline not showing in iOS Safari.
            ':focus-visible, .ms-Fabric--isFocusVisible &:focus': {
                '::after': {
                    content: '""',
                    position: 'absolute',
                    inset: '2px',
                    outline: `1px solid ${theme.palette.black}`,
                    borderRadius: teamsBorderRadius,
                },
            },
        },
        rootHovered: {
            backgroundColor: 'none',
            color: theme.palette.accent,
            boxShadow: 'none',
        },
        rootExpanded: {
            boxShadow: 'none',
        },
        rootPressed: {
            background: 'none',
        },
        rootDisabled: {
            color: isContrastTheme(themeName) ? ContrastColors.appGreenContrast : undefined,
        },
    } as Partial<IButtonStyles>;

    // Contrast theme overrides
    if (isContrastTheme(themeName)) {
        // Root Hovered
        const rootHovered = styles.rootHovered as IRawStyle;
        rootHovered.backgroundColor = theme.palette.themeDark;
        rootHovered.color = theme.palette.white;
        rootHovered.boxShadow = ShadowLevels.shadowLevel6;

        // Menu is expanded
        const rootExpanded = styles.rootExpanded as IRawStyle;
        rootExpanded.boxShadow = ShadowLevels.shadowLevel6;
    }
    return styles;
}
