import { ActionButton, IconButton, mergeStyles, TextField } from '@fluentui/react';
import React from 'react';
import { ChoiceAction, ChoiceAppearance, FormQuestionType } from '../../../types';
import { getNewChoice, useTextFieldStyles } from '../../../utilities';
import { QuestionSettingsProps } from '../types';
import { useChoiceSettingsStyles } from './ChoiceSettings.styles';

export function ChoiceSettings({
    question,
    onQuestionChange,
    constants,
    strings,
}: QuestionSettingsProps): JSX.Element | null {
    const styles = useChoiceSettingsStyles();
    const textFieldStyles = useTextFieldStyles();

    if (question.type !== FormQuestionType.Choice) return null;

    const reachMinChoices = question.choices.length <= constants.minChoices;
    const reachMaxChoices = question.choices.length >= constants.maxChoices;

    const decoratorClassName =
        question.appearance === ChoiceAppearance.Checkbox
            ? mergeStyles(styles.decorator, styles.checkbox)
            : question.appearance === ChoiceAppearance.Radio
            ? mergeStyles(styles.decorator, styles.radio)
            : undefined;

    const decorator = decoratorClassName && <div className={decoratorClassName} />;

    return (
        <div className={styles.root}>
            {question.choices.map((choice, index) => (
                <div className={styles.choiceRow} key={index}>
                    {decorator}
                    <TextField
                        value={choice}
                        onChange={(_, value) => {
                            const newChoices = [...question.choices];
                            newChoices[index] = value ?? '';
                            onQuestionChange({ ...question, choices: newChoices });
                        }}
                        maxLength={constants.maxChoiceLength}
                        placeholder={strings.choicePlaceholder}
                        styles={textFieldStyles}
                    />
                    <IconButton
                        iconProps={{ iconName: 'Delete' }}
                        title={strings.choiceAction(ChoiceAction.Delete)}
                        disabled={reachMinChoices}
                        onClick={() => {
                            const newChoices = [...question.choices];
                            newChoices.splice(index, 1);
                            onQuestionChange({ ...question, choices: newChoices });
                        }}
                    />
                    <IconButton
                        iconProps={{ iconName: 'Up' }}
                        title={strings.choiceAction(ChoiceAction.MoveUp)}
                        disabled={index === 0}
                        onClick={() => {
                            const newChoices = [...question.choices];
                            [newChoices[index - 1], newChoices[index]] = [newChoices[index], newChoices[index - 1]];
                            onQuestionChange({ ...question, choices: newChoices });
                        }}
                    />
                    <IconButton
                        iconProps={{ iconName: 'Down' }}
                        title={strings.choiceAction(ChoiceAction.MoveDown)}
                        disabled={index === question.choices.length - 1}
                        onClick={() => {
                            const newChoices = [...question.choices];
                            [newChoices[index + 1], newChoices[index]] = [newChoices[index], newChoices[index + 1]];
                            onQuestionChange({ ...question, choices: newChoices });
                        }}
                    />
                </div>
            ))}
            {question.allowOther && (
                <div className={styles.choiceRow}>
                    {decorator}
                    <TextField placeholder={strings.choiceOther} disabled styles={textFieldStyles} />
                    <IconButton
                        iconProps={{ iconName: 'Delete' }}
                        title={strings.choiceAction(ChoiceAction.Delete)}
                        onClick={() => {
                            const newQuestion = { ...question };
                            delete newQuestion.allowOther;
                            onQuestionChange(newQuestion);
                        }}
                    />
                    <IconButton
                        className={styles.hidden}
                        iconProps={{ iconName: 'Up' }}
                        title={strings.choiceAction(ChoiceAction.MoveUp)}
                        disabled
                    />
                    <IconButton
                        className={styles.hidden}
                        iconProps={{ iconName: 'Down' }}
                        title={strings.choiceAction(ChoiceAction.MoveDown)}
                        disabled
                    />
                </div>
            )}
            <div className={styles.actionRow}>
                <ActionButton
                    iconProps={{ iconName: 'Add' }}
                    text={strings.choiceAction(ChoiceAction.Add)}
                    disabled={reachMaxChoices}
                    onClick={() => {
                        const newChoices = [...question.choices, getNewChoice(strings, question.choices)];
                        onQuestionChange({ ...question, choices: newChoices });
                    }}
                />
                {!question.allowOther && (
                    <ActionButton
                        text={strings.choiceAction(ChoiceAction.AddOther)}
                        onClick={() => onQuestionChange({ ...question, allowOther: true })}
                    />
                )}
            </div>
        </div>
    );
}
