import { IconButton } from '@fluentui/react';
import React from 'react';
import { FieldProps } from '../../../abstractions';
import { FormContext, FormEntry, FormFeature, QuestionAction } from '../../types';
import { addNewFormEntry } from '../../utilities';

export type DesigningActionsProps = FieldProps<FormEntry, FormContext, FormFeature>;

export function DesigningActions({
    entry,
    onEntryChange,
    context,
    onContextChange,
    feature,
}: DesigningActionsProps): JSX.Element {
    const { entries } = context;
    const { comparator, idGenerator, orderGenerator, constants, strings } = feature;

    const [sortedEntries, sortedIndex, nextSlotOrder] = React.useMemo(() => {
        const sortedEntries = [...entries].sort(comparator);
        const sortedIndex = sortedEntries.findIndex(e => e === entry);
        const nextSlotOrder = orderGenerator(
            sortedEntries[sortedIndex]?.question.order,
            sortedEntries[sortedIndex + 1]?.question.order
        );
        return [sortedEntries, sortedIndex, nextSlotOrder];
    }, [comparator, entries, entry, orderGenerator]);

    const reachMaxEntries = entries.length >= constants.maxEntries;

    const addEntry = React.useCallback(() => {
        const newEntries = addNewFormEntry(entries, { idGenerator, order: nextSlotOrder, strings });
        onContextChange?.({ ...context, entries: newEntries });
    }, [context, entries, idGenerator, nextSlotOrder, onContextChange, strings]);

    const copyEntry = React.useCallback(() => {
        const newEntry = { ...entry };
        newEntry.question = { ...newEntry.question, id: idGenerator(), order: nextSlotOrder };
        delete newEntry.question.familyId;
        delete newEntry.question.eTag;
        delete newEntry.answer.content;
        delete newEntry.active;
        onContextChange?.({ ...context, entries: [...entries, newEntry] });
    }, [context, entries, entry, idGenerator, nextSlotOrder, onContextChange]);

    const deleteEntry = React.useCallback(() => {
        const newEntries = entries.filter(e => e !== entry);
        onContextChange?.({ ...context, entries: newEntries });
    }, [entries, onContextChange, context, entry]);

    const moveEntry = React.useCallback(
        (newIndex: number) => {
            if (newIndex === sortedIndex) return;

            const [newPrevIndex, newNextIndex] =
                newIndex < sortedIndex ? [newIndex - 1, newIndex] : [newIndex, newIndex + 1];

            const newOrder = orderGenerator(
                sortedEntries[newPrevIndex]?.question.order,
                sortedEntries[newNextIndex]?.question.order
            );

            const newEntry = { ...entry };
            newEntry.question = { ...newEntry.question, order: newOrder };
            onEntryChange?.(newEntry);
        },
        [entry, onEntryChange, orderGenerator, sortedEntries, sortedIndex]
    );

    return (
        <>
            <IconButton
                iconProps={{ iconName: 'Up' }}
                title={strings.questionAction(QuestionAction.MoveUp)}
                disabled={!sortedEntries[sortedIndex - 1]}
                onClick={() => moveEntry(sortedIndex - 1)}
            />
            <IconButton
                iconProps={{ iconName: 'Down' }}
                title={strings.questionAction(QuestionAction.MoveDown)}
                disabled={!sortedEntries[sortedIndex + 1]}
                onClick={() => moveEntry(sortedIndex + 1)}
            />
            <IconButton
                iconProps={{ iconName: 'Add' }}
                title={strings.questionAction(QuestionAction.Add)}
                onClick={addEntry}
                disabled={reachMaxEntries}
            />
            <IconButton
                iconProps={{ iconName: 'Copy' }}
                title={strings.questionAction(QuestionAction.Copy)}
                onClick={copyEntry}
                disabled={reachMaxEntries}
            />
            <IconButton
                iconProps={{ iconName: 'Delete' }}
                title={strings.questionAction(QuestionAction.Delete)}
                onClick={deleteEntry}
            />
        </>
    );
}
