import { ITheme } from '@fluentui/theme';

import {
    TeamsThemeContrast,
    TeamsThemeDark,
    TeamsThemeDefault,
    TeamsThemeDefaultV2,
    TeamsThemeDarkV2,
} from './teams/teams-theme';

export type themeNames = 'dark' | 'default' | 'contrast' | 'defaultV2' | 'darkV2' | 'tfl-dark';

export const getPalette = (themeName: themeNames): ITheme => {
    switch (themeName) {
        case 'defaultV2':
            return TeamsThemeDefaultV2;
        case 'dark':
        case 'tfl-dark':
            return TeamsThemeDark;
        case 'darkV2':
            return TeamsThemeDarkV2;
        case 'contrast':
            return TeamsThemeContrast;
        default:
            return TeamsThemeDefault;
    }
};
