import { isContrastTheme, makeStyles } from '@addressbook/components-fluentui-teams-theme';
import { fieldClassNames } from '../../utilities';

const actionsWidth = 48;
const horizontalExtendingMargin = 6;
const verticalExtendingMargin = 16;

export const useDesigningDecoratorStyles = makeStyles(theme => ({
    root: {
        margin: `-${horizontalExtendingMargin}px -${verticalExtendingMargin}px `,
    },
    inactive: {
        marginInlineEnd: `${actionsWidth - horizontalExtendingMargin}px`,
        padding: '15px',
        border: '1px solid transparent',
        ':hover, :focus': {
            backgroundColor: isContrastTheme(theme.name) ? theme.palette.white : theme.palette.neutralLighter,
            borderColor: isContrastTheme(theme.name) ? theme.palette.black : 'transparent',
            [fieldClassNames]: {
                backgroundColor: `${theme.palette.white} !important`,
            },
        },
        borderRadius: '8px',
        cursor: 'pointer',
    },
}));

export const useActiveDesigningDecoratorStyles = makeStyles(theme => ({
    root: {
        display: 'flex',
        flexDirection: 'row',

        overflow: 'hidden',
        borderRadius: '8px',

        borderWidth: '1px',
        borderStyle: 'solid',
        borderColor: isContrastTheme(theme.name) ? theme.palette.black : 'transparent',

        borderInlineStartWidth: '4px',
        borderInlineStartStyle: 'solid',
        borderInlineStartColor: theme.palette.themePrimary,

        [fieldClassNames]: {
            backgroundColor: `${theme.palette.white} !important`,
        },
    },
    settings: {
        flexGrow: 1,

        display: 'flex',
        flexDirection: 'column',
        padding: '15px', // 16px - 1px (parent border)
        paddingInlineStart: '12px', // 16px - 4px (parent border)
        gap: '12px',
        backgroundColor: isContrastTheme(theme.name) ? theme.palette.white : theme.palette.neutralLighter,

        borderInlineEndWidth: '1px',
        borderInlineEndStyle: 'solid',
        borderInlineEndColor: isContrastTheme(theme.name) ? theme.palette.black : 'transparent',
    },
    separator: {
        height: '1px',
        marginBlock: '7.5px',
        backgroundColor: theme.palette.neutralTertiaryAlt,
    },
    actions: {
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        gap: '8px',

        padding: '7px', // 8px - 1px (parent border)
        paddingInlineStart: '8px',

        backgroundColor: theme.palette.neutralLighterAlt,
        '.ms-Button-menuIcon': {
            display: 'none',
        },
    },
}));
