/* istanbul ignore file */

import { IBreadcrumbStyleProps, ITheme, FontWeights, IBreadcrumbStyles, IStyleFunctionOrObject } from '@fluentui/react';
import { FontSizes } from '../teams-sizes';
import { isContrastTheme } from '../teams-theme';

export function getBreadcrumbStyles(
    themeName: string,
    _theme: ITheme
): IStyleFunctionOrObject<IBreadcrumbStyleProps, IBreadcrumbStyles> {
    return (props: IBreadcrumbStyleProps) => {
        const { theme } = props;
        const { palette } = theme;

        const stateSelectors = {
            ':hover': {
                color: palette.themePrimary,
                textDecoration: 'none',
                backgroundColor: 'none',
            },
            ':active': {
                color: palette.themePrimary,
                backgroundColor: 'none',
                textDecoration: 'none',
            },
            '&:active:hover': {
                color: palette.themePrimary,
                backgroundColor: 'none',
                textDecoration: 'none',
            },
        };
        const styles = {
            root: {
                margin: 0,
                fontSize: FontSizes.size14,
                lineHeight: FontSizes.size20,
                selectors: {
                    '.ms-Breadcrumb-listItem:last-child .ms-Breadcrumb-itemLink': {
                        fontWeight: FontWeights.bold,
                        color: palette.black,
                    },
                    '.ms-Breadcrumb-listItem:last-child .ms-Breadcrumb-item': {
                        fontWeight: FontWeights.bold,
                    },
                },
            },
            item: {
                color: palette.black,
                fontSize: FontSizes.size14,
                lineHeight: '40px',
                height: '40px',
                selectors: {
                    ':last-child': {
                        color: palette.black,
                    },
                },
            },
            listItem: {
                selectors: {
                    ':last-child': {
                        selectors: {
                            '.ms-Breadcrumb-item': {
                                color: palette.black,
                            },
                        },
                    },
                },
            },
            itemLink: {
                fontSize: FontSizes.size14,
                lineHeight: '40px',
                outline: 'none',
                fontWeight: FontWeights.regular,
                color: palette.black,
                height: '40px',
                selectors: {
                    '.ms-Fabric--isFocusVisible &:focus': {
                        // Necessary due to changes of Link component not using getFocusStyle.
                        outline: 'none',
                    },
                    ...stateSelectors,
                },
            },
            chevron: {
                color: palette.neutralDark,
                fontSize: FontSizes.size12,
            },
            overflowButton: {
                color: palette.neutralDark,
                selectors: {
                    ...stateSelectors,
                },
            },
        };

        // Contrast theme overrides
        if (isContrastTheme(themeName)) {
            stateSelectors['&:active:hover'].color = palette.themeDark;
            stateSelectors[':hover'].color = palette.themeDark;
            stateSelectors[':active'].color = palette.themeDark;
        }
        return styles;
    };
}
