import { BaseFormFeature } from '../../abstractions';
import { FormContext } from './context';
import { FormEntry, FormQuestion, TextNumberRestrictionType, TextNumberRestrictionValueKey } from './entries';

/**
 * The success type of a validation result.
 */
export interface ValidationSuccess {
    valid: true;
}

/**
 * The failure type of a validation result.
 */
export interface ValidationFailure {
    valid: false;
    errorMessage?: string;
    preventChange?: boolean;
}

/**
 * The result of a validation, which can be either a success or a failure.
 */
export type Validation = ValidationSuccess | ValidationFailure;

/**
 * A validator is a function that validates a form entry.
 */
export interface Validator<Entry> {
    validate(entry: Entry): Validation;
}

/**
 * A sanitizer is a function that sanitizes HTML, to ensure the rich text title or subtitle is safe to render.
 *
 * @param html The HTML to sanitize.
 * @returns The sanitized HTML.
 */
export type Sanitizer = (html: string) => string;

/**
 * An ID generator is a function that generates a unique ID, which is used for form questions.
 *
 * @returns The generated ID.
 */
export type IdGenerator = () => string;

/**
 * An order generator is a function that generates the order of a form question.
 *
 * @param prevOrder The order of the previous question. Undefined if the question is the first question.
 * @param nextOrder The order of the next question. Undefined if the question is the last question.
 * @returns The generated order.
 */
export type OrderGenerator = (prevOrder: number | undefined, nextOrder: number | undefined) => number;

export enum QuestionAction {
    Add,
    Copy,
    Delete,
    MoveUp,
    MoveDown,
}

export enum QuestionDisplayType {
    ShortText,
    MultilineText,
    RichText,
    Number,
    MultipleChoice,
    SingleChoice,
    Dropdown,
    Date,
}

export enum ChoiceAction {
    Add,
    AddOther,
    Delete,
    MoveUp,
    MoveDown,
}

/**
 * The strings used in the form, to localize the form.
 */
export interface FormStrings {
    questionHint: (question: FormQuestion, active: boolean) => string;
    questionAction: (action: QuestionAction) => string;
    questionMockedFieldDefaultPlaceholder: (type: QuestionDisplayType) => string;
    questionDisplayTypeOption: (type: QuestionDisplayType) => string;
    questionNewTitle: string;
    questionTitle: string;
    questionSubtitle: string;
    questionRequired: string;
    questionRestriction: string;
    questionShuffle: string;
    choiceAction: (action: ChoiceAction) => string;
    choiceNew: (index: number) => string;
    choiceOther: string;
    choicePlaceholder: string;
    restrictionOption: (type: TextNumberRestrictionType) => string;
    restrictionValuePlaceholder: (key: TextNumberRestrictionValueKey) => string;
}

/**
 * The constants used in the form, which define limits or behaviors of the form.
 */
export interface FormConstants {
    maxEntries: number;
    maxTitleLength: number;
    maxSubtitleLength: number;
    initialChoices: number;
    minChoices: number;
    maxChoices: number;
    maxChoiceLength: number;
}

/**
 * The feature of a form, which is immutable during the lifecycle of the form.
 */
export interface FormFeature extends BaseFormFeature<FormEntry, FormContext, FormFeature> {
    validators: Validator<FormEntry>[];
    sanitizer: Sanitizer;
    idGenerator: IdGenerator;
    orderGenerator: OrderGenerator;
    constants: FormConstants;
    strings: FormStrings;
}
