import Mention from '@tiptap/extension-mention';

export const CustomizedMention = Mention.extend({
    addAttributes() {
        // Return an object with attribute configuration
        return {
            id: {
                default: null,
                parseHTML: element => {
                    return element.getAttribute('data-mention-id');
                },
                renderHTML: attributes => {
                    if (!attributes.id) {
                        return {};
                    }

                    return {
                        'data-mention-id': attributes.id,
                    };
                },
            },

            type: {
                default: null,
                parseHTML: element => element.getAttribute('data-mention-type'),
                renderHTML: attributes => {
                    if (!attributes.type) {
                        return {};
                    }

                    return {
                        'data-mention-type': attributes.type,
                    };
                },
            },

            label: {
                default: null,
                rendered: false,
                parseHTML: element => element.innerText,
            },
        };
    },

    renderHTML: props => [
        'span',
        {
            'data-type': 'mention',
            'data-mention-id': props.node.attrs?.id,
            'data-mention-type': props.node.attrs?.type,
        },
        props.node.attrs?.label ?? props.node.attrs?.id,
    ],
});
