import { mergeStyles } from '@fluentui/react';
import { RibbonPosition, RichTextField, RichTextFieldProps } from '@addressbook/components-rich-text-tiptap';
import React from 'react';
import { FormEntry, FormMode, FormQuestionType, RichTextEntry } from '../../types';
import { useRichTextFieldStyles } from '../../utilities';
import { BaseDescriptor, FieldComponentTypeFactory } from './BaseDescriptor';

const defaultOptions = {
    defaultPlaceholder: 'Enter your response',
};

/**
 * The options for the rich text descriptor to customize the rich text field.
 * `strings` is used for localization purposes.
 * `compact` is used for mobile view.
 * `callbacks` is used for callbacks to log events.
 */
export type RichTextDescriptorOptions = Partial<typeof defaultOptions> &
    Pick<RichTextFieldProps, 'strings' | 'compact' | 'callbacks'>;

const makeFieldComponent: FieldComponentTypeFactory<RichTextDescriptorOptions & typeof defaultOptions, RichTextEntry> =
    ({ defaultPlaceholder, ...options }) =>
    ({ entry, onEntryChange, context, showPlaceholder, delegatedProps }) => {
        const { mode } = context;
        const { question, answer } = entry;

        const value = answer.content ?? '';
        const onChange = React.useCallback(
            (value: string) => {
                const newAnswer = { ...entry.answer };
                newAnswer.content = value;
                if (!newAnswer.content) delete newAnswer.content;

                onEntryChange?.({ ...entry, answer: newAnswer });
            },
            [entry, onEntryChange]
        );
        const placeholder = showPlaceholder ? question.placeholder || defaultPlaceholder : undefined;
        const ribbonPosition =
            mode === FormMode.Viewing
                ? RibbonPosition.None
                : options.compact
                ? RibbonPosition.Bottom
                : RibbonPosition.Top;

        const styles = useRichTextFieldStyles();
        return React.useMemo(
            () => (
                <RichTextField
                    {...delegatedProps}
                    className={mergeStyles(
                        styles.root,
                        options.compact && styles.compact,
                        mode !== FormMode.Viewing && styles.minHeightConstrained
                    )}
                    value={value}
                    onChange={onChange}
                    placeholder={placeholder}
                    ribbonPosition={ribbonPosition}
                    focusable={mode !== FormMode.Viewing}
                    {...options}
                />
            ),
            [delegatedProps, mode, onChange, placeholder, ribbonPosition, styles, value]
        );
    };

/**
 * The descriptor that renders a rich text field for rich text questions.
 */
export class RichTextDescriptor extends BaseDescriptor<RichTextEntry> {
    protected readonly FieldComponent;

    constructor(options?: RichTextDescriptorOptions) {
        super();
        this.FieldComponent = makeFieldComponent({ ...defaultOptions, ...options });
    }

    /**
     * Tests whether the entry is a rich text question.
     *
     * @param entry The entry to test.
     * @returns Passes the test or not.
     */
    test(entry: FormEntry): entry is RichTextEntry {
        return entry.question.type === FormQuestionType.RichText;
    }
}
