import { Mark, mergeAttributes } from '@tiptap/core';
import { DOMOutputSpec } from '@tiptap/pm/model';
import { MarkRenderHTMLProps } from './types';

declare module '@tiptap/core' {
    interface Commands<ReturnType> {
        link: {
            /**
             * Set a link
             */
            setLink: (attributes: { href: string; target?: string | null; rel?: string | null }) => ReturnType;

            /**
             * Toggle a link
             */
            toggleLink: (attributes: { href: string; target?: string | null; rel?: string | null }) => ReturnType;

            /**
             * Unset a link
             */
            unsetLink: () => ReturnType;

            /**
             * Insert a link
             */
            insertLink: (
                text: string,
                attributes: { href: string; target?: string | null; rel?: string | null }
            ) => ReturnType;
        };
    }
}

export interface LinkOptions {
    HTMLAttributes: Record<string, any>;
}

export const Link = Mark.create<LinkOptions>({
    name: 'link',
    priority: 1000,
    keepOnSplit: false,
    inclusive: false,

    addOptions() {
        return {
            HTMLAttributes: {
                target: '_blank',
                rel: 'noopener noreferrer',
            },
        };
    },

    addAttributes() {
        return {
            href: {
                default: null,
            },
            target: {
                default: this.options.HTMLAttributes.target,
            },
            rel: {
                default: this.options.HTMLAttributes.rel,
            },
        };
    },

    parseHTML() {
        return [{ tag: 'a:not([href^="javascript:"])' }];
    },

    renderHTML({ HTMLAttributes }: MarkRenderHTMLProps): DOMOutputSpec {
        return ['a', mergeAttributes(this.options.HTMLAttributes, HTMLAttributes), 0];
    },

    addCommands() {
        return {
            setLink:
                attributes =>
                ({ chain }) =>
                    chain().setMark(this.name, attributes).run(),
            toggleLink:
                attributes =>
                ({ chain }) =>
                    chain().toggleMark(this.name, attributes, { extendEmptyMarkRange: true }).run(),
            unsetLink:
                () =>
                ({ chain }) =>
                    chain().unsetMark(this.name, { extendEmptyMarkRange: true }).run(),
            insertLink:
                (text, attributes) =>
                ({ tr, dispatch, state }) => {
                    const { selection } = tr;
                    const { from, to } = selection;

                    const mark = state.schema.marks.link.create(attributes);
                    tr.insertText(text, from, to).addMark(from, from + text.length, mark);

                    dispatch?.(tr);
                    return true;
                },
        };
    },
});
