import {
    ICheckboxStyles,
    IChoiceGroupOptionStyles,
    IChoiceGroupStyles,
    IDatePickerStyles,
    IDropdownStyles,
    ITextFieldStyles,
    IToggleStyles,
} from '@fluentui/react';
import { FontSizes, isContrastTheme, makeStyles } from '@addressbook/components-fluentui-teams-theme';

export const useRichTextFieldStyles = makeStyles({
    root: {
        '--min-height': '160px',
    },
    compact: {
        '--min-height': '120px',
    },
    minHeightConstrained: {
        minHeight: 'var(--min-height)',
    },
});

export const useTextFieldStyles = makeStyles<Pick<ITextFieldStyles, 'fieldGroup' | 'field' | 'wrapper'>>(theme => ({
    fieldGroup: {
        '.ms-TextField-wrapper &.ms-TextField-fieldGroup': {
            backgroundColor: isContrastTheme(theme.name) ? theme.palette.white : theme.palette.neutralLighter,
            ':has(input)': {
                height: '32px',
            },
            ':has([aria-invalid=true])': {
                borderWidth: '1px',
                borderStyle: 'solid',
                borderColor: theme.palette.redDark,
            },
            '.is-active &:not(:has([aria-invalid=true]))': {
                borderBottomColor: isContrastTheme(theme.name) ? theme.palette.accent : theme.palette.themePrimary,
            },
        },
    },
    field: {
        '::placeholder': {
            color: isContrastTheme(theme.name) ? theme.palette.black : theme.palette.neutralPrimary,
        },
    },
    wrapper: {
        '.ms-Label': {
            fontSize: `${FontSizes.size14} !important`,
            color: theme.palette.neutralDark,
            fontWeight: 600,
            padding: 0,
            paddingBottom: '4px',
        },
    },
}));

export const useCheckboxStyles = makeStyles<Pick<ICheckboxStyles, 'root' | 'checkbox' | 'label' | 'text'>>(theme => ({
    root: {
        '&.ms-Checkbox': {
            padding: 0,
        },
    },
    checkbox: {
        marginInlineEnd: '12px',
        borderColor: theme.palette.neutralDark,
    },
    label: {
        minWidth: 0,
    },
    text: {
        marginInlineStart: 0,
        overflowWrap: 'break-word',
        minWidth: 0,
    },
}));

export const useChoiceGroupStyles = makeStyles<Pick<IChoiceGroupStyles, 'flexContainer'>>({
    flexContainer: {
        display: 'flex',
        flexDirection: 'column',
        gap: '12px',
        paddingInline: '12px',

        // By default the input stretches to the full width of the container and accepts clicks.
        // We want to prevent that and solely rely on the label for the click.
        '.ms-ChoiceField-input': {
            width: 0,
            height: 0,
        },
    },
});

export const useChoiceGroupOptionStyles = makeStyles<
    Pick<IChoiceGroupOptionStyles, 'root' | 'choiceFieldWrapper' | 'field'>
>(theme => ({
    root: {
        margin: 0,
        minHeight: '20px',
    },
    choiceFieldWrapper: {
        minWidth: 0,
    },
    field: {
        '&.ms-ChoiceField-field': {
            display: 'block',
            // Make the radio button centered vertically
            '::before, ::after': {
                top: '50%',
                transform: 'translateY(-50%)',
            },
            '::before': {
                width: '16px',
                height: '16px',
                backgroundColor: 'transparent',
            },
            '::after': {
                insetInlineStart: '3px',
                width: '10px',
                height: '10px',
            },
            ':hover': isContrastTheme(theme.name) && {
                '::before': {
                    borderColor: theme.palette.accent,
                },
                '::after': {
                    border: 'none',
                    backgroundColor: theme.palette.accent,
                },
            },
        },
        '.ms-ChoiceFieldLabel': {
            display: 'block',
            paddingInlineStart: '28px',
            overflowWrap: 'break-word',
        },
    },
}));

export const useDropdownStyles = makeStyles<Pick<IDropdownStyles, 'dropdown' | 'title' | 'label'>>(theme => ({
    dropdown: {
        backgroundColor: isContrastTheme(theme.name) ? theme.palette.white : theme.palette.neutralLighter,
        '&.ms-Dropdown[aria-invalid=true]': {
            borderWidth: '1px',
            borderStyle: 'solid',
            borderColor: theme.palette.redDark,
        },
        '&.ms-Dropdown:not([aria-invalid=true])': {
            ':focus, .is-open': {
                borderBottomColor: isContrastTheme(theme.name) ? theme.palette.accent : theme.palette.themePrimary,
            },
        },
    },
    title: {
        '&.ms-Dropdown-titleIsPlaceHolder': {
            color: isContrastTheme(theme.name) ? theme.palette.black : theme.palette.neutralPrimary,
        },
    },
    label: {
        '&.ms-Label': {
            fontSize: FontSizes.size14,
            color: theme.palette.neutralDark,
            fontWeight: 600,
            padding: 0,
            paddingBottom: '4px',
        },
    },
}));

export const useOtherFieldStyles = makeStyles<Pick<ITextFieldStyles, 'wrapper' | 'fieldGroup' | 'field'>>(theme => ({
    wrapper: {
        marginInlineStart: '-6px',
    },
    fieldGroup: {
        '.ms-TextField-wrapper &.ms-TextField-fieldGroup': {
            backgroundColor: isContrastTheme(theme.name) ? theme.palette.white : theme.palette.neutralLighter,
            ':has([aria-invalid=true])': {
                borderWidth: '1px',
                borderStyle: 'solid',
                borderColor: theme.palette.redDark,
            },
            '.is-active &:not(:has([aria-invalid=true]))': {
                borderBottomColor: isContrastTheme(theme.name) ? theme.palette.accent : theme.palette.themePrimary,
            },
        },
    },
    field: {
        height: '28px',
        paddingInline: '6px',
        '::placeholder': {
            color: isContrastTheme(theme.name) ? theme.palette.black : theme.palette.neutralPrimary,
        },
    },
}));

export const useDatePickerStyles = makeStyles<Pick<IDatePickerStyles, 'statusMessage' | 'textField'>>(theme => ({
    statusMessage: {
        display: 'none',
    },
    textField: {
        '&.ms-TextField .ms-TextField-wrapper .ms-TextField-fieldGroup': {
            backgroundColor: isContrastTheme(theme.name) ? theme.palette.white : theme.palette.neutralLighter,
            ':has([aria-invalid=true])': {
                borderWidth: '1px',
                borderStyle: 'solid',
                borderColor: theme.palette.redDark,
            },
            ':not(:has([aria-invalid=true]))': {
                ':focus, .is-open': {
                    borderBottomColor: isContrastTheme(theme.name) ? theme.palette.accent : theme.palette.themePrimary,
                },
            },
            '.ms-TextField-field::placeholder, .ms-TextField-field span': {
                color: isContrastTheme(theme.name) ? theme.palette.black : theme.palette.neutralPrimary,
            },
        },
    },
}));

export const useToggleStyles = makeStyles<Pick<IToggleStyles, 'root' | 'label'>>({
    root: {
        margin: 0,
        gap: '12px',
    },
    label: {
        padding: 0,
        margin: 0,
    },
});

export const fieldClassNames = '.ms-TextField-fieldGroup, .ms-Dropdown, .ms-RichTextField';
