import { IIconStyles } from '@fluentui/react';

export function getIconStyles(): Partial<IIconStyles> {
    return {
        root: {
            width: '1em',
            height: '1em',
            img: {
                display: 'block',
                width: '100%',
                height: '100%',
                lineHeight: '100%',
                objectFit: 'contain',
            },
        },
    };
}
