import { FocusZone, mergeStyles } from '@fluentui/react';
import React from 'react';
import { RichTextFieldStrings, RichTextFieldTableSize } from '../../types';
import { useInsertTableStyles } from './InsertTable.styles';

export interface InsertTableProps extends React.HTMLAttributes<HTMLDivElement> {
    maxTableSize: RichTextFieldTableSize;
    onConfirm?: (tableSize: RichTextFieldTableSize) => void;
    strings: Pick<RichTextFieldStrings, 'insertTableTitle'>;
}

export const InsertTable: React.FC<InsertTableProps> = ({ maxTableSize, onConfirm, strings, className, ...props }) => {
    const [tableSize, setTableSize] = React.useState<RichTextFieldTableSize>();
    const title = React.useMemo(() => strings.insertTableTitle(tableSize), [strings, tableSize]);

    const styles = useInsertTableStyles();
    const gridStyle = React.useMemo(
        () => ({ gridTemplate: `repeat(${maxTableSize.rows}, 1fr) / repeat(${maxTableSize.cols}, 1fr)` }),
        [maxTableSize]
    );

    const gridCells = React.useMemo(
        () =>
            Array.from({ length: maxTableSize.rows }, (_, row) =>
                Array.from({ length: maxTableSize.cols }, (_, col) => (
                    <button
                        key={`${row}-${col}`}
                        className={mergeStyles(
                            styles.cell,
                            tableSize && row < tableSize.rows && col < tableSize.cols && styles.selecting
                        )}
                        onClick={onConfirm && (() => onConfirm(getTableSize(row, col)))}
                        onFocus={() => setTableSize(getTableSize(row, col))}
                        onPointerOver={event => event.currentTarget.focus()}
                    />
                ))
            ),
        [maxTableSize, onConfirm, styles, tableSize]
    );

    return (
        <div className={mergeStyles(styles.root, className)} {...props}>
            <div className={styles.header} role='alert' aria-live='polite'>
                {title}
            </div>
            <FocusZone className={styles.table} style={gridStyle} role='grid' shouldFocusOnMount>
                {gridCells}
            </FocusZone>
        </div>
    );
};

function getTableSize(row: number, col: number): RichTextFieldTableSize {
    return { rows: row + 1, cols: col + 1 };
}
