import { DatePicker, IDatePicker, IDatePickerProps } from '@fluentui/react';
import React from 'react';
import { DateTimeEntry, FormContext, FormEntry, FormMode, FormQuestionType } from '../../types';
import { useDatePickerStyles } from '../../utilities';
import { BaseDescriptor, FieldComponentTypeFactory } from './BaseDescriptor';

const defaultOptions = {
    defaultPlaceholder: 'Select a date',
};

/**
 * The options for the date time descriptor to customize the date time field.
 * `strings` and `formatDate` are supported for localization purposes, and `allowTextInput` indicates whether to allow
 * input text directly.
 */
export type DateTimeDescriptorOptions = Partial<typeof defaultOptions> &
    Pick<IDatePickerProps, 'strings' | 'formatDate' | 'allowTextInput'>;

const makeFieldComponent: FieldComponentTypeFactory<DateTimeDescriptorOptions & typeof defaultOptions, DateTimeEntry> =
    ({ defaultPlaceholder, ...options }) =>
    ({ entry, onEntryChange, showPlaceholder, delegatedProps }) => {
        const { question, answer } = entry;

        const value = answer.content;
        const onChange = React.useCallback(
            (value?: Date | null) => {
                const newAnswer = { ...entry.answer };
                newAnswer.content = value ?? undefined;
                if (!newAnswer.content) delete newAnswer.content;

                onEntryChange?.({ ...entry, answer: newAnswer });
            },
            [entry, onEntryChange]
        );
        const placeholder = showPlaceholder ? question.placeholder || defaultPlaceholder : undefined;
        const datePickerRef = React.useRef<IDatePicker>(null);

        // Workaround for read only (previewing mode), since the DatePicker is uncontrollable component and does not
        // support read only. When read only and no value, the picker will be reset after selected any date.
        const hasValue = value !== undefined;
        const calendarProps = React.useMemo(() => {
            if (!delegatedProps.readOnly || hasValue) return;
            return {
                onSelectDate: () => {
                    const reset = datePickerRef.current?.reset;
                    if (reset) requestAnimationFrame(reset);
                },
            };
        }, [delegatedProps.readOnly, hasValue]);

        const datePickerStyles = useDatePickerStyles();

        return (
            <DatePicker
                {...delegatedProps}
                componentRef={datePickerRef}
                value={value}
                onSelectDate={onChange}
                placeholder={placeholder}
                showGoToToday={false}
                calendarProps={calendarProps}
                textField={delegatedProps}
                styles={datePickerStyles}
                {...options}
            />
        );
    };

/**
 * The descriptor that renders a date time field for date time entries.
 */
export class DateTimeDescriptor extends BaseDescriptor<DateTimeEntry> {
    protected readonly FieldComponent;

    constructor(options?: DateTimeDescriptorOptions) {
        super();
        this.FieldComponent = makeFieldComponent({ ...defaultOptions, ...options });
    }

    /**
     * Tests whether the entry is a date time entry, and the form is not in viewing mode.
     *
     * @param entry The entry to test.
     * @param context The context.
     * @returns Passes the test or not.
     */
    test(entry: FormEntry, context: FormContext): entry is DateTimeEntry {
        return entry.question.type === FormQuestionType.DateTime && context.mode !== FormMode.Viewing;
    }
}
