import { BaseFormAnswer, BaseFormEntry, BaseFormQuestion, FormQuestionType } from './base';

export enum ChoiceAppearance {
    Radio,
    Dropdown,
    Checkbox,
}

/**
 * A choice question.
 *
 * @property choices The choices that the user can choose from.
 * @property appearance The appearance of the choices.
 * @property shuffle Whether to shuffle the choices.
 * @property allowOther Whether to allow the user to enter other value.
 */
export interface ChoiceQuestion extends BaseFormQuestion<FormQuestionType.Choice> {
    choices: string[];
    appearance: ChoiceAppearance;
    shuffle?: boolean;
    allowOther?: boolean;
}

/**
 * The content of a choice answer.
 *
 * @property chosen The chosen choices.
 * @property other The other value that the user entered. Only available if `allowOther` of the question is `true`.
 * If the user choose the other option, but did not enter any value, this property will be an empty string.
 * If the user did not choose the other option, this property will be `undefined`.
 */
export interface ChoiceAnswerContent {
    chosen: string[];
    other?: string;
}

export type ChoiceAnswer = BaseFormAnswer<ChoiceAnswerContent>;

export type ChoiceEntry = BaseFormEntry<ChoiceQuestion, ChoiceAnswer>;
