import { Extension } from '@tiptap/core';

declare module '@tiptap/core' {
    interface Commands<ReturnType> {
        backgroundColor: {
            /**
             * Set the backgroundColor
             */
            setBackgroundColor: (backgroundColor: string) => ReturnType;

            /**
             * Unset the backgroundColor
             */
            unsetBackgroundColor: () => ReturnType;
        };
    }
}

export interface BackgroundColorOptions {
    types: string[];
}

export const BackgroundColor = Extension.create<BackgroundColorOptions>({
    name: 'backgroundColor',

    addOptions() {
        return {
            types: ['textStyle'],
        };
    },

    addGlobalAttributes() {
        return [
            {
                types: this.options.types,
                attributes: {
                    backgroundColor: {
                        default: null,
                        parseHTML: element => element.style.backgroundColor?.replace(/['"]+/g, ''),
                        renderHTML: attributes => {
                            if (!attributes.backgroundColor) return {};
                            return { style: `background-color: ${attributes.backgroundColor}` };
                        },
                    },
                },
            },
        ];
    },

    addCommands() {
        return {
            setBackgroundColor:
                backgroundColor =>
                ({ chain }) =>
                    chain().setMark('textStyle', { backgroundColor }).run(),
            unsetBackgroundColor:
                () =>
                ({ chain }) =>
                    chain().setMark('textStyle', { backgroundColor: null }).removeEmptyTextStyle().run(),
        };
    },
});
