import { Extensions } from '@tiptap/core';
import React from 'react';
import { RichTextFieldStrings, RichTextFileStorageService } from '../../types';
import { RichTextFieldRibbonCallbacks } from '../RichTextRibbon';

export enum RibbonPosition {
    None,
    Top,
    Bottom,
}

export type RichTextFieldCallbacks = RichTextFieldRibbonCallbacks;

export interface RichTextFieldProps extends Omit<React.HTMLAttributes<HTMLDivElement>, 'onChange'> {
    value: string;
    onChange?: (value: string) => void;
    errorMessage?: string;
    label?: string;
    description?: string;
    required?: boolean;
    placeholder?: string;
    prefix?: string;
    readOnly?: boolean;
    invalid?: boolean;
    compact?: boolean;
    ribbonPosition?: RibbonPosition;
    focusable?: boolean;
    callbacks?: RichTextFieldCallbacks;
    fileStorageService?: RichTextFileStorageService;
    additionalExtensions?: Extensions;
    strings?: Partial<RichTextFieldStrings>;
    rootClassName?: string;
    containerClassName?: string;
}
