import { MotionAnimations } from '@fluentui/react';
import { FontSizes, makeStyles } from '@addressbook/components-fluentui-teams-theme';

export const useRenderingFieldStyles = makeStyles(theme => ({
    headerContainer: {
        display: 'flex',
        flexDirection: 'column',
        gap: '4px',
        marginBottom: '8px',
    },
    labelContainer: {
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center',
    },
    label: {
        fontSize: FontSizes.size14,
        color: theme.palette.neutralDark,
        fontWeight: 600,
        wordBreak: 'break-word',
    },
    requiredLabel: {
        '::after': {
            content: '"*"',
            color: theme.palette.redDark,
            marginInline: '4px',
        },
    },
    description: {
        fontSize: FontSizes.size12,
        color: theme.palette.neutralSecondary,
        wordBreak: 'break-word',
    },
    errorMessage: {
        fontSize: FontSizes.size12,
        color: theme.palette.redDark,
        paddingInline: '12px',
        marginTop: '4px',
        wordBreak: 'break-word',
        animation: `${MotionAnimations.fadeIn}, ${MotionAnimations.slideDownIn}`,
    },
}));
