import { TextField } from '@fluentui/react';
import React from 'react';
import { FormContext, FormEntry, FormMode, FormQuestionType, TextEntry } from '../../types';
import { useTextFieldStyles } from '../../utilities';
import { BaseDescriptor, FieldComponentTypeFactory } from './BaseDescriptor';

const defaultOptions = {
    defaultPlaceholder: 'Enter your response',
};

export type TextDescriptorOptions = Partial<typeof defaultOptions>;

const makeFieldComponent: FieldComponentTypeFactory<TextDescriptorOptions & typeof defaultOptions, TextEntry> =
    ({ defaultPlaceholder }) =>
    ({ entry, onEntryChange, showPlaceholder, delegatedProps }) => {
        const { question, answer } = entry;
        const { multiline, number } = question;

        const value = answer.content ?? '';
        const onChange = React.useCallback(
            (_: unknown, value?: string) => {
                if (number) value = value?.trim();
                const newAnswer = { ...entry.answer };
                newAnswer.content = value;
                if (!newAnswer.content) delete newAnswer.content;

                onEntryChange?.({ ...entry, answer: newAnswer });
            },
            [entry, number, onEntryChange]
        );
        const placeholder = showPlaceholder ? question.placeholder || defaultPlaceholder : undefined;

        const textFieldStyles = useTextFieldStyles();

        return (
            <TextField
                {...delegatedProps}
                value={value}
                onChange={onChange}
                placeholder={placeholder}
                multiline={multiline}
                inputMode={number ? 'decimal' : 'text'}
                resizable={false}
                styles={textFieldStyles}
            />
        );
    };

/**
 * The descriptor that renders a text field for text entries.
 * It supports both single-line and multi-line text fields, either with or without number restrictions.
 */
export class TextDescriptor extends BaseDescriptor<TextEntry> {
    protected readonly FieldComponent;

    constructor(options?: TextDescriptorOptions) {
        super();
        this.FieldComponent = makeFieldComponent({ ...defaultOptions, ...options });
    }

    /**
     * Tests whether the entry is a text entry, and the form is not in viewing mode.
     *
     * @param entry The entry to test.
     * @param context The context.
     * @returns Passes the test or not.
     */
    test(entry: FormEntry, context: FormContext): entry is TextEntry {
        return entry.question.type === FormQuestionType.Text && context.mode !== FormMode.Viewing;
    }
}
