/* istanbul ignore file */

import { ITooltipStyleProps, ITheme, IStyleFunctionOrObject, ITooltipStyles } from '@fluentui/react';
import { isContrastTheme, isDarkTheme } from '../teams-theme';
import { teamsBorderRadius } from './constants';

export function getTooltipStyles(
    themeName: string,
    _theme: ITheme
): IStyleFunctionOrObject<ITooltipStyleProps, ITooltipStyles> {
    return (props: ITooltipStyleProps) => {
        const { theme } = props;
        const { palette } = theme;

        const styles = {
            root: {
                selectors: {
                    '.ms-Callout-beak': {
                        backgroundColor: palette.white,
                        borderBottomRightRadius: teamsBorderRadius,
                    },
                },
            },
            content: {
                backgroundColor: palette.white,
                color: palette.black,
            },
        };

        // Dark theme overrides
        if (isDarkTheme(themeName)) {
            styles.content.backgroundColor = palette.white;
            styles.content.color = palette.black;
        }

        // Contrast theme overrides
        if (isContrastTheme(themeName)) {
            styles.content.backgroundColor = palette.white;
            styles.content.color = palette.black;
        }
        return styles;
    };
}
