import { ITheme, ITeachingBubbleStyles } from '@fluentui/react';
import { FontSizes } from '../teams-sizes';

export function getTeachingBubbleStyles(themeName: string, theme: ITheme): Partial<ITeachingBubbleStyles> {
    return {
        subComponentStyles: {
            callout: {
                root: {
                    maxWidth: '280px',
                    boxShadow: '0px 0px 2px rgba(0, 0, 0, 0.12), 0px 4px 8px rgba(0, 0, 0, 0.14)',
                },
                beak: {
                    background: theme.palette.themePrimary,
                    borderRadius: '3px',
                    width: '12px',
                    height: '12px',
                },
                calloutMain: {
                    background: theme.palette.themePrimary,
                },
            },
        },
        content: {
            backgroundColor: theme.palette.themePrimary,
        },
        headline: {
            fontSize: FontSizes.size18,
            fontWeight: '700',
            lineHeight: FontSizes.size24,
        },
        subText: {
            fontSize: FontSizes.size12,
        },
        closeButton: {
            margin: '14px 18px 0px 0px',
            padding: '0px 0px',
            fontSize: '20px',
            height: '20px',
            width: '20px',
            ' i': {
                fontSize: '19px',
                margin: '0px 0px',
                height: '20px',
                lineHeight: '20px',
            },
        },
        body: {
            ':not(:last-child)': { marginBottom: '0px' },
            lineHeight: FontSizes.size16,
        },
        header: {
            ':not(:last-child)': { marginBottom: '12px' },
        },
        bodyContent: {
            padding: '12px 16px',
            lineHeight: FontSizes.size16,
            fontSize: FontSizes.size12,
            fontWeight: '400',
            whiteSpace: 'pre-wrap',
            background: theme.palette.themePrimary,
        },
        secondaryButton: {
            borderColor: theme.palette.themeTertiary,
            '.ms-Button-label': {
                fontWeight: '500',
            },
        },
        footer: {
            marginTop: '12px',
            flexDirection: 'column',
            alignItems: 'stretch',
            '.ms-StackItem': {
                width: '100%',
            },
            '.ms-Button:not(:first-child)': {
                margin: '0px',
            },
        },
    };
}
