/* istanbul ignore file */

import { IDocumentCardStyles, ITheme } from '@fluentui/react';
import { ContrastColors } from '../teams-colors';
import { ShadowLevels } from '../teams-shadow-levels';
import { FontSizes } from '../teams-sizes';
import { isContrastTheme, isDarkTheme } from '../teams-theme';
import { teamsBorderRadius } from './constants';

export function getDocumentCardStyles(themeName: string, theme: ITheme): Partial<IDocumentCardStyles> {
    let BOX_SHADOW = ShadowLevels.shadowLevel1;
    let BORDER_COLOR = theme.palette.neutralQuaternary;

    if (isDarkTheme(themeName)) {
        BOX_SHADOW = ShadowLevels.shadowLevel1Dark;
    }
    if (isContrastTheme(themeName)) {
        BORDER_COLOR = ContrastColors.appBlack;
    }
    const styles: Partial<IDocumentCardStyles> = {
        root: {
            borderRadius: teamsBorderRadius,
            backgroundColor: theme.palette.neutralLighterAlt,
            borderColor: BORDER_COLOR,
            borderStyle: 'solid',
            borderWidth: '1px',
            boxShadow: BOX_SHADOW,
            selectors: {
                ':hover': {
                    backgroundColor: theme.palette.neutralQuaternary,
                },
                ':hover::after': {
                    display: 'none',
                },
                ':focus': {
                    borderColor: theme.palette.black,
                },
                '.ms-DocumentCardTitle': {
                    color: theme.palette.black,
                    fontSize: FontSizes.size14,
                    height: FontSizes.size20,
                    lineHeight: FontSizes.size20,
                },
            },
        },
    };
    return styles;
}
