/* istanbul ignore file */

import {
    IPersonaCoinStyleProps,
    IPersonaCoinStyles,
    IPersonaStyleProps,
    IPersonaStyles,
    IStyleFunctionOrObject,
    ITheme,
} from '@fluentui/react';
import { FontSizes } from '../teams-sizes';
export function getPersonaStyles(
    _themeName: string,
    theme: ITheme
): IStyleFunctionOrObject<IPersonaStyleProps, IPersonaStyles> {
    return (_props: IPersonaStyleProps) => {
        return {
            root: {
                height: 'auto',
            },
            details: {
                width: 'auto',
                flex: '1 1 0',
            },
            primaryText: {
                height: 'auto',
                lineHeight: 'auto',
                fontSize: FontSizes.size14,
                color: theme.palette.black,
            },
            secondaryText: {
                height: 'auto',
                lineHeight: 'auto',
                fontSize: FontSizes.size14,
                color: theme.palette.neutralDark,
            },
        };
    };
}

export function getPersonaCoinStyles(
    _themeName: string,
    _theme: ITheme
): IStyleFunctionOrObject<IPersonaCoinStyleProps, IPersonaCoinStyles> {
    return (_props: IPersonaCoinStyleProps) => {
        return {
            initials: {
                alignItems: 'center',
                display: 'flex',
                justifyContent: 'center',
            },
        };
    };
}
