import React from 'react';
import { FormFragment } from './FormFragment';
import { BaseFormContext, BaseFormFeature, FormProps } from './types';

/**
 * A form is responsible for rendering the form entries with the given context and feature.
 * It uses the comparator to determine the order of the entries and the key extractor to extract a unique key from each
 * entry, and find the appropriate descriptor to render the entries.
 */
export function Form<
    Entry,
    Context extends BaseFormContext<Entry>,
    Feature extends BaseFormFeature<Entry, Context, Feature>
>(props: FormProps<Context, Feature>): JSX.Element | null {
    const { context, feature } = props;
    const { entries } = context;
    const { comparator, keyExtractor, trailingRender } = feature;

    const sortedIndices = React.useMemo(
        () => Array.from(entries.keys()).sort((i, j) => comparator(entries[i], entries[j])),
        [comparator, entries]
    );

    return (
        <>
            {sortedIndices.map(index => (
                <FormFragment<Entry, Context, Feature> key={keyExtractor(entries[index])} {...props} index={index} />
            ))}
            {trailingRender(props)}
        </>
    );
}
