import { isContrastTheme, makeStyles } from '@addressbook/components-fluentui-teams-theme';

export const useColorMenuItemStyles = makeStyles(theme => ({
    root: {
        display: 'block',
        width: '16px',
        height: '16px',
        padding: 0,

        border: 'none',
        borderRadius: '4px',

        cursor: 'pointer',
    },
    color: {
        boxShadow: 'rgba(0, 0, 0, 0.1) 0px 0px 2px, rgba(0, 0, 0, 0.15) 0px 2px 4px',
        ':hover': {
            borderStyle: 'solid',
            borderWidth: '1px',
            borderColor: theme.palette.neutralDark,
        },
    },
    text: {
        height: '24px',
        width: '100%',
        '.ms-ContextualMenu-item:has(&)': {
            flex: '100%',
        },

        color: isContrastTheme(theme.name) ? theme.palette.black : theme.palette.neutralPrimary,
        backgroundColor: theme.palette.white,

        borderStyle: 'solid',
        borderWidth: '1px',
        borderColor: isContrastTheme(theme.name) ? theme.palette.black : theme.palette.neutralTertiary,

        ':hover': {
            color: isContrastTheme(theme.name) ? theme.palette.white : undefined,
            backgroundColor: isContrastTheme(theme.name) ? theme.palette.accent : theme.palette.neutralLighterAlt,
        },
    },
}));
