import { JSONContent, mergeAttributes, Node } from '@tiptap/core';
import { Fragment } from '@tiptap/pm/model';
import { Plugin } from '@tiptap/pm/state';
import { ReactNodeViewRenderer } from '@tiptap/react';
import {
    createFileByBlob,
    dataTransferIncludesFiles,
    decodeDataUrl,
    extractFilesFromDataTransfer,
    getExtensionByMime,
    getMimeByDataUrl,
    isImageMime,
    joinFileName,
    normalizeFileName,
    splitFileName,
    uuid,
} from '@addressbook/utilities-core';
import { defaultRichTextFieldStrings, defaultRichTextFileStorageService } from '../../utils';
import { NodeRenderHTMLProps } from '../types';
import { FileSessions } from './sessions';
import { FileOptions } from './types';
import { FileNodeView } from './view';

declare module '@tiptap/core' {
    interface Commands<ReturnType> {
        file: {
            /**
             * Insert a file.
             *
             * @param file - The file to insert.
             */
            insertFile: (file: File) => ReturnType;

            /**
             * Insert files.
             *
             * @param files - The files to insert.
             */
            insertFiles: (files: File[]) => ReturnType;
        };
    }
}

/**
 * The file extension.
 */
export const File = Node.create<FileOptions, FileSessions>({
    name: 'file',
    content: 'text?',
    group: 'inline',
    inline: true,
    atom: true,
    selectable: true,
    draggable: true,
    defining: true,

    addOptions() {
        return {
            fileStorageService: defaultRichTextFileStorageService,
            strings: defaultRichTextFieldStrings,
            defaultMime: 'application/octet-stream',
            HTMLAttributes: {
                target: '_blank',
                rel: 'noopener noreferrer',
            },
        };
    },

    addStorage() {
        return new FileSessions(this.options.fileStorageService);
    },

    addAttributes() {
        return {
            href: {
                default: null,
            },
            target: {
                default: this.options.HTMLAttributes.target,
            },
            rel: {
                default: this.options.HTMLAttributes.rel,
            },
            mime: {
                default: this.options.defaultMime,
                parseHTML: element => element.getAttribute(`data-${this.name}-mime`) || null,
                renderHTML: attributes => (attributes.mime ? { [`data-${this.name}-mime`]: attributes.mime } : {}),
            },
            error: {
                default: null,
                parseHTML: element => (element.getAttribute(`data-${this.name}-error`) ? true : null),
                renderHTML: attributes => (attributes.error ? { [`data-${this.name}-error`]: attributes.error } : {}),
            },
            key: {
                rendered: false,
            },
        };
    },

    parseHTML() {
        const getFileNameByImage = (image: HTMLImageElement): string => {
            const name = image.alt || image.title || this.options.strings.defaultImageFileStem;
            const mime = getMimeByDataUrl(image.src);
            const extension = mime && getExtensionByMime(mime);

            const normalizedName = normalizeFileName(name);
            const nameParts = splitFileName(normalizedName);
            const ensuredNameParts =
                !extension || nameParts.extension === extension
                    ? nameParts
                    : { stem: joinFileName(nameParts), extension };

            return joinFileName(ensuredNameParts);
        };

        return [
            {
                tag: `a[data-type="${this.name}"]`,
                priority: 100,
                getAttrs: element => {
                    const anchor = element as HTMLAnchorElement;

                    // anchor.href is not suitable here because it is resolved to an absolute URL, even if the href is
                    // empty or a fragment. We need the original href value.
                    const url = anchor.getAttribute('href');
                    if (!url) return false;

                    const mime = anchor.getAttribute(`data-${this.name}-mime`);
                    if (!mime) return false;

                    const key = uuid();
                    if (isImageMime(mime)) {
                        this.storage.download(key, url);
                    } else {
                        this.storage.thumbnail(key, url);
                    }
                    return { key };
                },
            },
            {
                tag: 'img[src^="data:"]',
                priority: 100,
                getAttrs: element => {
                    const image = element as HTMLImageElement;
                    const blob = decodeDataUrl(image.src);
                    if (!blob) return false;

                    const mime = getMimeByDataUrl(image.src);

                    const key = uuid();
                    this.storage.upload(key, createFileByBlob(blob, getFileNameByImage(image)));
                    return { mime, key };
                },
                getContent: (node, schema) => Fragment.from(schema.text(getFileNameByImage(node as HTMLImageElement))),
            },
        ];
    },

    renderHTML({ HTMLAttributes }: NodeRenderHTMLProps) {
        return ['a', mergeAttributes({ 'data-type': this.name }, this.options.HTMLAttributes, HTMLAttributes), 0];
    },

    addNodeView() {
        return ReactNodeViewRenderer(FileNodeView);
    },

    addCommands() {
        return {
            insertFile:
                file =>
                ({ commands }) =>
                    commands.insertFiles([file]),
            insertFiles:
                files =>
                ({ commands, editor }) => {
                    // Map files to JSON content
                    const content: JSONContent[] = files.map(file => {
                        const mime = file.type || this.options.defaultMime;

                        const key = uuid();
                        this.storage.upload(key, file);
                        return {
                            type: this.name,
                            attrs: { mime, key },
                            content: [{ type: 'text', text: file.name }],
                        };
                    });

                    // Wrap content in a paragraph if grouping with other file nodes
                    const { $from, $to } = editor.state.selection;
                    const grouping =
                        ($from.nodeBefore && $from.nodeBefore.type.name === this.name) ||
                        ($to.nodeAfter && $to.nodeAfter.type.name === this.name);
                    const wrappedContent = grouping ? content : { type: 'paragraph', content };

                    return commands.insertContent(wrappedContent);
                },
        };
    },

    addProseMirrorPlugins() {
        const consumeDataTransfer = (data: DataTransfer | null): boolean | void => {
            if (!data || !dataTransferIncludesFiles(data) || data.types.length > 1) return;
            const files = extractFilesFromDataTransfer(data);
            return files.length > 0 && this.editor.chain().insertFiles(files).focus().run();
        };

        return [
            new Plugin({
                props: {
                    handleDrop: (_, event) => consumeDataTransfer(event.dataTransfer),
                    handlePaste: (_, event) => consumeDataTransfer(event.clipboardData),
                },
            }),
        ];
    },
});
