/* istanbul ignore file */

import { ICalloutContentStyleProps, ICalloutContentStyles, ITheme, IStyleFunctionOrObject } from '@fluentui/react';
import { ShadowLevels } from '../teams-shadow-levels';
import { isContrastTheme, isDarkTheme } from '../teams-theme';
import { teamsBorderRadius } from './constants';

export function getCalloutContentStyles(
    themeName: string,
    _theme: ITheme
): IStyleFunctionOrObject<ICalloutContentStyleProps, ICalloutContentStyles> {
    return (props: ICalloutContentStyleProps) => {
        const { theme } = props;
        const { palette } = theme;

        const TEXT_COLOR = palette.black;

        const styles = {
            root: {
                borderRadius: teamsBorderRadius,
                boxShadow: ShadowLevels.shadowLevel3,
                background: palette.white,
                borderWidth: '1px',
                borderStyle: 'solid',
                borderColor: palette.neutralTertiaryAlt,
                color: TEXT_COLOR,
                selectors: {
                    '.ms-Spinner': {
                        padding: '32px',
                    },
                    '.ms-CalloutExample-title': {
                        color: 'inherit',
                    },
                    '.ms-CalloutExample-subText': {
                        color: 'inherit',
                    },
                    "[class*='menuItemIconContainer']": {
                        marginLeft: '0',
                        marginRight: '12px',
                    },
                },
            },
            beakCurtain: {
                borderRadius: teamsBorderRadius,
            },
            beak: {
                background: palette.white,
            },
            callout: {
                borderRadius: teamsBorderRadius,
            },
            calloutMain: {
                borderRadius: teamsBorderRadius,
                background: palette.white,
            },
        };

        // Dark theme overrides
        if (isDarkTheme(themeName)) {
            styles.root.boxShadow = ShadowLevels.shadowLevel3Dark;
            styles.root.borderColor = palette.white;
            styles.root.background = palette.neutralLight;
            styles.beak.background = palette.neutralLight;
            styles.calloutMain.background = palette.neutralLight;
        }

        // Contrast theme overrides
        if (isContrastTheme(themeName)) {
            styles.root.boxShadow = 'none';
            styles.root.borderColor = palette.black;
            styles.root.background = palette.white;
        }
        return styles;
    };
}
