/* istanbul ignore file */

import { ILabelStyleProps, ILabelStyles, FontWeights } from '@fluentui/react';

export const LabelStyles = (props: ILabelStyleProps): Partial<ILabelStyles> => {
    const { theme, disabled } = props;
    const { palette } = theme;

    return {
        root: [
            {
                fontWeight: FontWeights.semibold,
                color: palette.black,
                ':after': { color: theme.palette.redDark },
            },
            disabled && {
                opacity: '.65',
            },
        ],
    };
};
