import { mergeStyles } from '@fluentui/react';
import React from 'react';
import { FieldProps } from '../../../abstractions';
import { FormContext, FormEntry, FormFeature } from '../../types';
import { useActive } from '../../utilities';
import { PreviewingDecorator } from '../PreviewingDecorator';
import { ActiveDesigningDecorator } from './DesigningDecorator.active';
import { useDesigningDecoratorStyles } from './DesigningDecorator.styles';

export function DesigningDecorator<CoercedEntry extends FormEntry>(
    props: React.PropsWithChildren<FieldProps<FormEntry, FormContext, FormFeature, CoercedEntry>>
): JSX.Element {
    const { entry, onEntryChange, feature } = props;
    const { strings } = feature;

    const active = entry.active ?? false;
    const setActive = React.useCallback(
        (value: boolean) => {
            const newEntry = { ...entry };
            newEntry.active = value;
            if (!newEntry.active) delete newEntry.active;
            onEntryChange?.(newEntry);
        },
        [entry, onEntryChange]
    );

    const hint = React.useMemo(() => strings.questionHint(entry.question, active), [active, entry.question, strings]);

    const ref = useActive<HTMLDivElement>({ active, setActive });

    const Component = active ? ActiveDesigningDecorator : PreviewingDecorator;
    const styles = useDesigningDecoratorStyles();
    return (
        <div ref={ref} className={mergeStyles(styles.root, !active && styles.inactive)} tabIndex={0} aria-label={hint}>
            <Component {...props} />
        </div>
    );
}
