import { IDropdownOption } from '@fluentui/react';
import {
    BaseFormQuestion,
    ChoiceAppearance,
    FormConstants,
    FormEntry,
    FormFeature,
    FormQuestion,
    FormQuestionType,
    FormStrings,
    QuestionDisplayType,
    TextNumberRestriction,
    TextNumberRestrictionType,
} from '../types';

/**
 * Gets the display type options for a form question.
 *
 * @param strings The strings to use for the options.
 * @returns The display type options.
 */
export function getDisplayTypeOptions(strings: FormStrings): IDropdownOption[] {
    return [
        {
            key: QuestionDisplayType.ShortText,
            text: strings.questionDisplayTypeOption(QuestionDisplayType.ShortText),
            data: 'TextField',
        },
        {
            key: QuestionDisplayType.MultilineText,
            text: strings.questionDisplayTypeOption(QuestionDisplayType.MultilineText),
            data: 'Textbox',
            hidden: true,
            disabled: true,
        },
        {
            key: QuestionDisplayType.RichText,
            text: strings.questionDisplayTypeOption(QuestionDisplayType.RichText),
            data: 'TextParagraph',
        },
        {
            key: QuestionDisplayType.Number,
            text: strings.questionDisplayTypeOption(QuestionDisplayType.Number),
            data: 'NumberSymbol',
        },
        {
            key: QuestionDisplayType.MultipleChoice,
            text: strings.questionDisplayTypeOption(QuestionDisplayType.MultipleChoice),
            data: 'CheckboxChecked',
        },
        {
            key: QuestionDisplayType.SingleChoice,
            text: strings.questionDisplayTypeOption(QuestionDisplayType.SingleChoice),
            data: 'RadioButton',
        },
        {
            key: QuestionDisplayType.Dropdown,
            text: strings.questionDisplayTypeOption(QuestionDisplayType.Dropdown),
            data: 'ChevronCircleDown',
        },
        {
            key: QuestionDisplayType.Date,
            text: strings.questionDisplayTypeOption(QuestionDisplayType.Date),
            data: 'Calendar',
        },
    ];
}

/**
 * Gets the display type for a form question.
 *
 * @param question The form question.
 * @returns The display type.
 */
export function getDisplayType(question: FormQuestion): QuestionDisplayType {
    switch (question.type) {
        case FormQuestionType.Text:
            return question.number
                ? QuestionDisplayType.Number
                : question.multiline
                ? QuestionDisplayType.MultilineText
                : QuestionDisplayType.ShortText;
        case FormQuestionType.RichText:
            return QuestionDisplayType.RichText;
        case FormQuestionType.Choice:
            switch (question.appearance) {
                case ChoiceAppearance.Radio:
                    return QuestionDisplayType.SingleChoice;
                case ChoiceAppearance.Dropdown:
                    return QuestionDisplayType.Dropdown;
                case ChoiceAppearance.Checkbox:
                    return QuestionDisplayType.MultipleChoice;
            }
        case FormQuestionType.DateTime:
            return QuestionDisplayType.Date;
    }
}

export type NewFormEntryProps = Pick<FormQuestion, 'order'> & Pick<FormFeature, 'idGenerator' | 'strings'>;

/**
 * Add a new form entry to existing form entries and return the new form entries.
 * This function will deactivate all existing form entries and activate the new form entry.
 *
 * @param entries The form entries to add the new form entry to.
 * @param props.order The order of the new form entry.
 * @param props.idGenerator The id generator to use for the new form entry.
 * @param props.strings The strings to use for the new form entry.
 * @returns The new form entries with the new form entry added.
 */
export function addNewFormEntry(entries: FormEntry[], { order, idGenerator, strings }: NewFormEntryProps): FormEntry[] {
    const newEntries = entries.map(entry => {
        if (!entry.active) return entry;
        const newEntry = { ...entry };
        delete newEntry.active;
        return newEntry;
    });

    newEntries.push({
        question: {
            type: FormQuestionType.Text,
            id: idGenerator(),
            order,
            title: strings.questionNewTitle,
        },
        answer: {},
        active: true,
    });

    return newEntries;
}

function deriveBaseFormQuestion(question: FormQuestion): BaseFormQuestion<FormQuestionType> {
    const base: BaseFormQuestion<FormQuestionType> = {
        type: question.type,
        id: question.id,
        familyId: question.familyId,
        eTag: question.eTag,
        order: question.order,
        title: question.title,
        rtTitle: question.rtTitle,
        subtitle: question.subtitle,
        rtSubtitle: question.rtSubtitle,
        required: question.required,
        tooltip: question.tooltip,
        placeholder: question.placeholder,
    };

    Object.keys(base).forEach(key => {
        if (base[key] === undefined) delete base[key];
    });

    return base;
}

function getFormQuestionTypeFromDisplayType(displayType: QuestionDisplayType): FormQuestionType {
    switch (displayType) {
        case QuestionDisplayType.ShortText:
        case QuestionDisplayType.MultilineText:
        case QuestionDisplayType.Number:
            return FormQuestionType.Text;
        case QuestionDisplayType.RichText:
            return FormQuestionType.RichText;
        case QuestionDisplayType.MultipleChoice:
        case QuestionDisplayType.SingleChoice:
        case QuestionDisplayType.Dropdown:
            return FormQuestionType.Choice;
        case QuestionDisplayType.Date:
            return FormQuestionType.DateTime;
    }
}

/**
 * Gets a new choice that is not already in the list of choices.
 *
 * @param strings The strings to use for the new choice.
 * @param choices The list of choices to check against.
 * @returns A new choice.
 */
export function getNewChoice(strings: FormStrings, choices: string[]): string {
    let index = 0;
    while (true) {
        const newChoice = strings.choiceNew(index);
        if (!choices.includes(newChoice)) return newChoice;
        index++;
    }
}

/**
 * Derives a new form question by a display type from an existing question.
 * If the display type is the same as the question's display type, the question is returned.
 * Otherwise, a new question is returned with the any common properties copied from the original question.
 *
 * @param displayType The display type to derive by.
 * @param question The question to derive from.
 * @param strings The strings to use for the new question.
 * @param constants The constants to use for the new question.
 * @returns A new form question.
 */
export function deriveFormQuestion(
    displayType: QuestionDisplayType,
    question: FormQuestion,
    strings: FormStrings,
    constants: FormConstants
): FormQuestion {
    if (displayType === getDisplayType(question)) return question;

    const base = deriveBaseFormQuestion(question);
    base.type = getFormQuestionTypeFromDisplayType(displayType);
    switch (base.type) {
        case FormQuestionType.Text:
            return {
                ...(base as BaseFormQuestion<FormQuestionType.Text>),
                ...(displayType === QuestionDisplayType.Number && { number: true }),
                ...(displayType === QuestionDisplayType.MultilineText && { multiline: true }),
            };
        case FormQuestionType.RichText:
            return base as BaseFormQuestion<FormQuestionType.RichText>;
        case FormQuestionType.Choice:
            return {
                ...(base as BaseFormQuestion<FormQuestionType.Choice>),
                appearance:
                    displayType === QuestionDisplayType.SingleChoice
                        ? ChoiceAppearance.Radio
                        : displayType === QuestionDisplayType.Dropdown
                        ? ChoiceAppearance.Dropdown
                        : ChoiceAppearance.Checkbox,
                choices:
                    question.type === FormQuestionType.Choice
                        ? question.choices
                        : Array.from({ length: constants.initialChoices }, (_, i) => strings.choiceNew(i)),
                ...(question.type === FormQuestionType.Choice && question.allowOther && { allowOther: true }),
                ...(question.type === FormQuestionType.Choice && question.shuffle && { shuffle: true }),
            };
        case FormQuestionType.DateTime:
            return base as BaseFormQuestion<FormQuestionType.DateTime>;
    }
}

/**
 * Gets the options for the text number restriction dropdown.
 *
 * @param strings The strings to use for the options.
 * @returns The options for the text number restriction dropdown.
 */
export function getTextNumberRestrictionOptions(strings: FormStrings): IDropdownOption[] {
    return [
        {
            key: TextNumberRestrictionType.GreaterThan,
            text: strings.restrictionOption(TextNumberRestrictionType.GreaterThan),
        },
        {
            key: TextNumberRestrictionType.GreaterThanOrEqualTo,
            text: strings.restrictionOption(TextNumberRestrictionType.GreaterThanOrEqualTo),
        },
        {
            key: TextNumberRestrictionType.LessThan,
            text: strings.restrictionOption(TextNumberRestrictionType.LessThan),
        },
        {
            key: TextNumberRestrictionType.LessThanOrEqualTo,
            text: strings.restrictionOption(TextNumberRestrictionType.LessThanOrEqualTo),
        },
        {
            key: TextNumberRestrictionType.EqualTo,
            text: strings.restrictionOption(TextNumberRestrictionType.EqualTo),
        },
        {
            key: TextNumberRestrictionType.NotEqualTo,
            text: strings.restrictionOption(TextNumberRestrictionType.NotEqualTo),
        },
        {
            key: TextNumberRestrictionType.Between,
            text: strings.restrictionOption(TextNumberRestrictionType.Between),
        },
        {
            key: TextNumberRestrictionType.NotBetween,
            text: strings.restrictionOption(TextNumberRestrictionType.NotBetween),
        },
    ];
}

/**
 * Derives a text number restriction from a type and an optional restriction.
 * If the restriction is provided, a new restriction is returned with any compatible properties copied from the original
 * restriction. Otherwise, a new restriction is returned with default values.
 *
 * @param type The type to derive by.
 * @param restriction The restriction to derive from.
 * @returns A new text number restriction.
 */
// Justification: This function contains a switch statement and many fallbacks.
// eslint-disable-next-line complexity
export function deriveTextNumberRestriction(
    type: TextNumberRestrictionType,
    restriction?: TextNumberRestriction
): TextNumberRestriction {
    const value = restriction && 'value' in restriction ? restriction.value : undefined;
    const min = restriction && 'min' in restriction ? restriction.min : undefined;
    const max = restriction && 'max' in restriction ? restriction.max : undefined;

    switch (type) {
        case TextNumberRestrictionType.GreaterThan:
        case TextNumberRestrictionType.GreaterThanOrEqualTo:
            return { type, min: min ?? max ?? value ?? 0 };
        case TextNumberRestrictionType.LessThan:
        case TextNumberRestrictionType.LessThanOrEqualTo:
            return { type, max: max ?? min ?? value ?? 100 };
        case TextNumberRestrictionType.EqualTo:
        case TextNumberRestrictionType.NotEqualTo:
            return { type, value: value ?? min ?? max ?? 0 };
        case TextNumberRestrictionType.Between:
        case TextNumberRestrictionType.NotBetween:
            return { type, min: min ?? 0, max: max ?? 100 };
    }
}
