import { BaseFormContext } from '../../abstractions';
import { FormEntry } from './entries';

export enum FormMode {
    Previewing,
    Designing,
    Filling,
    Viewing,
}

/**
 * The context of a form, which is mutable during the lifecycle of the form.
 *
 * @property mode The mode of the form.
 */
export interface FormContext extends BaseFormContext<FormEntry> {
    mode: FormMode;
}
