import { FontSizes, isContrastTheme, makeStyles } from '@addressbook/components-fluentui-teams-theme';

export const useRichTextFieldStyles = makeStyles(theme => ({
    editorContainer: {
        display: 'flex',
        flexDirection: 'column',
        backgroundColor: isContrastTheme(theme.name) ? theme.palette.white : theme.palette.neutralLighter,
        borderRadius: '4px',
        borderWidth: '1px',
        borderStyle: 'solid',
        borderColor: isContrastTheme(theme.name) ? theme.palette.black : 'transparent',
        overflow: 'hidden',
    },
    focusable: {
        borderBottomWidth: isContrastTheme(theme.name) ? undefined : '2px',
        '&:focus-within': {
            borderBottomWidth: '2px',
            borderBottomColor: isContrastTheme(theme.name) ? theme.palette.accent : theme.palette.themePrimary,
        },
    },
    error: {
        borderColor: theme.palette.redDark,
    },
    description: {
        fontSize: FontSizes.size12,
        marginBottom: '8px',
    },
    editor: {
        flexGrow: 1,
        boxSizing: 'border-box',
        overflow: 'auto',
        overflowWrap: 'break-word',
        padding: '6px 12px',
        cursor: 'text',

        display: 'flex',
        flexDirection: 'column',
        '.tiptap': {
            // Layout and style the rich text field input area
            flexGrow: 1,
            boxShadow: 'none',
            outline: 'none',

            // Style the rich text field content
            color: theme.palette.black,
            a: {
                color: theme.palette.themePrimary,
            },
            'p, h1, h2, h3, h4, h5, h6, pre, blockquote': {
                marginBlock: 0,
                padding: 0,
            },
            '[data-type=mention]': {
                color: theme.palette.accent,
            },

            table: {
                width: '100%',
                tableLayout: 'fixed',
                borderCollapse: 'collapse',

                td: {
                    border: `1px solid ${theme.palette.neutralSecondary}`,

                    // Mitigate nested tables overflowing the parent cell
                    overflow: 'auto',
                },

                th: {
                    textAlign: 'inherit',
                    backgroundColor: theme.palette.neutralLighter,
                    border: `1px solid ${theme.palette.neutralSecondary}`,
                },

                '.selectedCell': {
                    position: 'relative',
                    ':after': {
                        opacity: 0.2,
                        backgroundColor: theme.palette.neutralSecondary,
                        content: '""',
                        inset: 0,
                        pointerEvents: 'none',
                        position: 'absolute',
                    },
                },
            },

            // Style the rich text field placeholder
            // An empty editor may contain several empty paragraphs or tables, so we need to ensure it is the only child
            'p.is-editor-empty:first-child:last-child': {
                position: 'relative',
                '::before': {
                    content: 'attr(data-placeholder)',
                    color: isContrastTheme(theme.name) ? theme.palette.black : theme.palette.neutralPrimary,
                    position: 'absolute',
                    inset: 0,
                    overflow: 'hidden',
                    textOverflow: 'ellipsis',
                    whiteSpace: 'nowrap',
                },
            },
        },

        '.tiptap[data-prefix]': {
            '::before': {
                content: 'attr(data-prefix) " "',
                color: isContrastTheme(theme.name) ? theme.palette.black : theme.palette.neutralPrimary,
            },
            '>div:first-child, >p:first-child': {
                display: 'inline',
            },
        },
    },
    errorMessage: {
        color: theme.palette.redDark,
        padding: '4px 12px',
        fontSize: FontSizes.size12,
    },
}));
