import { DirectionalHint, Icon, mergeStyles, TooltipHost } from '@fluentui/react';
import { useId } from '@fluentui/react-hooks';
import React from 'react';
import { FieldProps } from '../../../abstractions';
import { FormContext, FormEntry, FormFeature, FormMode } from '../../types';
import { mergeValidations } from '../../utilities';
import { DesigningDecorator } from '../DesigningDecorator';
import { PreviewingDecorator } from '../PreviewingDecorator';
import { useRenderingFieldStyles } from './RenderingField.styles';

type DelegatedAriaAttributes = Pick<
    React.AriaAttributes,
    'aria-labelledby' | 'aria-describedby' | 'aria-required' | 'aria-readonly' | 'aria-errormessage' | 'aria-invalid'
>;

interface DelegatedFieldComponentProps extends DelegatedAriaAttributes {
    readOnly?: boolean;
    invalid?: boolean;
}

interface FieldComponentProps<CoercedEntry extends FormEntry>
    extends FieldProps<FormEntry, FormContext, FormFeature, CoercedEntry> {
    delegatedProps: DelegatedFieldComponentProps;
    showPlaceholder?: boolean;
}

export type FieldComponentType<CoercedEntry extends FormEntry> = React.ComponentType<FieldComponentProps<CoercedEntry>>;

export interface RenderingComponentProps<CoercedEntry extends FormEntry>
    extends FieldProps<FormEntry, FormContext, FormFeature, CoercedEntry> {
    Component: FieldComponentType<CoercedEntry>;
}

/**
 * This component renders the provided field component with some delegated props, and handles validation.
 */
export function RenderingField<CoercedEntry extends FormEntry>({
    Component,
    onEntryChange,
    ...props
}: RenderingComponentProps<CoercedEntry>): JSX.Element {
    const { entry, context, feature } = props;
    const { mode } = context;
    const { validators, sanitizer } = feature;
    const { question, answer, errorMessage } = entry;
    const { title, rtTitle, subtitle, rtSubtitle, required, tooltip } = question;

    const styles = useRenderingFieldStyles();

    const labelId = useId('label');
    const descriptionId = useId('description');
    const errorMessageId = useId('errormessage');

    const safeRtTitle = React.useMemo(
        () => (rtTitle ? { __html: sanitizer(rtTitle) } : undefined),
        [rtTitle, sanitizer]
    );
    const safeRtSubtitle = React.useMemo(
        () => (rtSubtitle ? { __html: sanitizer(rtSubtitle) } : undefined),
        [rtSubtitle, sanitizer]
    );

    const readOnly = mode !== FormMode.Filling;
    const invalid = mode === FormMode.Filling && Boolean(errorMessage);

    const showPlaceholder = mode !== FormMode.Viewing;
    const delegatedProps: DelegatedFieldComponentProps = React.useMemo(
        () => ({
            ...(readOnly && { readOnly: true, 'aria-readonly': true }),
            ...((title || safeRtTitle) && { 'aria-labelledby': labelId }),
            ...(required && { 'aria-required': true }),
            ...((subtitle || safeRtSubtitle) && { 'aria-describedby': descriptionId }),
            ...(invalid && { invalid: true, 'aria-invalid': true, 'aria-errormessage': errorMessageId }),
        }),
        [
            readOnly,
            title,
            safeRtTitle,
            labelId,
            required,
            subtitle,
            safeRtSubtitle,
            descriptionId,
            invalid,
            errorMessageId,
        ]
    );

    const handleEntryChange = React.useMemo(
        () =>
            onEntryChange &&
            ((newEntry: FormEntry): void => {
                // Use the validators to validate the new entry.
                const validation = mergeValidations(validators.map(validator => validator.validate(newEntry)));

                // Set the entry's validity based on the failures.
                newEntry.invalid = !validation.valid;

                // If any validator fails and filling the form, set the error message and prevent change if necessary.
                delete newEntry.errorMessage;
                if (mode === FormMode.Filling && !validation.valid) {
                    newEntry.errorMessage = validation.errorMessage;
                    if (validation.preventChange) newEntry.answer = answer;
                }

                onEntryChange(newEntry);
            }),
        [onEntryChange, answer, validators, mode]
    );

    const field = (
        <div>
            <div className={styles.headerContainer}>
                <div className={styles.labelContainer}>
                    <label
                        id={labelId}
                        className={mergeStyles(styles.label, required && styles.requiredLabel)}
                        // Justification: The label is sanitized before being rendered.
                        // eslint-disable-next-line react/no-danger
                        dangerouslySetInnerHTML={safeRtTitle}>
                        {safeRtTitle ? undefined : title}
                    </label>
                    {tooltip && (
                        <TooltipHost
                            content={tooltip}
                            directionalHint={DirectionalHint.rightCenter}
                            directionalHintForRTL={DirectionalHint.leftCenter}>
                            <Icon tabIndex={0} aria-hidden={false} iconName='Info' />
                        </TooltipHost>
                    )}
                </div>
                {(subtitle || safeRtSubtitle) && (
                    <div
                        id={descriptionId}
                        className={styles.description}
                        // Justification: The subtitle is sanitized before being rendered.
                        // eslint-disable-next-line react/no-danger
                        dangerouslySetInnerHTML={safeRtSubtitle}>
                        {safeRtSubtitle ? undefined : subtitle}
                    </div>
                )}
            </div>
            <Component
                {...props}
                onEntryChange={handleEntryChange}
                showPlaceholder={showPlaceholder}
                delegatedProps={delegatedProps}
            />
            {invalid && (
                <div id={errorMessageId} className={styles.errorMessage} role='alert'>
                    {errorMessage}
                </div>
            )}
        </div>
    );

    const Decorator =
        mode === FormMode.Previewing
            ? PreviewingDecorator
            : mode === FormMode.Designing
            ? DesigningDecorator
            : undefined;

    return Decorator ? (
        <Decorator {...props} onEntryChange={handleEntryChange}>
            {field}
        </Decorator>
    ) : (
        field
    );
}
