/* istanbul ignore file */

import { IPanelStyles, ITheme, FontWeights, IRawStyle } from '@fluentui/react';
import { CoreColors, ContrastColors, DarkColors } from '../teams-colors';
import { isContrastTheme, isDarkTheme } from '../teams-theme';

export function getPanelStyles(themeName: string, theme: ITheme): Partial<IPanelStyles> {
    let BUTTON_ICON_HOVER_COLOR = theme.palette.themePrimary;

    if (isContrastTheme(themeName)) {
        BUTTON_ICON_HOVER_COLOR = theme.palette.themeDark;
    }
    const styles = {
        main: {
            selectors: {
                '&.ms-Panel-main': {
                    borderWidth: 0,
                    borderStyle: 'solid',
                    borderColor: theme.palette.black,
                },
            },
        },
        overlay: {
            backgroundColor: CoreColors.overlayBackground,
        },
        closeButton: {
            color: theme.palette.black,
            selectors: {
                '&:hover': {
                    color: theme.palette.themePrimary,
                    backgroundColor: 'transparent',
                    selectors: {
                        i: {
                            fontWeight: FontWeights.bold,
                        },
                    },
                },
                '&:active': {
                    color: theme.palette.themePrimary,
                    backgroundColor: 'transparent',
                    selectors: {
                        i: {
                            fontWeight: FontWeights.bold,
                        },
                    },
                },
            },
        },
        commands: {
            paddingTop: '10px',
            paddingBottom: '20px',
        },
        content: {
            color: theme.palette.black,
            selectors: {
                '.ms-Button--icon': {
                    selectors: {
                        i: {
                            color: theme.palette.black,
                        },
                        ':hover': {
                            backgroundColor: 'transparent',
                            selectors: {
                                i: {
                                    fontWeight: 'bold',
                                    color: BUTTON_ICON_HOVER_COLOR,
                                },
                            },
                        },
                        ':active': {
                            backgroundColor: 'transparent',
                        },
                    },
                },
            },
        },
        headerText: {
            color: theme.palette.black,
        },
    } as Partial<IPanelStyles>;

    // Dark theme overrides
    if (isDarkTheme(themeName)) {
        (styles.overlay as IRawStyle).backgroundColor = DarkColors.overlayBackground;
    }

    // Contrast theme overrides
    if (isContrastTheme(themeName)) {
        (styles.overlay as IRawStyle).backgroundColor = ContrastColors.overlayBackground;

        ((styles.main as Required<IRawStyle>).selectors['&.ms-Panel-main'] as IRawStyle).borderWidth = '2px';

        ((styles.closeButton as Required<IRawStyle>).selectors['&:hover'] as IRawStyle).color = theme.palette.themeDark;
        ((styles.closeButton as Required<IRawStyle>).selectors['&:active'] as IRawStyle).color =
            theme.palette.themeDark;
    }

    return styles;
}
