import { Extension } from '@tiptap/core';
import { Blockquote } from '@tiptap/extension-blockquote';
import { Bold } from '@tiptap/extension-bold';
import { BulletList } from '@tiptap/extension-bullet-list';
import { Code } from '@tiptap/extension-code';
import { CodeBlock } from '@tiptap/extension-code-block';
import { Color } from '@tiptap/extension-color';
import { Document } from '@tiptap/extension-document';
import { Dropcursor } from '@tiptap/extension-dropcursor';
import { Gapcursor } from '@tiptap/extension-gapcursor';
import { HardBreak } from '@tiptap/extension-hard-break';
import { Heading } from '@tiptap/extension-heading';
import { History } from '@tiptap/extension-history';
import { HorizontalRule } from '@tiptap/extension-horizontal-rule';
import { Italic } from '@tiptap/extension-italic';
import { ListItem } from '@tiptap/extension-list-item';
import { OrderedList } from '@tiptap/extension-ordered-list';
import { Paragraph } from '@tiptap/extension-paragraph';
import { Strike } from '@tiptap/extension-strike';
import { Table } from '@tiptap/extension-table';
import { TableCell } from '@tiptap/extension-table-cell';
import { TableHeader } from '@tiptap/extension-table-header';
import { TableRow } from '@tiptap/extension-table-row';
import { Text } from '@tiptap/extension-text';
import { TextStyle } from '@tiptap/extension-text-style';
import { Underline } from '@tiptap/extension-underline';
import { isDefined } from '@addressbook/utilities-core';
import { BackgroundColor } from './background-color';
import { FontSize } from './font-size';
import { Link } from './link';
import { Misc } from './misc';

const defaults = [
    // Extensions
    { key: 'history', extension: History },
    { key: 'dropcursor', extension: Dropcursor },
    { key: 'gapcursor', extension: Gapcursor },
    { key: 'misc', extension: Misc },

    // Nodes
    { key: 'document', extension: Document },
    { key: 'text', extension: Text },

    // Nodes.blocks
    { key: 'heading', extension: Heading },
    { key: 'paragraph', extension: Paragraph },
    { key: 'blockquote', extension: Blockquote },
    { key: 'codeBlock', extension: CodeBlock },
    { key: 'hardBreak', extension: HardBreak },
    { key: 'horizontalRule', extension: HorizontalRule },

    // Nodes.lists
    { key: 'listItem', extension: ListItem },
    { key: 'bulletList', extension: BulletList },
    { key: 'orderedList', extension: OrderedList },

    // Nodes.tables
    { key: 'table', extension: Table },
    { key: 'tableRow', extension: TableRow },
    { key: 'tableHeader', extension: TableHeader },
    { key: 'tableCell', extension: TableCell },

    // Marks.styles
    // Must be before other marks to ensure it's applied at the most top level
    // Otherwise, for example, strike won't know the current font size or color, then it can't apply the correct styles
    { key: 'textStyle', extension: TextStyle },
    { key: 'backgroundColor', extension: BackgroundColor },
    { key: 'color', extension: Color },
    { key: 'fontSize', extension: FontSize },

    // Marks.formats
    { key: 'bold', extension: Bold },
    { key: 'italic', extension: Italic },
    { key: 'underline', extension: Underline },
    { key: 'strike', extension: Strike },

    // Marks.embeds
    { key: 'link', extension: Link },
    { key: 'code', extension: Code },
] as const;

type DefaultItem = typeof defaults[number];
type DefaultItemKey = DefaultItem['key'];
type DefaultItemByKey<K extends DefaultItem['key']> = Extract<DefaultItem, { key: K }>;

type DiscardAny<T> = any extends T ? never : T;

/**
 * Options for default extensions.
 * - `false` to disable the extension.
 * - `undefined` to use the default options without configuration.
 * - Otherwise, provide the options to configure the extension.
 *
 * @default {}
 * @see {@link https://www.tiptap.dev/api/extensions/starter-kit}
 */
export type DefaultsOptions = {
    [K in DefaultItemKey]?: Partial<DiscardAny<DefaultItemByKey<K>['extension']['options']>> | false;
};

export const Defaults = Extension.create<DefaultsOptions>({
    name: 'defaults',

    addExtensions() {
        return defaults
            .map(({ key, extension }) => {
                const options = this.options?.[key];

                if (options === false) return;
                if (options === undefined) return extension;
                return extension.configure(options);
            })
            .filter(isDefined);
    },
});
