/* istanbul ignore file */

import { IButtonStyles, ITheme } from '@fluentui/react';
import { teamsBorderRadius } from './constants';

export function getActionButtonStyles(themeName: string, theme: ITheme): Partial<IButtonStyles> {
    return {
        root: {
            color: theme.palette.neutralDark,
            // Work around for fluentui button focus outline not showing in iOS Safari.
            ':focus-visible, .ms-Fabric--isFocusVisible &:focus': {
                '::after': {
                    content: '""',
                    position: 'absolute',
                    inset: '2px',
                    outline: `1px solid ${theme.palette.black}`,
                    borderRadius: teamsBorderRadius,
                },
            },
        },
    };
}
