import React from 'react';
import { InsertLink, InsertLinkProps } from './InsertLink';
import { useInsertLinkMenuStyles } from './InsertLink.styles';

export type InsertLinkMenuProps = InsertLinkProps;

export const InsertLinkMenu: React.FC<InsertLinkMenuProps> = ({
    initialLink,
    onConfirm,
    onCancel,
    normalizeUrl,
    strings,
}) => {
    const styles = useInsertLinkMenuStyles();
    return (
        <div className={styles.root}>
            <div className={styles.header} role='heading'>
                {strings.insertLinkTitle}
            </div>
            <InsertLink
                initialLink={initialLink}
                onConfirm={onConfirm}
                onCancel={onCancel}
                normalizeUrl={normalizeUrl}
                strings={strings}
            />
        </div>
    );
};
