import { Dropdown, Icon, ResponsiveMode, TextField } from '@fluentui/react';
import React from 'react';
import { QuestionDisplayType } from '../../../types';
import {
    deriveFormQuestion,
    getDisplayType,
    getDisplayTypeOptions,
    useDropdownStyles,
    useTextFieldStyles,
} from '../../../utilities';
import { QuestionSettingsProps } from '../types';
import { usePrimarySettingsStyles } from './PrimarySettings.styles';

export function PrimarySettings({
    question,
    onQuestionChange,
    constants,
    strings,
}: QuestionSettingsProps): JSX.Element {
    const styles = usePrimarySettingsStyles();
    const textFieldStyles = useTextFieldStyles();
    const dropdownStyles = useDropdownStyles();

    const selectedDisplayType = getDisplayType(question);
    const displayTypeOptions = getDisplayTypeOptions(strings);

    return (
        <>
            <div className={styles.row}>
                <TextField
                    value={question.title ?? ''}
                    onChange={(_, value) => onQuestionChange({ ...question, rtTitle: '', title: value })}
                    maxLength={constants.maxTitleLength}
                    placeholder={strings.questionTitle}
                    styles={textFieldStyles}
                />
                <Dropdown
                    selectedKey={selectedDisplayType}
                    onChange={(_, option) =>
                        option &&
                        onQuestionChange(
                            deriveFormQuestion(option.key as QuestionDisplayType, question, strings, constants)
                        )
                    }
                    options={displayTypeOptions}
                    onRenderTitle={(options, defaultRender) => (
                        <div className={styles.optionRow}>
                            <Icon
                                className={styles.optionIcon}
                                iconName={options?.find(({ key }) => key === selectedDisplayType)?.data}
                            />
                            {defaultRender?.(options)}
                        </div>
                    )}
                    onRenderOption={(option, defaultRender) => (
                        <div className={styles.optionRow}>
                            <Icon className={styles.optionIcon} iconName={option?.data} />
                            {defaultRender?.(option)}
                        </div>
                    )}
                    responsiveMode={ResponsiveMode.unknown}
                    styles={dropdownStyles}
                />
            </div>
            <TextField
                value={question.subtitle ?? ''}
                onChange={(_, value) => onQuestionChange({ ...question, rtSubtitle: '', subtitle: value })}
                maxLength={constants.maxSubtitleLength}
                placeholder={strings.questionSubtitle}
                styles={textFieldStyles}
            />
        </>
    );
}
