/* istanbul ignore file */

import { ICommandBarStyles, IRawStyle, ITheme } from '@fluentui/react';
import { FontSizes } from '../teams-sizes';
import { isContrastTheme } from '../teams-theme';

export function getCommandBarStyles(themeName: string, theme: ITheme): Partial<ICommandBarStyles> {
    const MS_BUTTON_ICON_SIZE = FontSizes.size14;
    const MS_BUTTON_MENU_ICON_SIZE = FontSizes.size12;
    const MS_COMMAND_BAR_OVERFLOW_BUTTON_SIZE = FontSizes.size16;

    const ROOT_TOP_BORDER = theme.palette.neutralTertiaryAlt;
    const ROOT_BOTTOM_BORDER = theme.palette.neutralTertiaryAlt;
    const BUTTON_COLOR = theme.palette.neutralDark;

    const COMMANDBAR_HEIGHT = '48px';
    const styles = {
        root: {
            height: COMMANDBAR_HEIGHT,
            paddingLeft: '20px',
            paddingRight: '20px',
            background: 'transparent',
            borderTop: `1px solid ${ROOT_TOP_BORDER}`,
            borderBottom: `1px solid ${ROOT_BOTTOM_BORDER}`,
            selectors: {
                '.icons-filled': {
                    display: 'none',
                },
                '.icons-unfilled': {
                    display: 'block',
                },
                svg: {
                    height: COMMANDBAR_HEIGHT,
                },
                'svg, .icons-default-fill': {
                    fill: BUTTON_COLOR,
                },
                '.ms-Button-menuIcon, .ms-Button-icon': {
                    color: BUTTON_COLOR,
                    fontSize: MS_BUTTON_ICON_SIZE,
                    minWidth: MS_BUTTON_ICON_SIZE,
                    lineHeight: COMMANDBAR_HEIGHT,
                    height: COMMANDBAR_HEIGHT,
                },
                '.ms-Button-menuIcon': {
                    fontSize: MS_BUTTON_MENU_ICON_SIZE,
                    minWidth: MS_BUTTON_MENU_ICON_SIZE,
                    lineHeight: COMMANDBAR_HEIGHT,
                    height: COMMANDBAR_HEIGHT,
                    marginLeft: '6px',
                    marginTop: '1px',
                    selectors: {
                        svg: {
                            width: '20px',
                            height: '38px',
                            margin: '0 -4px',
                        },
                    },
                },
                '.ms-Button--commandBar': {
                    border: '1px solid transparent',
                },
                '.ms-CommandBarItem-link': {
                    textDecoration: 'none',
                    border: '1px solid transparent',
                    color: BUTTON_COLOR,
                },
                'button, a': {
                    marginRight: '16px',
                    minWidth: 'auto',
                },
                button: {
                    height: '100%',
                },
                '.ms-OverflowSet-item:last-child button,  .ms-OverflowSet-item:last-child a': {
                    marginRight: '0',
                },
                'a, a:link, a:visited, a:hover, button, button:active, button:hover': {
                    backgroundColor: 'transparent',
                    color: BUTTON_COLOR,
                },
                'button:hover': {
                    color: theme.palette.themePrimary,
                    selectors: {
                        'svg, .icons-default-fill': {
                            fill: theme.palette.themePrimary,
                        },
                        '.ms-Button-menuIcon, .ms-Button-icon': {
                            fontWeight: 'bold',
                            color: theme.palette.themePrimary,
                            backgroundColor: 'transparent',
                        },
                        '.icons-filled': {
                            display: 'block',
                        },
                        '.icons-unfilled': {
                            display: 'none',
                        },
                    },
                },
                '.ms-Fabric--isFocusVisible &': {
                    selectors: {
                        '.ms-Button:focus': {
                            selectors: {
                                ':after': {
                                    borderColor: theme.palette.white,
                                    outline: `1px solid ${theme.palette.black}`,
                                },
                            },
                        },
                    },
                },
                '.is-expanded': {
                    color: theme.palette.themePrimary,
                    selectors: {
                        'svg, .icons-default-fill': {
                            fill: theme.palette.themePrimary,
                        },
                        '.ms-Button-menuIcon, .ms-Button-icon': {
                            fontWeight: 'bold',
                            color: theme.palette.themePrimary,
                            backgroundColor: 'transparent',
                        },
                        '.icons-filled': {
                            display: 'block',
                        },
                        '.icons-unfilled': {
                            display: 'none',
                        },
                    },
                },
                'a:hover': {
                    color: theme.palette.themePrimary,
                    selectors: {
                        'svg, .icons-default-fill': {
                            fill: theme.palette.themePrimary,
                        },
                        '.ms-Button-menuIcon, .ms-Button-icon': {
                            fontWeight: 'bold',
                            color: theme.palette.themePrimary,
                            backgroundColor: 'transparent',
                        },
                        '.icons-filled': {
                            display: 'block',
                        },
                        '.icons-unfilled': {
                            display: 'none',
                        },
                    },
                },
                '.ms-CommandBar-overflowButton': {
                    minWidth: 'auto',
                    selectors: {
                        '.ms-Button-menuIcon': {
                            margin: 0,
                            fontSize: MS_COMMAND_BAR_OVERFLOW_BUTTON_SIZE,
                        },
                    },
                },
            },
        },
        secondarySet: {
            selectors: {
                '.ms-Button-icon': {
                    display: 'flex',
                    width: 'auto',
                },
            },
        },
    } as Partial<ICommandBarStyles>;

    // Contrast theme overrides
    if (isContrastTheme(themeName)) {
        // Overriding text color
        (
            (styles.root as Required<IRawStyle>).selectors[
                'a, a:link, a:visited, a:hover, button, button:active, button:hover'
            ] as IRawStyle
        ).color = theme.palette.themeDark;

        // Overriding icons color
        ((styles.root as Required<IRawStyle>).selectors['.ms-Button-menuIcon, .ms-Button-icon'] as IRawStyle).color =
            theme.palette.themeDark;

        // Overriding svg fill color
        ((styles.root as Required<IRawStyle>).selectors['svg, .icons-default-fill'] as IRawStyle).fill =
            theme.palette.themeDark;
    }
    return styles;
}
