import { ContrastColors, FontSizes, isContrastTheme, makeStyles } from '@addressbook/components-fluentui-teams-theme';

export const useMockedFieldStyles = makeStyles(theme => ({
    root: {
        color: isContrastTheme(theme.name) ? theme.palette.black : theme.palette.neutralTertiary,
        boxSizing: 'border-box',
        borderWidth: '1px',
        borderStyle: 'solid',
        borderColor: isContrastTheme(theme.name) ? ContrastColors.appGreenContrast : theme.palette.neutralTertiaryAlt,
        borderRadius: '4px',

        pointerEvents: 'none',
        userSelect: 'none',
    },
    ribbon: {
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center',
        height: FontSizes.size32,
        gap: '8px',
        padding: '7px 11px 1px',
        '> *': {
            padding: '0 8px',
        },
    },
    input: {
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center',
        justifyContent: 'space-between',
        padding: '5px 12px',
        fontSize: FontSizes.size14,
        fontWeight: '400',
        lineHeight: FontSizes.size20,
    },
    multiline: {
        height: '96px',
    },
    icon: {
        fontSize: FontSizes.size16,
    },
}));
