import {
    ChoiceAction,
    FormStrings,
    QuestionAction,
    QuestionDisplayType,
    TextNumberRestrictionType,
    TextNumberRestrictionValueKey,
} from '../types';

export const defaultFormStrings: FormStrings = {
    questionHint: (question, active) =>
        active
            ? `Editing question "${question.title}". Press Tab and Shift+Tab to navigate between questions and option sections. Press arrow keys to navigate between options. Press Escape or Space to exit editing.`
            : `Question "${question.title}". Press Tab and Shift+Tab to navigate between questions. Press Enter or Space to edit.`,
    questionAction: action => defaultQuestionActionStrings[action],
    questionMockedFieldDefaultPlaceholder: type =>
        type === QuestionDisplayType.Dropdown ? 'Select your response' : 'Enter your response',
    questionDisplayTypeOption: type => defaultQuestionDisplayTypeOptionStrings[type],
    questionNewTitle: 'Question',
    questionTitle: 'Title',
    questionSubtitle: 'Subtitle (optional)',
    questionRequired: 'Required',
    questionRestriction: 'Restriction',
    questionShuffle: 'Shuffle',
    choiceAction: action => defaultChoiceActionStrings[action],
    choiceNew: index => `Option ${index + 1}`,
    choiceOther: 'Other',
    choicePlaceholder: 'Option',
    restrictionOption: type => defaultRestrictionOptionStrings[type],
    restrictionValuePlaceholder: key => defaultRestrictionValuePlaceholderStrings[key],
};

const defaultQuestionActionStrings: Record<QuestionAction, string> = {
    [QuestionAction.Add]: 'Add question',
    [QuestionAction.Copy]: 'Copy question',
    [QuestionAction.Delete]: 'Delete question',
    [QuestionAction.MoveUp]: 'Move question up',
    [QuestionAction.MoveDown]: 'Move question down',
};

const defaultQuestionDisplayTypeOptionStrings: Record<QuestionDisplayType, string> = {
    [QuestionDisplayType.ShortText]: 'Short text',
    [QuestionDisplayType.MultilineText]: 'Long text',
    [QuestionDisplayType.RichText]: 'Rich text',
    [QuestionDisplayType.Number]: 'Number',
    [QuestionDisplayType.MultipleChoice]: 'Multiple choice',
    [QuestionDisplayType.SingleChoice]: 'Single choice',
    [QuestionDisplayType.Dropdown]: 'Dropdown',
    [QuestionDisplayType.Date]: 'Date',
};

const defaultChoiceActionStrings: Record<ChoiceAction, string> = {
    [ChoiceAction.Add]: 'Add option',
    [ChoiceAction.AddOther]: 'Add "other" option',
    [ChoiceAction.Delete]: 'Delete option',
    [ChoiceAction.MoveUp]: 'Move option up',
    [ChoiceAction.MoveDown]: 'Move option down',
};

const defaultRestrictionOptionStrings: Record<TextNumberRestrictionType, string> = {
    [TextNumberRestrictionType.GreaterThan]: 'Greater than',
    [TextNumberRestrictionType.GreaterThanOrEqualTo]: 'Greater than or equal to',
    [TextNumberRestrictionType.LessThan]: 'Less than',
    [TextNumberRestrictionType.LessThanOrEqualTo]: 'Less than or equal to',
    [TextNumberRestrictionType.EqualTo]: 'Equal to',
    [TextNumberRestrictionType.NotEqualTo]: 'Not equal to',
    [TextNumberRestrictionType.Between]: 'Between',
    [TextNumberRestrictionType.NotBetween]: 'Not between',
};

const defaultRestrictionValuePlaceholderStrings: Record<TextNumberRestrictionValueKey, string> = {
    min: 'Min',
    max: 'Max',
    value: 'Value',
};
