import { Dropdown, mergeStyles, ResponsiveMode } from '@fluentui/react';
import { DecimalTextField } from '@addressbook/components-fields';
import React from 'react';
import { FormQuestionType, TextNumberRestrictionType, TextNumberRestrictionValueKey } from '../../../types';
import {
    deriveTextNumberRestriction,
    getTextNumberRestrictionOptions,
    useDropdownStyles,
    useTextFieldStyles,
} from '../../../utilities';
import { QuestionSettingsProps } from '../types';
import { useRestrictionSettingsStyles } from './RestrictionSettings.styles';

const allTextNumberRestrictionValueKeys: TextNumberRestrictionValueKey[] = ['min', 'max', 'value'];

export function RestrictionSettings({
    question,
    onQuestionChange,
    strings,
}: QuestionSettingsProps): JSX.Element | null {
    const styles = useRestrictionSettingsStyles();
    const textFieldStyles = useTextFieldStyles();
    const dropdownStyles = useDropdownStyles();

    if (!(question.type === FormQuestionType.Text && question.number && question.restriction)) return null;

    const restriction = question.restriction;
    const valueSettings = allTextNumberRestrictionValueKeys
        .filter(key => key in restriction)
        .map(key => (
            <DecimalTextField
                key={key}
                value={restriction[key]}
                onChange={value => {
                    const newRestriction = { ...restriction };

                    if (key in newRestriction) {
                        newRestriction[key] = value;
                    }

                    // Ensure max is always greater than min
                    if (key === 'min' && 'max' in newRestriction && value > newRestriction.max) {
                        newRestriction.max = value;
                    }

                    // Ensure min is always less than max
                    if (key === 'max' && 'min' in newRestriction && value < newRestriction.min) {
                        newRestriction.min = value;
                    }

                    onQuestionChange({ ...question, restriction: newRestriction });
                }}
                placeholder={strings.restrictionValuePlaceholder(key)}
                styles={textFieldStyles}
            />
        ));

    return (
        <div className={styles.row}>
            <Dropdown
                selectedKey={restriction.type}
                onChange={(_, option) =>
                    option &&
                    onQuestionChange({
                        ...question,
                        restriction: deriveTextNumberRestriction(option.key as TextNumberRestrictionType, restriction),
                    })
                }
                options={getTextNumberRestrictionOptions(strings)}
                responsiveMode={ResponsiveMode.unknown}
                styles={dropdownStyles}
            />
            <div className={mergeStyles(styles.values, valueSettings.length === 2 && styles.between)}>
                {valueSettings}
            </div>
        </div>
    );
}
