import { ChoiceGroup, IChoiceGroupOption, TextField } from '@fluentui/react';
import React from 'react';
import { ChoiceAppearance, ChoiceEntry, FormContext, FormEntry, FormMode, FormQuestionType } from '../../types';
import {
    useChoiceChange,
    useChoiceGroupOptionStyles,
    useChoiceGroupStyles,
    useChosenAndOther,
    useOtherFieldStyles,
    useShuffled,
} from '../../utilities';
import { BaseDescriptor, FieldComponentTypeFactory } from './BaseDescriptor';

const defaultOptions = {
    otherPlaceholder: 'Other',
    choiceAriaLabel: (title: string, choice: string) => `${title}: ${choice}`,
};

export type RadioChoiceDescriptorOptions = Partial<typeof defaultOptions>;

const makeFieldComponent: FieldComponentTypeFactory<
    RadioChoiceDescriptorOptions & typeof defaultOptions,
    ChoiceEntry
> =
    ({ otherPlaceholder, choiceAriaLabel }) =>
    ({ entry, onEntryChange, showPlaceholder, delegatedProps }) => {
        const { question, answer } = entry;
        const { choices, shuffle, allowOther } = question;

        const shuffledChoices = useShuffled({ array: choices, enabled: shuffle && !delegatedProps.readOnly });
        const onChange = useChoiceChange(entry, onEntryChange);
        const { chosen, otherKey, otherValue, otherSelected, otherFieldRef } = useChosenAndOther(
            choices,
            answer.content
        );

        const choiceGroupStyles = useChoiceGroupStyles();
        const choiceGroupOptionStyles = useChoiceGroupOptionStyles();
        const otherFieldStyles = useOtherFieldStyles();

        const options = shuffledChoices.map<IChoiceGroupOption>(choice => ({
            key: choice,
            text: choice,
            styles: choiceGroupOptionStyles,
            ariaLabel: choiceAriaLabel(question.title ?? '', choice),
        }));

        if (allowOther) {
            options.push({
                key: otherKey,
                text: otherValue ?? '',
                styles: choiceGroupOptionStyles,
                onRenderLabel: choiceGroupProps => (
                    <TextField
                        componentRef={otherFieldRef}
                        value={choiceGroupProps?.text}
                        onChange={onChange && ((_, newValue) => onChange([], newValue))}
                        placeholder={showPlaceholder ? otherPlaceholder : undefined}
                        readOnly={delegatedProps.readOnly}
                        invalid={otherSelected && delegatedProps.invalid}
                        id={choiceGroupProps?.labelId}
                        className='ms-ChoiceFieldLabel'
                        styles={otherFieldStyles}
                    />
                ),
                ariaLabel: choiceAriaLabel(question.title ?? '', otherValue ?? otherPlaceholder),
            });
        }

        return (
            <ChoiceGroup
                {...delegatedProps}
                selectedKey={otherSelected ? otherKey : chosen[0] ?? null}
                onChange={
                    onChange &&
                    ((_, option) => {
                        if (!option) return;

                        if (option.key === otherKey) {
                            otherFieldRef.current?.focus();
                            onChange([], '');
                        } else {
                            onChange([option.key]);
                        }
                    })
                }
                options={options}
                role='radiogroup'
                styles={choiceGroupStyles}
            />
        );
    };

/**
 * The descriptor that renders a group of radio buttons for choice entries with radio appearance.
 */
export class RadioChoiceDescriptor extends BaseDescriptor<ChoiceEntry> {
    protected readonly FieldComponent;

    constructor(options?: RadioChoiceDescriptorOptions) {
        super();
        this.FieldComponent = makeFieldComponent({ ...defaultOptions, ...options });
    }

    /**
     * Tests whether the entry is a choice entry with radio appearance, and the form is not in viewing mode.
     *
     * @param entry The entry to test.
     * @param context The context.
     * @returns Passes the test or not.
     */
    test(entry: FormEntry, context: FormContext): entry is ChoiceEntry {
        return (
            entry.question.type === FormQuestionType.Choice &&
            entry.question.appearance === ChoiceAppearance.Radio &&
            context.mode !== FormMode.Viewing
        );
    }
}
