import { BaseFormAnswer, BaseFormEntry, BaseFormQuestion, FormQuestionType } from './base';

export enum TextNumberRestrictionType {
    GreaterThan,
    GreaterThanOrEqualTo,
    LessThan,
    LessThanOrEqualTo,
    EqualTo,
    NotEqualTo,
    Between,
    NotBetween,
}

export interface TextNumberRestrictionWithMin {
    type: TextNumberRestrictionType.GreaterThan | TextNumberRestrictionType.GreaterThanOrEqualTo;
    min: number;
}

export interface TextNumberRestrictionWithMax {
    type: TextNumberRestrictionType.LessThan | TextNumberRestrictionType.LessThanOrEqualTo;
    max: number;
}

export interface TextNumberRestrictionWithValue {
    type: TextNumberRestrictionType.EqualTo | TextNumberRestrictionType.NotEqualTo;
    value: number;
}

export interface TextNumberRestrictionWithMinAndMax {
    type: TextNumberRestrictionType.Between | TextNumberRestrictionType.NotBetween;
    min: number;
    max: number;
}

export type TextNumberRestriction =
    | TextNumberRestrictionWithMin
    | TextNumberRestrictionWithMax
    | TextNumberRestrictionWithValue
    | TextNumberRestrictionWithMinAndMax;

export type TextNumberRestrictionValueKey = 'min' | 'max' | 'value';

/**
 * A text question.
 *
 * @property multiline Whether the question is a multiline text.
 * @property number Whether the question is a number.
 * @property restriction The restriction of the field.
 */
export interface TextQuestion extends BaseFormQuestion<FormQuestionType.Text> {
    multiline?: boolean;
    number?: boolean;
    restriction?: TextNumberRestriction;
}

export type TextAnswer = BaseFormAnswer<string>;

export type TextEntry = BaseFormEntry<TextQuestion, TextAnswer>;
