import { FormEntry, FormQuestionType, Validation, ValidationFailure, Validator } from '../../types';
import { createValidationSuccess } from '../../utilities';

/**
 * A function that returns the max length of the answer content.
 *
 * @param type The type of the question.
 * @returns The max length of the answer content.
 */
export type MaxLength = (type: FormQuestionType) => number;

/**
 * A function that returns the error message the given max length.
 *
 * @param maxLength The max length of the answer content.
 * @param type The type of the question.
 * @returns The error message.
 */
export type ErrorMessageByMaxLength = (maxLength: number, type: FormQuestionType) => string;

export interface LengthValidatorOptions {
    maxLength?: MaxLength;
    errorMessageByMaxLength?: ErrorMessageByMaxLength;
}

const defaultMaxLength: MaxLength = () => 10000;
const defaultErrorMessageByMaxLength: ErrorMessageByMaxLength = maxLength =>
    `Please enter less than ${maxLength} characters`;

/**
 * A validator that checks if the answer not exceeds the max length.
 * If the answer content is not a string, this validator will always pass.
 * Otherwise, this validator will fail if the answer content exceeds the max length, and prevent the change.
 */
export class LengthValidator implements Validator<FormEntry> {
    private maxLength: MaxLength;
    private errorMessageByMaxLength: ErrorMessageByMaxLength;

    /**
     * Creates a length validator.
     *
     * @param options.maxLength The max length of the answer content.
     * @param options.errorMessageByMaxLength The error message if the answer content exceeds the max length.
     * @returns A length validator.
     */
    constructor({
        maxLength = defaultMaxLength,
        errorMessageByMaxLength = defaultErrorMessageByMaxLength,
    }: LengthValidatorOptions = {}) {
        this.maxLength = maxLength;
        this.errorMessageByMaxLength = errorMessageByMaxLength;
    }

    validate({ question, answer }: FormEntry): Validation {
        const content = answer.content;

        // Check for undefined.
        if (content === undefined) {
            return createValidationSuccess();
        }

        const type = question.type;
        const maxLength = this.maxLength(type);

        // Check for string.
        if (typeof content === 'string') {
            const length = content.length;
            return length <= maxLength ? createValidationSuccess() : this.createValidationFailure(maxLength, type);
        }

        // Check for choice answer.
        if (typeof content === 'object' && 'chosen' in content) {
            const length = content.chosen.reduce((l, c) => l + c.length, 0) + (content.other?.length ?? 0);
            return length <= maxLength ? createValidationSuccess() : this.createValidationFailure(maxLength, type);
        }

        return createValidationSuccess();
    }

    private createValidationFailure(maxLength: number, type: FormQuestionType): ValidationFailure {
        const errorMessage = this.errorMessageByMaxLength(maxLength, type);
        return { valid: false, errorMessage, preventChange: true };
    }
}
