import { FontSizes, isContrastTheme, makeStyles } from '@addressbook/components-fluentui-teams-theme';
import React from 'react';
import { FormContext, FormEntry, FormMode } from '../../types';
import { BaseDescriptor, FieldComponentTypeFactory } from './BaseDescriptor';

const defaultOptions = {
    formatList: (value: string[]): string => value.join('\n'),
    formatDate: (value: Date): string => value.toDateString(),
};

const useFallbackViewingStyles = makeStyles(theme => ({
    root: {
        cursor: 'text',
        whiteSpace: 'pre-wrap',
        overflowWrap: 'break-word',
        fontSize: FontSizes.size14,
        color: theme.palette.black,
        backgroundColor: isContrastTheme(theme.name) ? theme.palette.white : theme.palette.neutralLighter,
        padding: '6px 12px',
        borderRadius: '4px',
        borderWidth: '1px',
        borderStyle: 'solid',
        borderColor: isContrastTheme(theme.name) ? theme.palette.black : 'transparent',
        lineHeight: FontSizes.size18,
        minHeight: FontSizes.size18,
    },
}));

/**
 * The options for the fallback viewing descriptor.
 */
export type FallbackViewingDescriptorOptions = Partial<typeof defaultOptions>;

const makeFieldComponent: FieldComponentTypeFactory<
    FallbackViewingDescriptorOptions & typeof defaultOptions,
    FormEntry
> =
    ({ formatList, formatDate }) =>
    ({ entry, delegatedProps }) => {
        const { answer } = entry;

        const viewingContent = React.useMemo<string>(() => {
            const content = answer.content;
            if (content === undefined) {
                return '';
            }

            if (typeof content === 'string') {
                return content;
            }

            if ('chosen' in content) {
                const list = content.other ? [...content.chosen, content.other] : content.chosen;
                return formatList(list);
            }

            if (content instanceof Date) {
                return formatDate(content);
            }

            const exhaustiveCheck: never = content;
            throw new Error(`Unhandled content type: ${exhaustiveCheck}`);
        }, [answer]);

        const styles = useFallbackViewingStyles();
        return (
            <div {...delegatedProps} className={styles.root} role='textbox' tabIndex={-1}>
                {viewingContent}
            </div>
        );
    };

/**
 * The fallback descriptor that renders a text field in viewing mode, for all entries.
 */
export class FallbackViewingDescriptor extends BaseDescriptor<FormEntry> {
    protected readonly FieldComponent;

    constructor(options: FallbackViewingDescriptorOptions = {}) {
        super();
        this.FieldComponent = makeFieldComponent({ ...defaultOptions, ...options });
    }

    /**
     * Tests whether the form is in viewing mode.
     *
     * @param _ Unused.
     * @param context The context.
     * @returns Passes the test or not.
     */
    test(_: FormEntry, context: FormContext): _ is FormEntry {
        return context.mode === FormMode.Viewing;
    }
}
