import { isContrastTheme, makeStyles } from '@addressbook/components-fluentui-teams-theme';

export const useChoiceSettingsStyles = makeStyles(theme => ({
    root: {
        display: 'flex',
        flexDirection: 'column',
        gap: '8px',
        paddingInlineStart: '8px',
    },
    choiceRow: {
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center',
        gap: '4px',
        '> .ms-TextField': {
            flexBasis: '400px',
            marginInline: '8px',
        },
    },
    hidden: {
        visibility: 'hidden',
    },
    actionRow: {
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center',
        gap: '8px 24px',
        flexWrap: 'wrap',
        '> .ms-Button': {
            height: '28px',
            padding: 0,
        },
    },
    decorator: {
        flexShrink: 0,
        height: '16px',
        width: '16px',
        margin: '4px',
        boxSizing: 'border-box',
        border: `1px solid ${isContrastTheme(theme.name) ? theme.palette.black : theme.palette.neutralTertiary}`,
    },
    checkbox: {
        borderRadius: '2px',
    },
    radio: {
        borderRadius: '50%',
    },
}));
