import { FocusZone } from '@fluentui/react';
import React from 'react';
import { FieldProps } from '../../../abstractions';
import { FormContext, FormEntry, FormFeature, FormQuestion } from '../../types';
import { ChoiceSettings } from './ChoiceSettings';
import { DesigningActions } from './DesigningActions';
import { useActiveDesigningDecoratorStyles } from './DesigningDecorator.styles';
import { MockedField } from './MockedField';
import { PrimarySettings } from './PrimarySettings';
import { RestrictionSettings } from './RestrictionSettings';
import { SecondarySettings } from './SecondarySettings';
import { QuestionSettingsProps } from './types';

export function ActiveDesigningDecorator(props: FieldProps<FormEntry, FormContext, FormFeature>): JSX.Element {
    const { entry, feature, onEntryChange } = props;
    const { question } = entry;
    const { constants, strings } = feature;

    const onQuestionChange = React.useCallback(
        (newQuestion: FormQuestion) => {
            // Delete answer content when question changes
            const newEntry = { ...entry };
            newEntry.question = newQuestion;
            delete newEntry.answer.content;
            onEntryChange?.(newEntry);
        },
        [entry, onEntryChange]
    );

    const styles = useActiveDesigningDecoratorStyles();
    const settingsProps: QuestionSettingsProps = { question, onQuestionChange, constants, strings };

    return (
        <div className={styles.root}>
            <FocusZone className={styles.settings}>
                <PrimarySettings {...settingsProps} />
                <MockedField {...settingsProps} />
                <ChoiceSettings {...settingsProps} />
                <div className={styles.separator} role='separator' />
                <SecondarySettings {...settingsProps} />
                <RestrictionSettings {...settingsProps} />
            </FocusZone>
            <FocusZone className={styles.actions}>
                <DesigningActions {...props} />
            </FocusZone>
        </div>
    );
}
