import { Comparator } from '../../abstractions';
import { BaseFormEntry } from '../types';

/**
 * A comparator that compares entries by their question order.
 *
 * @param first The first entry.
 * @param second The second entry.
 * @returns The comparison result.
 */
export const orderComparator: Comparator<BaseFormEntry<{ order: number }, unknown>> = (first, second) =>
    first.question.order - second.question.order;
