import { makeStyles, FontSizes, isContrastTheme } from '@addressbook/components-fluentui-teams-theme';

export const useCollapsibleTextFieldStyles = makeStyles(theme => ({
    root: {
        '&.ms-TextField .ms-TextField-wrapper .ms-TextField-fieldGroup': {
            display: 'flex',
            alignItems: 'center',
            gap: '4px',
            input: {
                fontSize: FontSizes.size24,
                '::placeholder': {
                    color: isContrastTheme(theme.name) ? theme.palette.black : theme.palette.neutralPrimary,
                },
            },
        },
        '.ms-TextField-errorMessage': {
            padding: '4px 12px',
        },
    },
    collapsed: {
        '&.ms-TextField .ms-TextField-wrapper': {
            maxWidth: 'max-content',
            '.ms-TextField-fieldGroup': {
                '::before': {
                    content: '""',
                },
                backgroundColor: 'unset',
            },
        },
    },
    error: {
        '&.ms-TextField .ms-TextField-wrapper .ms-TextField-fieldGroup': {
            border: `1px solid ${theme.palette.redDark}`,
        },
    },
    collapsedText: {
        marginInlineStart: '12px',
        color: theme.palette.black,
        fontSize: FontSizes.size24,
        overflow: 'hidden',
        textOverflow: 'ellipsis',
        whiteSpace: 'nowrap',
    },
    collapsedPlaceholder: {
        color: isContrastTheme(theme.name) ? theme.palette.black : theme.palette.neutralPrimary,
    },
    button: {
        margin: '4px',
    },
}));
