import { Spinner, SpinnerSize, TooltipHost } from '@fluentui/react';
import { useBlobObjectUrl } from '@addressbook/utilities-hooks';
import React from 'react';
import { useFallbackViewStyles } from './styles';

export interface FallbackViewProps extends React.PropsWithChildren<React.AnchorHTMLAttributes<HTMLAnchorElement>> {
    thumbnail?: Blob;
    loading?: boolean;
}

export const FallbackView: React.FC<FallbackViewProps> = ({ thumbnail, loading, children, ...props }) => {
    const targetRef = React.useRef<HTMLAnchorElement>(null);
    const thumbnailUrl = useBlobObjectUrl(thumbnail);

    const styles = useFallbackViewStyles();
    return (
        // Justification: the props are spread to the anchor element
        // eslint-disable-next-line react/jsx-no-target-blank
        <a ref={targetRef} {...props}>
            {loading && <Spinner className={styles.spinner} size={SpinnerSize.xSmall} />}
            {thumbnailUrl ? <TooltipHost content={<img src={thumbnailUrl} />}>{children}</TooltipHost> : children}
        </a>
    );
};
