import { FocusZone } from '@fluentui/react';
import React from 'react';
import { FieldProps } from '../../../abstractions';
import { FormContext, FormEntry, FormFeature } from '../../types';
import { usePreviewingDecoratorStyles } from './PreviewingDecorator.styles';

export function PreviewingDecorator<CoercedEntry extends FormEntry>({
    children,
}: React.PropsWithChildren<FieldProps<FormEntry, FormContext, FormFeature, CoercedEntry>>): JSX.Element {
    const styles = usePreviewingDecoratorStyles();

    return (
        <FocusZone className={styles.root} disabled>
            {children}
            <div className={styles.mask} />
        </FocusZone>
    );
}
