import { isContrastTheme, makeStyles } from '@addressbook/components-fluentui-teams-theme';

const SeparatorWidth = 1;
const RibbonButtonGap = 4;

export const useRibbonStyles = makeStyles(theme => ({
    leadingButton: {
        position: 'relative',
        '.ms-OverflowSet-item:not(:first-child) &::before': {
            content: '""',
            backgroundColor: isContrastTheme(theme.name) ? theme.palette.black : theme.palette.neutralTertiaryAlt,
            position: 'absolute',
            width: `${SeparatorWidth}px`,
            insetBlock: '25%',
            insetInlineStart: `-${(RibbonButtonGap + SeparatorWidth) / 2}px`,
        },
    },
}));

export const useRibbonCommandBarStyles = makeStyles(theme => ({
    root: {
        ':first-child': {
            paddingTop: '8px',
        },
        ':last-child': {
            paddingBottom: '8px',
        },
        height: '32px',
        paddingInline: '12px',
        border: 'none',
        '.ms-OverflowSet': {
            gap: `${RibbonButtonGap}px`,
        },
        '.ms-Button': {
            color: theme.palette.black,
            border: 'none',
            margin: 0,
            padding: '0 8px',
            borderRadius: '4px',
            '.ms-Icon': {
                height: '16px',
                width: '16px',
                margin: 0,
                padding: 0,
                color: 'inherit !important',
                svg: {
                    margin: 0,
                    fill: 'currentColor !important',
                },
            },
            svg: {
                display: 'block',
                height: '100%',
                width: '100%',
            },
        },
        '.ms-Button.is-checked': {
            color: isContrastTheme(theme.name) ? theme.palette.themeDark : theme.palette.accent,
            backgroundColor: isContrastTheme(theme.name) ? undefined : theme.palette.neutralQuaternary,
        },
        '.ms-Button:hover': {
            '@media (hover: hover)': {
                color: isContrastTheme(theme.name) ? theme.palette.white : theme.palette.accent,
                backgroundColor: isContrastTheme(theme.name) ? theme.palette.accent : undefined,
            },
        },
        '.ms-Button:hover:not(.is-checked)': {
            '@media (hover: none)': {
                color: theme.palette.black,
            },
        },
        '.ms-Button:not(.ms-CommandBar-overflowButton) .ms-Button-menuIcon': {
            display: 'none',
        },
    },
}));
