import { ISuggestionsStyleProps, ITheme, ISuggestionsStyles } from '@fluentui/react';

export function getSuggestionsStyles(themeName: string, _theme: ITheme) {
    return (props: ISuggestionsStyleProps): Partial<ISuggestionsStyles> => {
        const { theme } = props;
        const { palette } = theme;

        const selectedColor = {
            default: `${palette.black} !important`,
            dark: `${palette.black} !important`,
            contrast: `${palette.white} !important`,
        }[themeName];

        const selectedBackground = {
            default: palette.neutralLighter,
            dark: palette.neutralLighter,
            contrast: palette.themeSecondary,
        }[themeName];

        const customizedStyles = {
            background: selectedBackground,
            selectors: {
                '.ms-Persona-details': {
                    selectors: {
                        div: {
                            color: selectedColor,
                        },
                    },
                },
            },
        };

        const styles: ISuggestionsStyles = {
            root: {},
            noSuggestions: {
                color: palette.black,
            },
            suggestionsContainer: {
                selectors: {
                    '.ms-Suggestions-item': {
                        height: '4.0em',
                        paddingBottom: '0',
                        marginBottom: '0',
                    },
                    '.ms-Suggestions-item .ms-Suggestions-itemButton:hover': {
                        ...customizedStyles,
                    },
                    '.ms-Suggestions-item .ms-Suggestions-itemButton.is-suggested': {
                        ...customizedStyles,
                    },
                },
            },
            title: {},
            forceResolveButton: {},
            searchForMoreButton: {},
            suggestionsAvailable: {},
            subComponentStyles: {
                spinner: {},
            },
        };
        return styles;
    };
}
