/* istanbul ignore file */

import {
    IChoiceGroupOptionStyleProps,
    IChoiceGroupOptionStyles,
    IStyleFunctionOrObject,
    ITheme,
} from '@fluentui/react';
import { ContrastColors } from '../teams-colors';
import { isContrastTheme } from '../teams-theme';
import { teamsBorderRadius } from './constants';

export function getChoiceGroupOptionStyles(
    themeName: string,
    _theme: ITheme
): IStyleFunctionOrObject<IChoiceGroupOptionStyleProps, IChoiceGroupOptionStyles> {
    return (props: IChoiceGroupOptionStyleProps) => {
        const { disabled, checked, theme } = props;
        const { palette } = theme;

        let CHOICEGROUPOPTION_BORDER_COLOR = palette.neutralTertiary;
        let CHECKBOX_LABEL_COLOR = palette.neutralTertiary;

        if (isContrastTheme(themeName)) {
            CHOICEGROUPOPTION_BORDER_COLOR = ContrastColors.appGreenContrast;
            CHECKBOX_LABEL_COLOR = ContrastColors.appGreenContrast;
        }

        const styles = {
            choiceFieldWrapper: [
                {
                    background: 'transparent',
                    // Work around for fluentui button focus outline not showing in iOS Safari.
                    position: 'relative',
                    ':has(.ms-ChoiceField-input:focus-visible), .ms-Fabric--isFocusVisible &:has(.ms-ChoiceField-input:focus)':
                        {
                            '::after': {
                                border: 'none',
                                content: '""',
                                position: 'absolute',
                                inset: 0,
                                outline: `1px solid ${theme.palette.black}`,
                                borderRadius: teamsBorderRadius,
                            },
                        },
                },
                disabled && {
                    color: CHECKBOX_LABEL_COLOR,
                },
                !disabled &&
                    checked && {
                        color: palette.black,
                    },

                !disabled &&
                    !checked && {
                        color: palette.neutralDark,
                    },
            ],
            field: [
                {
                    selectors: {
                        '.ms-ChoiceField-field': {
                            color: 'inherit',
                        },
                        '.ms-ChoiceFieldLabel': {
                            color: 'inherit',
                        },
                        ':hover': [
                            !disabled && {
                                selectors: {
                                    ':before': {
                                        borderColor: palette.black,
                                    },
                                },
                            },
                        ],
                    },
                },
                !disabled &&
                    !checked && {
                        selectors: {
                            ':before': {
                                borderColor: palette.neutralDark,
                            },
                        },
                    },
                !disabled &&
                    checked && {
                        color: palette.black,
                        selectors: {
                            ':before': {
                                borderColor: palette.themePrimary,
                            },
                            ':after': {
                                borderColor: palette.themePrimary,
                            },
                        },
                    },
                disabled &&
                    !checked && {
                        selectors: {
                            ':before': {
                                borderColor: CHOICEGROUPOPTION_BORDER_COLOR,
                                background: 'transparent',
                            },
                            ':after': {
                                borderColor: CHOICEGROUPOPTION_BORDER_COLOR,
                            },
                        },
                    },
                disabled &&
                    checked && {
                        selectors: {
                            ':before': {
                                borderColor: CHOICEGROUPOPTION_BORDER_COLOR,
                                background: 'transparent',
                            },
                            ':after': {
                                borderColor: CHOICEGROUPOPTION_BORDER_COLOR,
                            },
                        },
                    },
            ],
        } as Partial<IChoiceGroupOptionStyles>;

        return styles;
    };
}
