import { Dialog } from '@fluentui/react';
import React from 'react';
import { InsertLink, InsertLinkProps, Link } from './InsertLink';
import { useInsertLinkDialogStyles } from './InsertLink.styles';

export type InsertLinkDialogProps = InsertLinkProps;

export const InsertLinkDialog: React.FC<InsertLinkDialogProps> = ({
    initialLink,
    onConfirm,
    onCancel,
    normalizeUrl,
    strings,
}) => {
    const [link, setLink] = React.useState<Link>();
    const [hidden, setHidden] = React.useState(false);
    const dismiss = React.useCallback(() => setHidden(true), []);

    const confirm = React.useCallback(
        (link: Link) => {
            setLink(link);
            dismiss();
        },
        [dismiss]
    );

    const cancel = React.useCallback(() => {
        setLink(undefined);
        dismiss();
    }, [dismiss]);

    const commit = React.useCallback(() => (link ? onConfirm?.(link) : onCancel?.()), [link, onConfirm, onCancel]);

    const styles = useInsertLinkDialogStyles();
    return (
        <Dialog
            hidden={hidden}
            dialogContentProps={{ title: strings.insertLinkTitle }}
            modalProps={{ className: styles.root, onDismiss: cancel, onDismissed: commit }}>
            <InsertLink
                initialLink={initialLink}
                onConfirm={confirm}
                onCancel={cancel}
                normalizeUrl={normalizeUrl}
                strings={strings}
            />
        </Dialog>
    );
};
