import { Dropdown, ResponsiveMode, TextField, mergeStyles } from '@fluentui/react';
import React from 'react';
import { ChoiceAppearance, ChoiceEntry, FormContext, FormEntry, FormMode, FormQuestionType } from '../../types';
import {
    useDropdownStyles,
    useTextFieldStyles,
    useChoiceChange,
    useChosenAndOther,
    useShuffled,
} from '../../utilities';
import { BaseDescriptor, FieldComponentTypeFactory } from './BaseDescriptor';

const containerClassName = mergeStyles({
    display: 'flex',
    flexDirection: 'column',
    gap: '8px',
});

const defaultOptions = {
    defaultPlaceholder: 'Select your response',
    otherText: 'Other',
    otherPlaceholder: 'Enter your response',
};

export type DropdownChoiceDescriptorOptions = Partial<typeof defaultOptions>;

const makeFieldComponent: FieldComponentTypeFactory<
    DropdownChoiceDescriptorOptions & typeof defaultOptions,
    ChoiceEntry
> =
    ({ defaultPlaceholder, otherText, otherPlaceholder }) =>
    ({ entry, onEntryChange, showPlaceholder, delegatedProps }) => {
        const { question, answer } = entry;
        const { choices, shuffle, allowOther } = question;

        const shuffledChoices = useShuffled({ array: choices, enabled: shuffle && !delegatedProps.readOnly });
        const onChange = useChoiceChange(entry, onEntryChange);
        const { chosen, otherKey, otherValue, otherSelected } = useChosenAndOther(choices, answer.content);

        const options = shuffledChoices.map(choice => ({ key: choice, text: choice }));
        if (allowOther) options.push({ key: otherKey, text: otherText });

        const placeholder = showPlaceholder ? question.placeholder || defaultPlaceholder : undefined;

        const dropdownStyles = useDropdownStyles();
        const textFieldStyles = useTextFieldStyles();

        return (
            <div className={containerClassName}>
                <Dropdown
                    {...delegatedProps}
                    selectedKey={otherSelected ? otherKey : chosen[0] ?? null}
                    options={options}
                    onChange={
                        onChange &&
                        ((_, option) =>
                            option && (option.key === otherKey ? onChange([], '') : onChange([option.key as string])))
                    }
                    placeholder={placeholder}
                    // Disable responsive mode to prevent the dropdown from being rendered as a panel.
                    responsiveMode={ResponsiveMode.unknown}
                    styles={dropdownStyles}
                />
                {otherSelected && (
                    <TextField
                        {...delegatedProps}
                        value={otherValue}
                        onChange={onChange && ((_, newValue) => onChange([], newValue))}
                        placeholder={showPlaceholder ? otherPlaceholder : undefined}
                        styles={textFieldStyles}
                    />
                )}
            </div>
        );
    };

/**
 * The descriptor that renders a dropdown for choice entries with dropdown appearance.
 */
export class DropdownChoiceDescriptor extends BaseDescriptor<ChoiceEntry> {
    protected readonly FieldComponent;

    constructor(options?: DropdownChoiceDescriptorOptions) {
        super();
        this.FieldComponent = makeFieldComponent({ ...defaultOptions, ...options });
    }

    /**
     * Tests whether the entry is a choice entry with dropdown appearance, and the form is not in viewing mode.
     *
     * @param entry The entry to test.
     * @param context The context.
     * @returns Passes the test or not.
     */
    test(entry: FormEntry, context: FormContext): entry is ChoiceEntry {
        return (
            entry.question.type === FormQuestionType.Choice &&
            entry.question.appearance === ChoiceAppearance.Dropdown &&
            context.mode !== FormMode.Viewing
        );
    }
}
